  //    let { gl, CurrntPose, Textures, textures } = this;
  //    // textures = Textures[0];
  //    var textsaree = new Texture("SAREE", gl);
  //    await textsaree.BindTexture(gl, data.sareeImg);
  //    this.Textures["SAREE"] = textsaree;
 
  //    var textpallu = new Texture("PALLU", gl);
  //    await textpallu.BindTexture(gl, data.palluImg);
  //    this.Textures["PALLU"] = textpallu;
 
  //    var textblouse = new Texture("BLOUSE", gl);
  //    await textblouse.BindTexture(gl, data.blouseImg);
  //    this.Textures["BLOUSE"] = textblouse;
