/**
 * @module usersPage
 */
import * as React from "react";
import { BrowserRouter, Switch, Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { DashboardLayout } from "../navbar/Layout";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import queryString from "query-string";
// import sha256 from "crypto-js/sha256";
// import hmacSHA512 from "crypto-js/hmac-sha512";
// import Base64 from "crypto-js/enc-base64";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
  Card,
  ListGroup,
} from "react-bootstrap";
import Loader from "react-loader-spinner";
var crypto = require("crypto");
require("dotenv").config();

/**
 * @module User_react_Component
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
class User extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      dataget: undefined,
      branches: [],
      nodes: [],
      selectedNodes: [],
      selectedBranches: [],
      currentUserid: undefined,
      showLoader:true,
    };
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name GetUserInfo
  * @description It will getting user informations
  */
  GetUserInfo() {
    //alert(this.props.match.params.id);
    // POST request using fetch with set headers
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        branchid: this.props.match.params.id,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/GetUserInfo", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        if(data.length>0)
        this.setState({ currentUserid: data[0].id });
              this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name Get_all_Branches
  * @description It will getting all branches
  */
  GetAllBranches() {

    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/Allbranches", requestOptions)
      .then((response) => response.json())
      .then((data) => {

        this.setState({ branches: data });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name GetSavedFlow
  * @description It will getting saved flow
  */
  GetSavedFlow() {
    // POST request using fetch with set headers
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        userid: this.props.match.params.id,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/Allbranchesnew", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        if (data.r) {
          data.db.forEach((itembranch, i) => {
            var brsel = data.r.filter(function (item) {
              return item._id === itembranch;
            })[0];
            if (brsel) brsel.selected = true;
          });

          this.setState({ branches: data.r });
          var lastFetch = 0;
          data.db.forEach((item, i) => {
            this.GetBrancheUsers(item).then(() => {
              lastFetch++;
              if (lastFetch === data.db.length) {
                data.dn.forEach((itembranch, i) => {
                  var brsel = this.state.nodes.filter(function (item) {
                    return item._id === itembranch.id;
                  })[0];
                  if (brsel) brsel.selected = true;
                });
                this.setState({ nodes: this.state.nodes });
              }
            });
          });

        } else {
          this.GetAllBranches();
        }
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name GetBrancheUsers
  * @description It will getting all branches
    * @param {string} branchid -branch id
  */
  async GetBrancheUsers(branchid) {

    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        branchid: branchid,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    await fetch(process.env.REACT_APP_SERVER_HOST + "/GetUsers", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        if (this.state.nodes.length > 0)
          this.state.nodes.forEach((item, i) => {
            data.push(item);
          });
        this.setState({ nodes: data });
        return true;

      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentWillMount
  * @description It will mount before start
  */
  componentWillMount() {
    this.GetUserInfo();
    this.GetSavedFlow();
    this.GetBrancheUsers();

  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name getLicfromServer
  * @description It will get License from server
  */
  getLicfromServer() {
          this.setState({ showLoader: true });
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        userid: this.state.currentUserid,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };

    fetch(
      process.env.REACT_APP_SERVER_HOST + "/GetUsersLicense",
      requestOptions
    )
      .then((response) => response.json())
      .then((data) => {
        this.setState({ lickey: data.lickey });

        const blob = new Blob([this.state.lickey], { type: "text/plain" });
        const url = URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.download = "License.ylic";
        link.href = url;
        link.click();
              this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name SaveFlow
  * @description It will save the user flow
  */
  SaveFlow() {
          this.setState({ showLoader: true });
    var selectedNod = this.state.nodes
      .filter(function (item) {
        return item.selected;
      })
      .map(function (obj) {
        return { user: obj.name, id: obj._id };
      });
    var selectedBranc = this.state.branches
      .filter(function (item) {
        return item.selected;
      })
      .map(function (obj) {
        return obj._id;
      });

    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        sourceId: this.state.currentUserid,
        destinationId: selectedNod,
        destinationBranch: selectedBranc,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };

    fetch(process.env.REACT_APP_SERVER_HOST + "/addflow", requestOptions)
      .then((response) => response.json())
      .then((data) => {
      this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name BranchSelect
  * @description It will selcet the correspond nodes
  * @param {object} e -event
    * @param {object} row -row
  */
  BranchSelect(e, row) {
    row.selected = e.target.checked;
    if (!row.selected) {
      var filteruser = this.state.nodes;
      row.branchUsers.forEach((itembranch, i) => {
        var seluser = this.state.nodes.filter(function (item) {
          return itembranch.user === item._id;
        })[0];
        const index = filteruser.indexOf(seluser);
        if (index > -1) {
          filteruser.splice(index, 1);
        }
      });

      this.setState({ nodes: filteruser });
    } else {
      this.GetBrancheUsers(row._id);
    }
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name SelectNode
  * @description It will selcet the nodes
  * @param {object} e -event
    * @param {object} row -row
  */
  SelectNode(e, row) {

    row.selected = e.target.checked;
    this.setState({ nodes: this.state.nodes });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    const classes = [{}]; // useStyles();

    const handleClose = () => this.setState({ show: false });
    const handleShow = () => this.setState({ show: true });

    const handleSubmit = (event) => {
    const form = event.currentTarget;
    const dataf = new FormData(event.currentTarget);
      {
        event.preventDefault();
        event.stopPropagation();
        const requestOptions = {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: JSON.parse(localStorage.getItem("token")).token,
          },
          body: JSON.stringify({
            name: this.state.name,
            username: this.state.username,
            apptype: this.state.apptype,
            password: this.state.password,
            branchid: this.props.match.params.id,
          }),
        };
        fetch(process.env.REACT_APP_SERVER_HOST + "/adduser", requestOptions)
          .then((response) => response.json())
          .then((data) => {
            this.GetBrancheUsers();
          });
      }
    };

    if (!this.state.branches) {
      return (
        <DashboardLayout>
          <h2>All Nodes</h2>
          <h2>Loading</h2>
        </DashboardLayout>
      );
    }

    else
      return (
        <DashboardLayout>
          <h2 style={{ paddingLeft: "2rem" }}>Node Flow Information</h2>

          <Container fluid>
            <Row className="offset-4">
            </Row>
            <Row style={{ height: 30 }}></Row>
            <Row>
            <Loader type="Watch"
              color="#00BFFF"
              height={100}
              width={100}
              visible={this.state.showLoader}
              style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height:"60vh",
            position:"absolute",
            left: 0,/*added*/
            right: 0, /*added*/
            zIndex:1 }}/>

              <Col xs={2}>
              <br></br>
              <br></br>
                <Card>
                  <Card.Body> Node Flow </Card.Body>
                </Card>
              </Col>

              <Col xs={3}>
              <br></br>
              <br></br>
                <Card border="primary" style={{ width: "23rem" }}>
                  <Card.Header> Branches</Card.Header>
                  <ListGroup variant="flush">
                    {this.state.branches.map((row) => (
                      <ListGroup.Item>
                        <Form.Check
                          type="checkbox"
                          id={row._id}
                          label={row.name}
                          checked={row.selected}
                          onChange={(e) => {
                            this.BranchSelect(e, row);
                          }}
                        />
                      </ListGroup.Item>
                    ))}
                  </ListGroup>
                </Card>
              </Col>

              <Col xs={3}  style={{ alignItems: "left" }}>
              <Button variant="primary" onClick={(e) => this.getLicfromServer()}> Download License</Button>
                <Card border="success" style={{ width: "23rem" }}>
                  <Card.Header> Nodes</Card.Header>
                  <ListGroup variant="flush">
                    {this.state.nodes.length>0&&this.state.nodes.map((row) => (
                      <ListGroup.Item>
                        <Form.Check
                          type="checkbox"
                          id={row._id}
                          label={row.username}
                          checked={row.selected}
                          onChange={(e) => {
                            this.SelectNode(e, row);
                          }}
                        />
                      </ListGroup.Item>
                    ))}
                  </ListGroup>
                </Card>
                <Row style={{ height: 30 }}></Row>
                <Button variant="success" onClick={(e) => this.SaveFlow()}>
                  Save
                </Button>
              </Col>

            </Row>
          </Container>
        </DashboardLayout>
      );
  }
}

export default User;
