import * as React from 'react';
import { withRouter } from 'react-router-dom';
import { DataGrid, GridColDef, GridApi, GridCellValue, GridCellParams } from '@mui/x-data-grid';
import { DashboardLayout } from "../navbar/Layout";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";
import Button from '@mui/material/Button';
import axios from 'axios';
import API from '../../api/services';
import hair1 from '../../Icons/hair1.png';
import edit from '../../Icons/edit.png';
import Delete1 from '../../Icons/delete.png';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure();
class ProductsTable extends React.Component{
    constructor(props)
    {
        super(props);
        this.state = {
            rows: null,
            copyRows:null,
            columns : null,
            paginationCount: 50,
            currentPage:'',
            paginationLimit: 4
        }

        this.currentlySelected = this.currentlySelected.bind(this);
        this.deleteprod = this.deleteprod.bind(this);
        this.editProduct = this.editProduct.bind(this);
        this.nextPagination = this.nextPagination.bind(this);
        this.prevPagination = this.prevPagination.bind(this);
    }

     componentDidMount(){
         this.LoadData();
         setTimeout(() => {
             this.setPagination(1)
         }, 2000);
    }

    currentlySelected(params) {
        let value = params.colDef.field;
        let api = params.api;
    
        if (!(value === "edit" || value === "delete")) {
          return;
        }

        if (value === "edit"){
            this.props.history.push({
                pathname : '/products/addproducts',
                state :{
                         selectdata : params.row
                    }
                } 
            );
        }
        else if (value === "delete"){
            const config = {
                headers: {
                    'Content-Type': 'application/json',
                  Authorization:JSON.parse(localStorage.getItem("token")).token,
                },
            };
            
            let ids=[];
            for(let ind=0;ind<params.row.images.length;ind++){
                ids.push(params.row.images[ind].filename)
            }
            
            var data = {
                'fileids': ids,
                '_id' : params.row._id
            }   
            this.deleteSelected(data, config);
        }
    }

    deleteprod(item){
        //toast.success("Successfully!", { position: toast.POSITION.BOTTOM_RIGHT, autoClose: 3000 })
        //toast.promise("deleting!", { position: toast.POSITION.BOTTOM_RIGHT, autoClose: 3000 })
        const config = {
            headers: {
                'Content-Type': 'application/json',
            Authorization:JSON.parse(localStorage.getItem("token")).token,
            },
        };
    
        let ids=[];
        for(let ind=0;ind<item.images.length;ind++){
            ids.push(item.images[ind].filename)
        }

    
        var data = {
            'fileids': ids,
            '_id' : item.id
        }   

        let _this = this;
        const deleteRecord =this.deleteSelected(data, config);
        toast.promise(
            deleteRecord,
            {
                pending: {
                    render(){
                        return "deleting.."
                    },
                    icon: false,
                },
                success: {
                    render(res){
                        let filteredItem = _this.state.rows.filter((_item, index) => {
                            return item.id !== _item.id
                        })

                        _this.setState({
                            rows : filteredItem
                        })
                        return `${item.name} deleted successfully!`
                    },
                    icon: "",
                },    
                error: {
                    render({res}){
                        return `${res} cannot deleted!`
                    }
                }
            })
        //this.deleteSelected(data, config);
    }

    deleteSelected(data, config){
       return API.deleteProduct(data);// axios.post(process.env.REACT_APP_SERVER_HOST + "/products/delete-product",data, config)
    }

    async LoadData()
    {
        let filename = "";
        let columns1 = [ { field: '_id', headerName: 'No.' , },
                    { field: 'thumbnail', headerName: 'Thumbnail',height: 120, renderCell: (params) => <img src={params.value} width = "70" height = "70" />},
                    { field: 'name',  headerName: 'Name',type: 'string',minWidth: 200, resizable: true },
                    { field: 'categoryname',  headerName: 'Category',type: 'string'},
                    { field: 'price',headerName: 'Price', },
                    { field: 'quantity',headerName: 'Quantity (tax excl.)',   },
                    { field: 'show',headerName: 'Online'},
                    { field: 'createdAt',headerName: 'CreatedAt'},
                    { 
                        field: 'edit', 
                        headerName: 'Edit',
                        width: 150,
                        height: 120,
                      
                        renderCell: (cell) => {
                          return (
                            <Button variant="contained" color="primary" onClick={(e)=>this.editProduct(cell)} startIcon={<EditIcon />}>
                              Edit
                            </Button>
                          );
                    }},
                    { 
                        field: 'delete', 
                        headerName: 'Delete',
                        width: 150,
                        height: 120,
                      
                        renderCell: (cell) => {
                          return (
                            <Button
                              variant="contained"
                              color="secondary"
                              startIcon={<DeleteIcon />}
                              onClick={(e)=>this.deleteprod(cell)}
                            >

                              Delete
                            </Button>
                          );
                        }
                    },
        ];
        this.setState({ columns: columns1 });
        let row1 = [{ id:1, thumbnail : hair1, filename : hair1, __V: "Snow", qr: 'jon', name: 'Normal', price: '$1233', type: '10', show: 'available', store: '11', createdAt: '111' }];
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization:JSON.parse(localStorage.getItem("token")).token,
            },
        };

        let resp= await API.getProducts("all");

        //  axios.get(process.env.REACT_APP_SERVER_HOST + "/products/get_all_products",config);
        // let resp = response.data;
        resp=  resp.map((item,index)=> {
            item.id=item._id
            item.categoryname=item.category?.name
            item._id= index + 1
            return item;
        })
        this.setState({
            rows: resp
        });
        let ids=[];
        for(let ind=0;ind<resp.length;ind++){
            ids.push(resp[ind].images[0].filename)
        }
        var data = {
            'fileids': ids,
        }
        // formData.append("fileCount",  this.state.images.length);
        const allfilesImagw = await API.get_images(data);
        
        // axios.post(process.env.REACT_APP_SERVER_HOST + "/products/get_images",data,config)
        
        for(let i=0; i < allfilesImagw.length;i++){
            let item1 = resp.filter((item)=>item.images[0].filename=== allfilesImagw[i].filename)[0]
            resp[i].thumbnail = allfilesImagw[i].img;
            this.setState({rows: resp});
        } 
        
        this.setState(
        {
            rows: resp,
            copyRows: resp
        });
        
        this.setState({
            paginationCount: resp.length,
            currentPage: 1
        })
    }

    editProduct(item){
        console.log("item",item)
        this.props.history.push({
            pathname : '/products/addproducts',
            state :{
                      selectdata : item
                   }
            } 
        );
    }

    setPagination(item){
        this.setState({
            currentPage: item,
            rows : this.state.copyRows
        });
        const startIndex = item * this.state.paginationLimit - this.state.paginationLimit;
        const endIndex = startIndex + this.state.paginationLimit;
        let sortArr = this.state.copyRows.sort((a,b) => (a._id > b._id) ? 1 : ((b._id > a._id) ? -1 : 0))
        let newRow = sortArr.slice(startIndex, endIndex);

        this.setState({
            rows: newRow
        })
    }

    nextPagination(e)
    {
        e.preventDefault();
        if (this.state.paginationCount === (this.state.currentPage * this.state.paginationLimit)) return;
        const startIndex = ((this.state.currentPage + 1) * this.state.paginationLimit) - this.state.paginationLimit;
        const endIndex = startIndex + this.state.paginationLimit;
        let sortArr = this.state.copyRows.sort((a,b) => (a._id > b._id) ? 1 : ((b._id > a._id) ? -1 : 0))
        let newRow = sortArr.slice(startIndex, endIndex);

        this.setState({
            rows: newRow,
            currentPage : this.state.currentPage + 1
        })
    }

    prevPagination(e){
        e.preventDefault();
        if (this.state.currentPage === 1) return;
        const startIndex = ((this.state.currentPage - 1) * this.state.paginationLimit) - this.state.paginationLimit;
        const endIndex = startIndex + this.state.paginationLimit;
        let sortArr = this.state.copyRows.sort((a,b) => (a._id > b._id) ? 1 : ((b._id > a._id) ? -1 : 0))
        let newRow = sortArr.slice(startIndex, endIndex);
        this.setState({
            rows: newRow,
            currentPage : this.state.currentPage - 1
        })
    }

    render(){

        const paggg=[];

        let count=1;
        for (var i = 0 ;i < this.state.paginationCount; i+=this.state.paginationLimit){
            paggg.push(count);
            count+=1;
        }

        return(
            <DashboardLayout>
                <div className="lg:flex lg:items-center lg:justify-between py-2 px-8">
                    <div className="flex-1 min-w-0">
                        <h2 className="text-2xl font-bold leading-7 text-gray-900 sm:text-3xl sm:truncate mt-5 flex lg:mt-0 lg:ml-4">
                            All Products
                        </h2>
                    </div>
                    <div className="mt-5 flex lg:mt-0 lg:ml-4">
                        <span className="sm:ml-3">
                            <button type="button" onClick = { () => this.props.history.push('/products/addtype') } className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                <svg class="h-6 w-6" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                                    <path fill-rule="evenodd" d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z" clip-rule="evenodd" />
                                </svg>
                                Add Type
                            </button>
                        </span>
                        <span className="sm:ml-3">
                            <button type="button" onClick = { () => this.props.history.push('/products/addproducts') } className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                <svg class="h-6 w-6" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                                    <path fill-rule="evenodd" d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z" clip-rule="evenodd" />
                                </svg>
                                New Product
                            </button>
                        </span>
                    </div>
                </div>
                <div className="flex flex-col">
                    <div className="py-2 align-middle items-end inline-block min-w-full sm:px-6 lg:px-8">
                        <div className="shadow overflow-auto border-b border-gray-200 sm:rounded-lg">
                            <table className="min-w-full divide-y divide-x divide-gray-200">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Id
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Thumbnail
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Name
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Category Name
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Price
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Quantity
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Show
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            CreatedAt
                                        </th>

                                        <th scope="col" className="relative px-6 py-3">
                                            <span className="sr-only">Edit</span>
                                        </th>

                                        <th scope="col" className="relative px-6 py-3">
                                            <span className="sr-only">Delete</span>
                                        </th>
                                    </tr>
                                    </thead>
                                    <tbody className="bg-white divide-y divide-gray-200 overflow-auto">
                                    { this.state.rows && this.state.rows.map((item, index) => {
                                        return<tr>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                { item._id }
                                            </td>
                                            <td className="px-4 py-2 ">
                                                <img className="h-20 w-full rounded-lg object-cover " src={item.thumbnail} alt="Thumbnail"></img>
                                                
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div class="text-sm text-gray-900">{ item.name }</div>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div class="text-sm text-gray-900">{ item.categoryname }</div>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm font-bold">
                                                Rs.{ item.price }
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                                { item.quantity }
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <span class="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                                                    *{ item.show != "" ? "Active" : "" }
                                                </span>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                                                 { (new Date(item.createdAt)).toString() }
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                                <a class="text-indigo-600 hover:text-indigo-900 cursor-pointer" onClick = { () => this.editProduct(item)}>Edit</a>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                                <a class="text-indigo-600 hover:text-indigo-900" onClick = { () => this.deleteprod(item) }>Delete</a>
                                            </td>
                                        </tr>
                                        }) }
                                        
                                    </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <div className = "px-8 py-2 flex items-center justify-between border-t border-gray-200 sm:px-6">
                    <div className = "flex-1 flex justify-between sm:hidden">
                        <a href = "#" onClick = { this.prevPagination } className="relative inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50">
                            Previous
                        </a>
                        <a href="#" onClick = { this.nextPagination } className="ml-3 relative inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50">
                            Next
                        </a>
                    </div>
                    <nav className="relative z-0 inline-flex rounded-md shadow-sm -space-x-px" aria-label="Pagination">
                        <a href="#" onClick = { this.prevPagination } class="relative inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-50">
                            <span class="sr-only">Previous</span>
                            <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                                <path fill-rule="evenodd" d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z" clip-rule="evenodd" />
                            </svg>
                        </a>
                        { paggg.map((item)=>
                            <a href="#" onClick = { () => this.setPagination(item) } aria-current="page" class="z-10 bg-indigo-50 border-indigo-500 text-indigo-600 relative inline-flex items-center px-4 py-2 border text-sm font-medium">
                                {item}
                            </a>
                        ) }
                       

                        
                        {/* <a href="#" class="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 relative inline-flex items-center px-4 py-2 border text-sm font-medium">
                            2
                        </a>
                        <a href="#" class="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 hidden md:inline-flex relative items-center px-4 py-2 border text-sm font-medium">
                            3
                        </a>
                        <span class="relative inline-flex items-center px-4 py-2 border border-gray-300 bg-white text-sm font-medium text-gray-700">
                            ...
                        </span>
                        <a href="#" class="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 hidden md:inline-flex relative items-center px-4 py-2 border text-sm font-medium">
                            8
                        </a>
                        <a href="#" class="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 relative inline-flex items-center px-4 py-2 border text-sm font-medium">
                            9
                        </a>
                        <a href="#" class="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 relative inline-flex items-center px-4 py-2 border text-sm font-medium">
                            10
                        </a> */}
                        <a href="#" onClick = { this.nextPagination } class="relative inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-50">
                            <span class="sr-only">Next</span>
                            <svg class="h-5 w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                                <path fill-rule="evenodd" d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z" clip-rule="evenodd" />
                            </svg>
                        </a>
                    </nav>
                <div>

            </div>
        </div>
    </DashboardLayout>
            // <DashboardLayout>
            //     <div style={{position:'fixed',right:'5%'}}>

            //     <Button style={{ position:'relative', left: '10px', marginTop:'1%' }}  variant="contained" onClick = { () => this.props.history.push('/products/addtype') } >Add Type</Button>
            //     <Button style={{ position:'relative', left: '30px', marginTop:'1%' }}  variant="contained" onClick = { () => this.props.history.push('/products/addproducts') } >New Product</Button>
               
            //     </div>
            //     <div style = {{ margin:"3%" }}>
            //         <div >
            //             { this.state.rows &&(
            //                 <div style={{ height: 600, width: '100%', marginTop:'5%' }}>
            //                     <DataGrid rows={this.state.rows}
            //                               columns={this.state.columns}
            //                               pageSize={30}
            //                               checkboxSelection
                                     
            //                               rowsPerPageOptions={[5]}
            //                               editable
            //                               autoPageSize
            //                               rowLength={100000}
            //                               dataSet= 'Commodity'
            //                             //   onCellClick = { this.currentlySelected }
            //                             //   onRowDoubleClick = { (e) => this.editProduct(e) } 
            //                             />
            //                 </div>
            //             )}
            //         </div>
            //     </div>
            // </DashboardLayout>
        )
    }
}

export default withRouter(ProductsTable);

