/**
 * @module HomePage
 */
import * as React from "react";
import { BrowserRouter, Switch, Link } from "react-router-dom";
import { DashboardLayout } from "../navbar/Layout";
import { withStyles, lighten, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import ReactNotification from "react-notifications-component";
import { store } from "react-notifications-component";
import { OTable } from "./table";
import GroupAddSharpIcon from "@material-ui/icons/GroupAddSharp";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
} from "react-bootstrap";
/**
* @author Muthu G & Balaji K
* @description Table cell Theme
* @param {Object} theme - childrens
*/
const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);
/**
* @author Muthu G & Balaji K
* @description Table Row Theme
*/
const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);
/**
* @author Muthu G & Balaji K
* @description ToolBar Theme
*/
const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  title: {
    flex: "1 1 100%",
  },
}));
/**
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
const Titlebar = (props) => {
  const classes = useToolbarStyles();
  return (
    <Typography
      align="right"
      className={classes.title}
      component="div"
      style={{ width: 40 }}
    >
      <IconButton onClick={props.clickfun}>
        <GroupAddSharpIcon style={{ fontSize: 40 }} />
      </IconButton>
      Add Client
    </Typography>
  );
};
const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});
const headCells = [
  {
    id: "name",
    numeric: false,
    disablePadding: true,
    label: "Client Name",
    link: false,
  },
  {
    id: "username",
    numeric: false,
    disablePadding: true,
    label: "Email",
    link: false,
  },
];
/**
 * @module AllStores_react_Component
* @author Muthu G & Balaji K
* @description AllStores
* @param {Object} props - Props from parent
*/
class AllStores extends React.Component {
  constructor() {
    super();
    this.state = {
      show: false,
      dataget: undefined,
    };
  }

  GetAllBranches() {
    // POST request using fetch with set headers
    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/AllStores", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ dataget: data });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentWillMount
  * @description It will mount before start
  */
  componentWillMount() {
    this.GetAllBranches();
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    const classes = [{}]; // useStyles();

    const handleClose = () => this.setState({ show: false });
    const handleShow = () => this.setState({ show: true });

    const handleSubmit = (event) => {
      const form = event.currentTarget;
      const dataf = new FormData(event.currentTarget);
      {
        event.preventDefault();
        event.stopPropagation();
        const requestOptions = {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: JSON.parse(localStorage.getItem("token")).token,
          },
          body: JSON.stringify({
            name: this.state.name,
            username: this.state.username,
            password: this.state.password,
            addressline1: this.state.addr1,
            addressline2: this.state.addr2,
            city: this.state.city,
            state: this.state.state,
            postcode: this.state.postcode,
            country: this.state.country,
          }),
        };
        fetch(process.env.REACT_APP_SERVER_HOST + "/addclient", requestOptions)
          .then((response) => response.json())
          .then((data) => {
            this.GetAllBranches();
            store.addNotification({
              title: "Clint!",
              message: "New Client Added",
              type: "success",
              insert: "top",
              container: "top-right",
              animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
              animationOut: ["animate__animated animate__fadeOut"],
              dismiss: {
                duration: 5000,
                onScreen: false,
              },
              slidingExit: {
                duration: 800,
                timingFunction: "ease-out",
                delay: 0,
              },
            });
          });
      }
    };

    return (
      <DashboardLayout>
        <Row style={{ height: 30 }}></Row>
        <Container fluid>
          <Row className="offset-8">
          </Row>
          <Row style={{ height: 30 }}></Row>
          <Row>
            <Col xs={10}>
              {this.state.dataget && (
                <OTable
                  Title="All Clients"
                  headCells={headCells}
                  tableData={this.state.dataget}
                  selecting={this.selecting}
                  delete={(row) => this.delete(row)}
                  edit={(row) => this.edit(row)}
                  isedit={false}
                  clickLink={(e, row) => {
                    e.preventDefault();
                    this.props.history.push("/branch/" + row._id);
                  }}
                  Titlebar={<Titlebar clickfun={handleShow} />}
                ></OTable>
              )}
            </Col>
          </Row>
        </Container>
        <Modal show={this.state.show} onHide={handleClose}>
          <Form onSubmit={handleSubmit}>
            <Modal.Header closeButton>
              <Modal.Title>New Client</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form.Group controlId="name">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label> Name</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) => this.setState({ name: e.target.value })}
                      placeholder="Store Name"
                      name="name"
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="username">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>User Name</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="email"
                      onChange={(e) =>
                        this.setState({ username: e.target.value })
                      }
                      placeholder="Email"
                      name="username"
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="password">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Password</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="password"
                      onChange={(e) =>
                        this.setState({ password: e.target.value })
                      }
                      placeholder="Passsword"
                      name="password"
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                Close
              </Button>
              <Button type="submit" variant="primary" onClick={handleClose}>
                Add Client
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      </DashboardLayout>
    );
  }
}

export default AllStores;
