import axios from "axios";
import { API_Urls } from "./config";
const GetHeader=()=>{
return { headers: {
    'Content-Type': 'application/json',
    Authorization: JSON.parse(localStorage.getItem("token")).token,
  }}
}

const API ={
    login:async(data)=>{
        const headers={ headers: {
            'Content-Type': 'application/json',
    }}
        const allfiles = await axios
        .post(API_Urls.server + "login",data,headers)
        return allfiles.data;
    },
    getProducts:async(origin)=>{
        const allfiles = await axios
        .get(API_Urls.Product + "get_all_products/" + origin, GetHeader())
        return allfiles.data;
    },
    get_images:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Product + "get_images", data, GetHeader())
  
        return allfilesImagw.data;
    },
    addProduct:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Product + "addproduct", data, GetHeader())
  
        return allfilesImagw.data;
    },
    updateProduct:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Product + "update-products", data, GetHeader())
  
        return allfilesImagw.data;
    },
    deleteProduct:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Product + "delete-product", data, GetHeader())
  
        return allfilesImagw.data;
    },
    get_alltype:async(origin)=>{
        const allfilesImagw = await axios
        .get(API_Urls.Type + "alltype/" + origin , GetHeader())
  
        return allfilesImagw.data;
    },
    add_type:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Type + "addtype",data, GetHeader())
  
        return allfilesImagw.data;
    },
    update_type:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Type + "updatetype",data, GetHeader())
  
        return allfilesImagw.data;
    },
    delete_type:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Type + "deletetype",data, GetHeader())
  
        return allfilesImagw.data;
    },
    get_Categories:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Category + "category",data, GetHeader())
  
        return allfilesImagw.data;
    },
   add_Categories:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Category + "addcategory",data, GetHeader())
  
        return allfilesImagw.data;
    },
   update_Categories:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Category + "updatecategory",data, GetHeader())
  
        return allfilesImagw.data;
    },
    delete_Categories:async(data)=>{
        const allfilesImagw = await axios
        .post(API_Urls.Category + "deletecategory",data, GetHeader())
  
        return allfilesImagw.data;
    },
    get_Type: async(_id) => {
        const type = await axios.get(API_Urls.Type + "gettype/" + _id, GetHeader());
        return type.data
    }
}
export default API;