/**
 * @module All_Branches
 */
import * as React from "react";
import { BrowserRouter, Switch, Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { DashboardLayout } from "../navbar/Layout";
import { withStyles, makeStyles, lighten } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
} from "react-bootstrap";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import DeleteForeverSharpIcon from "@material-ui/icons/DeleteForeverSharp";
import EditSharpIcon from "@material-ui/icons/EditSharp";
import ReactNotification from "react-notifications-component";
import GroupAddSharpIcon from "@material-ui/icons/GroupAddSharp";
import Typography from "@material-ui/core/Typography";
import { OTable } from "./table";
import { store } from "react-notifications-component";
import Loader from "react-loader-spinner";
require("dotenv").config();

/**
* @author Muthu G & Balaji K
* @description ToolBar Theme
*/
const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  title: {
    flex: "1 1 100%",
  },
}));
/**
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
const Titlebar = (props) => {
  const classes = useToolbarStyles();
  return (
    <Typography
      align="right"
      className={classes.title}
      component="div"
      style={{ width: 40 }}
    >
      <IconButton onClick={props.clickfun}>
        <GroupAddSharpIcon style={{ fontSize: 40 }} />
      </IconButton>
      Add Branch
    </Typography>
  );
};
const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});
const headCells = [
  {
    id: "name",
    numeric: false,
    disablePadding: true,
    label: "Name",
    link: true,
  },
  {
    id: "addressline2",
    numeric: true,
    disablePadding: false,
    label: "Address",
    link: false,
  },
  {
    id: "city",
    numeric: true,
    disablePadding: false,
    label: "City",
    link: false,
  },
  {
    id: "state",
    numeric: true,
    disablePadding: false,
    label: "State",
    link: false,
  },
];
/**
 * @module All_Branches_react_Component
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
class AllBranches extends React.Component {
  constructor() {
    super();
    this.state = {
      show: false,
      dataget: undefined,
      showLoader:true,
    };
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name Get_all_Branches
  * @description It will getting all branches
  */
  GetAllBranches() {
    // POST request using fetch with set headers
    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/Allbranches", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ dataget: data });
            this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name edit
  * @description It will Edit the row
  * @param {object} row -row
  */
  edit(row) {
    this.setState({ update: true });
    this.setState({ show: true });
    this.setState({ name: row.name });
    this.setState({ addr1: row.addressline1 });
    this.setState({ addr2: row.addressline2 });
    this.setState({ city: row.city });
    this.setState({ state: row.state });
    this.setState({ postcode: row.postcode });
    this.setState({ country: row.country });
    this.setState({ branchUsers: row.branchUsers });
    this.setState({ id: row._id });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name delete
  * @description It will delete the row
  * @param {object} row -row
  */
  delete(row) {
    this.setState({ showLoader: true });
    const dataf = new FormData();
    {
      const requestOptions = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: JSON.parse(localStorage.getItem("token")).token,
        },
        body: JSON.stringify({
          id: row._id,
        }),
      };

      var url = process.env.REACT_APP_SERVER_HOST + "/deletebranch";
      fetch(url, requestOptions)
        .then((response) => response.json())
        .then((data) => {
          this.GetAllBranches();
          store.addNotification({
            title: "Branch!",
            message: "Branch has deleted",
            type: "danger",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
            animationOut: ["animate__animated animate__fadeOut"],
            dismiss: {
              duration: 5000,
              onScreen: false,
            },
            slidingExit: {
              duration: 800,
              timingFunction: "ease-out",
              delay: 0,
            },
          });
              this.setState({ showLoader: false });
        });
    }
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentWillMount
  * @description It will mount before start
  */
  componentWillMount() {
    this.GetAllBranches();
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    const classes = [{}];

    const handleClose = () => this.setState({ show: false });
    const handleShow = () => this.setState({ show: true });

    const handleSubmit = (event) => {
          this.setState({ showLoader: true });
      const form = event.currentTarget;
      const dataf = new FormData(event.currentTarget);
      {
        event.preventDefault();
        event.stopPropagation();
        const requestOptions = {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: JSON.parse(localStorage.getItem("token")).token,
          },
          body: JSON.stringify({
            name: this.state.name,
            addressline1: this.state.addr1,
            addressline2: this.state.addr2,
            city: this.state.city,
            state: this.state.state,
            postcode: this.state.postcode,
            country: this.state.country,
            branchUsers: this.state.branchUsers,
            id: this.state.id,
          }),
        };
        var url = process.env.REACT_APP_SERVER_HOST + "/addbranch";
        if (this.state.update)
          url = process.env.REACT_APP_SERVER_HOST + "/updatebranch";
        fetch(url, requestOptions)
          .then((response) => response.json())
          .then((data) => {
            this.GetAllBranches();
            store.addNotification({
              title: "Branch!",
              message: this.state.update ? "Updated Branch" : "Branch Added",
              type: "success",
              insert: "top",
              container: "top-right",
              animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
              animationOut: ["animate__animated animate__fadeOut"],
              dismiss: {
                duration: 5000,
                onScreen: false,
              },
              slidingExit: {
                duration: 800,
                timingFunction: "ease-out",
                delay: 0,
              },
            });
                this.setState({ showLoader: false });
          });
      }
    };

    return (
      <DashboardLayout>
        <Row style={{ height: 30 }}></Row>
        <Container fluid>
          <Row className="offset-8"></Row>
          <Row style={{ height: 30 }}></Row>
          <Row>
          <Loader type="Watch"
            color="#00BFFF"
            height={100}
            width={100}
            visible={this.state.showLoader}
            style={{
         display: "flex",
         justifyContent: "center",
         alignItems: "center",
         height:"60vh",
         position:"absolute",
         left: 0,/*added*/
         right: 0, /*added*/
         zIndex:1
          }}
          />
            <Col xs={10}>
              {this.state.dataget && (
                <OTable
                  Title="Branches"
                  headCells={headCells}
                  tableData={this.state.dataget}
                  selecting={this.selecting}
                  delete={(row) => this.delete(row)}
                  edit={(row) => this.edit(row)}
                  isedit={true}
                  clickLink={(e, row) => {
                    e.preventDefault();
                    this.props.history.push("/branch/" + row._id);
                  }}
                  Titlebar={<Titlebar clickfun={handleShow} />}
                ></OTable>
              )}
            </Col>
          </Row>
        </Container>
        <Modal show={this.state.show} onHide={handleClose}>
          <Form onSubmit={handleSubmit}>
            <Modal.Header closeButton>
              <Modal.Title>New Branch</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form.Group controlId="name">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Name</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) => this.setState({ name: e.target.value })}
                      placeholder="Branch Name"
                      name="name"
                      value={this.state.name}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="addressline1">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Address Line 1</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) => this.setState({ addr1: e.target.value })}
                      placeholder="Address Line 1"
                      name="addressline1"
                      value={this.state.addr1}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="addressline2">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Address Line 2</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) => this.setState({ addr2: e.target.value })}
                      placeholder="Address Line 2"
                      name="addressline1"
                      value={this.state.addr2}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="city">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>City</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) => this.setState({ city: e.target.value })}
                      placeholder="City"
                      name="city"
                      value={this.state.city}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="state">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>State</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) => this.setState({ state: e.target.value })}
                      placeholder="State"
                      name="state"
                      value={this.state.state}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="postcode">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Postcode</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="number"
                      onChange={(e) =>
                        this.setState({ postcode: e.target.value })
                      }
                      placeholder="Postcode"
                      name="postcode"
                      value={this.state.postcode}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
              <Form.Group controlId="country">
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Country</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      type="text"
                      onChange={(e) =>
                        this.setState({ country: e.target.value })
                      }
                      placeholder="country"
                      name="postcode"
                      value={this.state.country}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                Close
              </Button>
              <Button type="submit" variant="primary" onClick={handleClose}>
                {this.state.update ? "update" : "Add Branch"}
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      </DashboardLayout>
    );
  }
}

export default withRouter(AllBranches);
