import * as React from 'react';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { DashboardLayout } from "../navbar/Layout";
import { height } from '@mui/system';
import '../SareeImporter/SareeImporter.css';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import FilledInput from '@mui/material/FilledInput';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';
import Button from '@mui/material/Button';
import ImageBox from './imagebox';
import axios from "axios";
import { withRouter } from 'react-router';
import MenuItem from '@mui/material/MenuItem';
import FormHelperText from '@mui/material/FormHelperText';
import Select from '@mui/material/Select';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import API from "../../api/services";
toast.configure();
class SareeImporter extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            selectedFile: null,
            sareeFile: null,
            palluFile: null,
            blouseFile: null,
            productprice: 0,
            isUpdate: false,
            buttontext: 'Add Product',
            productname: '',
            productquantity: 1,
            name: '',
            qr: '',
            quantity: '',
            price: '',
            types: [],
            selectWidth: '0%',
            selectCategory:[],
            images: [
                {
                    url: '',
                    title: 'Saree',
                    width: '30%',
                    height: '300px',
                    top: '0px'
                },
                {
                    url: '',
                    title: 'Pallu',
                    width: '30%',
                    height: '300px',
                    top: '0px'
                },
                {
                    url: '',
                    title: 'Blouse',
                    width: '40%',
                    height: '200px',
                    top: '50px'
                },
            ],
            nonSareeImages: [
                {
                    url: '',
                    title: '',
                    width: '100%',
                    height: '300px',
                    top: '0px'
                }
            ],
        };
        this.onFileChange = this.onFileChange.bind(this);
        this.createProduct = this.createProduct.bind(this);
        this.updateProduct = this.updateProduct.bind(this);
    }

    async LoadData() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            },
        };

        let selectData = this.props.location.state.selectdata;
        if (selectData) {
            this.setState({
                selectData,
                name: selectData.name,
                qr: selectData.qr,
                quantity: selectData.quantity,
                price: selectData.price,
                isUpdate: true,
                buttontext: 'Update Product'
            })

            let ids = [];
            for (let ind = 0; ind < selectData.images.length; ind++) {
                ids.push(selectData.images[ind].filename)
            }
            var data = {
                'fileids': ids,
            }
            // formData.append("fileCount",  this.state.images.length);
            let images = this.state.images;
            const allfilesImagw = await API.get_images(data);//  axios.post(process.env.REACT_APP_SERVER_HOST + "/products/get_images", data, config)
            //console.log("allfilesImagw", allfilesImagw);
            let textures = {};
            for (let i = 0; i < allfilesImagw.length; i++) {
                let item1 = selectData.images.filter((item) => item.filename === allfilesImagw[i].filename)[0]
                if (item1.originalname.toUpperCase() === "SAREE") {
                    let sareeimg = images.filter((item) => item.title === 'Saree')[0];
                    sareeimg.url = allfilesImagw[i].img;
                }
                else if (item1.originalname.toUpperCase() === "PALLU") {
                    let sareeimg = images.filter((item) => item.title === 'Pallu')[0];
                    sareeimg.url = allfilesImagw[i].img;
                }
                else if (item1.originalname.toUpperCase() === "BLOUSE") {
                    let sareeimg = images.filter((item) => item.title === 'Blouse')[0];
                    sareeimg.url = allfilesImagw[i].img;
                }
            }
            this.setState({ images: [] })
            this.setState({ images })
        }
    }

    componentDidMount() {
        console.log("this.props",this.props)
        if (this.props.location.state)
            this.LoadData();

        this.loadCatlog();
        //console.log("this.props.location.state._id", this.props.location);
    }
    async loadCatlog() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            }
        };
        const types =  await API.get_alltype();
        //  axios
        //     .get(process.env.REACT_APP_SERVER_HOST + "/products/alltype", config)
        // console.log(response);

        // const types = response.data;

        for (let ind = 0; ind < types.length; ind++) {
            var data = {
                _id: types[ind]._id
            }
            types[ind].catlog  = await API.get_Categories(data);
            //  axios
            //     .post(process.env.REACT_APP_SERVER_HOST + "/products/category", data, config)
            // console.log(responsecat);
            // types[ind].catlog = responsecat.data;
        }
        //console.log("Types",types);
        let newCategories = types[0].catlog;
        let newAllCateogories = [];
        for (var i = 0; i < newCategories.length; i++){
            let category = newCategories[i];
            let categoryname = category.name.toString().toLowerCase();
            if (categoryname==="saree"){
                category.selectedImage = this.state.images;
            }else{
                let selectimage = this.state.nonSareeImages;
                category.selectedImage = selectimage;
            }
            newAllCateogories[i] = category;
        }
        
        this.setState({
            types: types,
            type: types[0]._id.toString(),
            Categories: newAllCateogories,
            Category: types[0].catlog[0]?._id
        })
        setTimeout(() => {
            //console.log("Timer111",this.state);
            this.setState({
                type: types[0]._id.toString(),
            })
        }, 20);
    }
    onFileChange = event => {
        //console.log("Saree");
        const reader = new FileReader();
        reader.onload = async (e) => {
            const text = e.target.result;
            const { selectCategory, curImage } = this.state;
            let cur = selectCategory.filter((im) => im.title === curImage)[0];
            cur.url = text;
            //console.log("text", curImage);
            this.setState({ selectCategory: [] });
            this.setState({ selectCategory: selectCategory });
        };
        if (event.target.files[0])
            reader.readAsDataURL(event.target.files[0]);
    };

    imageClick = () => {
        document.getElementById("choosefile").click();
    }

    dataURLtoFile(dataurl, filename) {
        var arr = dataurl.split(','),
            mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]),
            n = bstr.length,
            u8arr = new Uint8Array(n);

        while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
        }

        return new File([u8arr], filename, { type: mime });
    }
    async createProduct(e) {
        //this.addProduct();
        e.preventDefault();
        const addProduct = this.addProduct();
        let _this = this;
        toast.promise(
            addProduct,
            {
                pending: {
                    render(){
                        return "Adding.."
                    },
                    icon: false,
                },
                success: {
                    render(res){
                        _this.setState({
                            qr: "",
                            name: "",
                            quantity:"",
                            price: ""
                        })
                        return `Product created successfully!`
                    },
                    icon: "",
                },    
                error: {
                    render({res}){
                        return `${res} cannot added!`
                    }
                }
        })
    }

    async addProduct(){
        const config = {
            headers: {
                "content-type": "multipart/form-data",
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            },

            onUploadProgress: (progressEvent) => {
                console.log({
                    progress: Math.round(
                        (100 * progressEvent.loaded) / progressEvent.total
                    )
                });
                this.setState({
                    progress: Math.round(
                        (100 * progressEvent.loaded) / progressEvent.total),
                });
            },
        };
        console.log("this.state.Category",this.state.Category.name);
        const formData = new FormData();
        formData.append("qr", this.state.qr);
        formData.append("name", this.state.name);
        formData.append("quantity", this.state.quantity);
        formData.append("price", this.state.price);
        formData.append("fileCount", this.state.images.length);
        formData.append("type", this.state.type)
        formData.append("category", this.state.Category._id)
        // formData.append("images","form data");
        for (let ind = 0; ind < this.state.selectCategory.length; ind++) {
            formData.append("images", this.dataURLtoFile(this.state.selectCategory[ind].url, this.state.selectCategory[ind].title));
        }
        const response = await API.addProduct(formData);
        console.log("response",response)
        //  axios.post(process.env.REACT_APP_SERVER_HOST + "/products/addproduct", formData, config)
        // console.log(response.data.data);
        // const formDataImage = new FormData();
        // formDataImage.append("filename", response.data.data.images[0].filename);
        // return axios.get(process.env.REACT_APP_SERVER_HOST + "/products/get_File/" + response.data.data.images[0].filename, config)
    }

    async updateProduct(e) {
        //console.log("ee", e);
        //console.log("this.props.location.state._id", this.props.location.state.selectdata._id);
        const productupdates = this.productUpdate();
        toast.promise(
            productupdates,
            {
                pending: {
                    render(){
                        return "Updating.."
                    },
                    icon: false,
                },
                success: {
                    render(res){
                        return `Product Updated successfully!`
                    },
                    icon: "",
                },    
                error: {
                    render({res}){
                        return `cannot updated!`
                    }
                }
        })
    }

   async productUpdate(){
        const config = {
            headers: {
                "content-type": "application/json",
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            },
        };

        var data = {
            'qr': this.state.qr,
            'name': this.state.name,
            'quantity': this.state.quantity,
            'price': this.state.price,
            'fileid': this.state.selectData.id,
            'category': this.state.Category
        }
        // const formData = new FormData();
        // formData.append("qr",  this.state.qr);
        // formData.append("name",  this.state.name);
        // formData.append("quantity",  this.state.quantity);
        // formData.append("price",  this.state.price);
        // formData.append("fileid",  this.props.location.state.selectdata._id);

        return await API.updateProduct(data);// axios.post(process.env.REACT_APP_SERVER_HOST + "/products/update-products", data, config)
        //console.log("Update Details", response);
    }
    chamgeType(e) {
        //console.log(this.state.types);
        let _type = e.target.value;
        let catlog = this.state.types.filter((item) => item._id === _type._id)[0];
        
        let newCategories = catlog.catlog;
        console.log("_type",_type);
        console.log("newCategories",newCategories);
        let newAllCateogories = [];
        for (var i = 0; i < newCategories.length; i++){
            let category = newCategories[i];
            let categoryname = category.name.toString().toLowerCase();
            if (categoryname==="saree"){
                category.selectedImage = this.state.images;
            }else{
                let selectimage = this.state.nonSareeImages;
                category.selectedImage = selectimage;
            }
            newAllCateogories[i] = category;
        }
        this.setState({
            type: _type.name,
            Categories: newAllCateogories
        })
    }
    chamgeCategory(e) {
        let selectCateogry = e.target.value;
        console.log("selectCateogry",selectCateogry);
        let selectImage = selectCateogry.selectedImage;
        selectImage[0].title = selectCateogry.name;
        this.setState({
            selectCategory : selectImage
        })
        this.setState({
            Category: e.target.value,
        })
    }
    render() {

        return (
            <DashboardLayout>
                
                <div className="px-5 py-5">
                    <div className='px-2 py-2 font-bold text-lg'>Create New Product</div>
                    <Box
                        component="form"
                        sx={{
                            '& .MuiTextField-root': { m: 1, width: '25ch' },
                        }}
                        noValidate
                        autoComplete="off"
                    >  <TextField
                            required
                            id="outlined-required"
                            label="Product QR"
                            defaultValue=""
                            value={this.state.qr}
                            onChange={(e) => this.setState({ qr: e.target.value })}
                        />
                        <TextField
                            required
                            id="outlined-required"
                            label="Product Name"
                            defaultValue=""
                            value={this.state.name}
                            onChange={(e) => this.setState({ name: e.target.value })}
                        />
                        <FormControl sx={{ m: 1 }}>
                            <InputLabel htmlFor="outlined-adornment-amount">Price</InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-amount"
                                value={this.state.price}
                                onChange={(e) => this.setState({ price: e.target.value })}
                                startAdornment={<InputAdornment position="start"></InputAdornment>}
                                label="Price"
                                type="number"
                            />
                        </FormControl>
                        <FormControl sx={{ m: 1 }}>
                            <InputLabel htmlFor="outlined-adornment-amount">Quantity</InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-amount"
                                value={this.state.quantity}
                                onChange={(e) => this.setState({ quantity: e.target.value })}
                                startAdornment={<InputAdornment position="start">No.</InputAdornment>}
                                label="Quantity"
                                type="number"
                                default={1}/>
                        </FormControl>
                        <FormControl sx={{ m: 1, minWidth: 120 }}>
                            <InputLabel id="types-label">Type</InputLabel>
                            <Select labelId="types"
                                    id="types-label"
                                    value={this.state.type}
                                    label="Types"
                                    onChange={(e) => this.chamgeType(e)}>
                                {this.state.types.map((item, index) =>
                                    <MenuItem value={item}>
                                        {item.name}
                                    </MenuItem>
                                )}
                            </Select>
                            {/* <FormHelperText>With label + helper text</FormHelperText> */}
                        </FormControl>
                        {this.state.type && (
                            <FormControl sx={{ m: 1, minWidth: 120 }}>
                                <InputLabel id="Category-label">Category</InputLabel>
                                <Select labelId="Category"
                                        id="types-label"
                                        value={this.state.Category}
                                        label="Category"
                                        onChange={(e) => this.chamgeCategory(e)}>
                                    {this.state.Categories && this.state.Categories.map((item, index) =>
                                        <MenuItem value={item}>
                                            {item.name}
                                        </MenuItem>
                                    )}
                                </Select>
                                {/* <FormHelperText>With label + helper text</FormHelperText> */}
                            </FormControl>
                        )}

                        {this.state.images && (

                            <ImageBox onclickBtn={(val) => {
                                this.setState({ curImage: val })
                                this.imageClick()
                            }} images={this.state.selectCategory} 
                               width = { this.state.selectWidth }/>


                        )}

                        <input style={{ visibility: "hidden" }} id="choosefile" type="file" accept="image/*" onChange={this.onFileChange}></input>

                        <Button style={{ left: '10%', top: '20px' }} variant="contained" size="large" onClick={(e) => {
                            if (!this.state.isUpdate) {
                                this.createProduct(e)
                            } else {
                                this.updateProduct(e)
                            }
                        }}>
                            {this.state.buttontext}
                        </Button>
                    </Box>
                </div>
                
            </ DashboardLayout>
        )
    }

}

export default withRouter(SareeImporter);