import * as React from 'react';
import axios from "axios";
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import Box from '@mui/material/Box';
//import CloseSharp from '@mui/icons-material/CloseOutlined';
import CloseTwoTone from '@mui/icons-material/CloseTwoTone';
import Button from '@mui/material/Button';
import SherwaniIcon from '../../Icons/sherwaniIcon.jsx';
import closeicon from '@mui/icons-material/CloseSharp'
import API from '../../api/services';
import ProductCard from '../ProductCard/productCard';
import { timers, type } from 'jquery';
import { StringServices } from '../../Utils/services.js';
import sherwaniPant from '../../Icons/sherwaniPant.svg';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl'
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import './ProductsTable.css'
import SelectMenu from '../Controls/SelectMenu.jsx';
class ProductList extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      products:this.props.products,
      types: [],
      category: [],
      selectCategory: [],
      favourites:[],
      tempCategory: [],
      origin: '',
      currentCategory:'All',
      categoryTitle: 'Select Category'
    };
  }

  componentWillReceiveProps(next){
    if(next.products.length>0&&this.state.products.length===0){
      this.setState({
        products: next.products
      });
    }
  }
  componentDidMount() {
      const config = {
        headers: {
            'Content-Type': 'application/json',
            Authorization: JSON.parse(localStorage.getItem("token")).token,
        }
      };
      this.setState({
        products: this.props.products
      });
      this.LoadData();
  }
  async LoadData() {
    const config = {
      headers: {
          'Content-Type': 'application/json',
          Authorization: JSON.parse(localStorage.getItem("token")).token,
      }
    };
    let types = await API.get_alltype(this.props.origin);
    
    types = types.filter((data, index) => {
      return this.props.origin === data.origin
    })
    
    let categoires = [];
    let _types = [];
    for (let ind = 0; ind < types.length; ind++) {
        var data = {
            _id: types[ind]._id
        }
        _types.push(types[ind].name)
        const responsecat = await API.get_Categories(data);
        types[ind].category = responsecat;
        categoires.push(responsecat);
    }
    // console.log("categoires[0]",categoires[0])
    // let AllCategory = {
    //   name: 'All'
    // }
    // categoires[0].push(AllCategory)
    if (categoires[0] !== undefined){
      this.setState({
        category: [{name: "All"},...categoires[0]],
        tempCategory: [{name: "All"},categoires[0]],
      })
    }

    if (types !== undefined){
      this.setState({
        types: [{name:'All'},{name:'Favourites'}, ...types],
      })
    }
  }


  selectCategory(e, key){
    // this.clearAll();
    // this.clearBG(e, key);
    this.setState({ categoryTitle: this.GetCategoryName(e.name)})
    this.setState({
      currentCategory: e.name
    })
    if (e.name === "All" || !e.name){
      this.setState({
        products: this.props.products,
        isCategoryShow: false
      });
      return;
    }

    if (e.name === 'Favourites' || !e.name){
      this.setState({
        products: this.state.products,
        isCategoryShow: false
      })
      return;
    }

    let category = this.state.category;
    let filterCategory = category.filter((item, index) => {
      return item?.type?._id === e._id
    })
    this.setState({
      selectCategory : filterCategory,
      isCategoryShow: true
    })
    
    let allProd = [];
    allProd = this.props.products;
    let filteProd = allProd.filter((item, index) => {
        return item.category.type === e._id 
    });
    
    this.setState({
      products: filteProd
    });
  }

  selectCategoryFab(e, key){
    this.setState({ categoryTitle: this.GetCategoryName(e.name)})
    this.setState({
      currentCategory: e.name
    })
    if (e.name === "All" || !e.name){
      this.setState({
        products: this.props.products,
        isCategoryShow: false
      });
      return;
    }
    let allProd = this.props.products;
    let filterProd = allProd.filter((item, index) => {
      console.log(item, e._id)
      return item.category._id === e._id
    })

    this.setState({
      products: filterProd
    })
  }

  GetCategoryName(str){
    if (str === undefined || str === "") return;
    if (str.includes('_')){
      var str1 = str.split('_');
      return StringServices.InitCap(str1[1]);
    }else{
      return str;
    }
  }

  SelectFav(item, index){
    const {products} = this.state;
    products[index].isFav = !item.isFav;
    // if (favourites.includes(item)){
    //   const index = favourites.indexOf(item);
    //   if (index > -1) {
    //     favourites.splice(index, 1); // 2nd parameter means remove one item only
    //   }
    // }else{
    //   favourites.push(item);
    // }
    // this.setState({
    //   products: []
    // })
    this.setState({
      products: products
    })
    this.forceUpdate()
  }

  card(item,index){
    if(this.state.currentCategory==='Favourites'&&!item.isFav) return;

    return (<div class="max-w-md w-full shadow-lg rounded-xl p-3 hover:bg-gray-500 cursor-pointer" style={{backgroundColor:"#4c4c6e"}}>
                <div class="flex flex-col">
                    <div class="">
                      <div class="relative">
                        <div class="absolute flex flex-col top-0 right-0 p-3">
                            <button class="transition ease-in duration-300 bg-gray-800  hover:text-purple-500 shadow hover:shadow-md text-gray-500 rounded-full w-8 h-8 text-center p-1">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-pressed="false" onClick={(e) => { this.SelectFav(item, index)}}>
                                    <path stroke-linecap="round" fill={item.isFav? 'red' : 'transparent'} stroke-linejoin="round" stroke-width="2" d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                                </svg>
                            </button>
                        </div>
                        <img src={ item.img } alt="Just a flower" class="h-48 w-full object-fill rounded-2xl" onClick={(e)=>this.props.SelectProduct(e,item)}></img>
                      </div>
                      <div class="flex-auto justify-evenly">
                        <div class="flex flex-wrap ">
                          <div class="w-full flex-none text-sm flex items-center text-gray-600">
                            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 text-red-500 mr-1" viewBox="0 0 20 20" fill="currentColor">
                                <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                            </svg>
                          </div>
                          <div class="flex items-center w-full  justify-between min-w-0">
                            <h2 class="text-base mr-auto font-semibold cursor-pointer text-gray-200 hover:text-purple-400 truncate ">{ item.name}</h2>
                            <div class="flex items-center bg-indigo-500 font-medium text-xxs text-white px-1 py-1 ml-3 rounded-lg">Instock</div>
                          </div>
                        </div>
                        <div class="flex flex-wrap ">
                          <div class="flex items-center w-2/3 justify-between min-w-0">
                            <h2 class="text-base mr-auto cursor-pointer text-gray-200 hover:text-purple-400 truncate font-bold">{ item.price }</h2>
                            <div class=" items-center bg-pink-600 text-white text-xs px-2 py-1 ml-3 rounded-2xl">
                              <span>
                                      <svg xmlns="http://www.w3.org/2000/svg" class="h-5" fill="none" viewBox="2 0 71 24" stroke="currentColor">
                                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                                      <text x="27" y="17" font-size="14" fill="white" stroke="white" stroke-width="0.06">View</text>
                                      </svg>
                              </span>
                            </div>
                          </div>
                        </div>
                        
                      </div>
                    </div>
                  </div>
                </div>)
        }

  render() {
    console.log("this.state.selectCategory",this.state.types)
    return (
      <>
        <div class="overflow-y-hidden h-full border-r-2 border-gray-400">
          <div className='flex w-full grid-cols-2 pt-2 pb-2' style={{ backgroundColor:'#141a3e' }}>
            <div class="text-lg font-bold w-full px-3 text-white">{ this.props.isMen ? "Men Fabrics" : "Women Fabrics" }</div>  
            <div class="relative w-full">
            <div className='absolute right-1 rounded-full border-2 h-5 w-5 mt-1  bg-red-700 py-1 cursor-pointer' onClick={(e) => this.props.Close()}>
                <svg xmlns="http://www.w3.org/2000/svg" className="" viewBox="-2 4 24 24" fill="white">
                  <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                </svg>
            </div>
            </div>
          </div>
           
          <div className='lg:flex lg:flex-row py-1 border-2 border-gray-400 border-opacity-5 px-3' style={{ backgroundColor:'#454d7e' }}>
                { this.state.category[1] === undefined ? (
                  <div></div>
                ): (
                  <ul class="top-level-menu z-10 rounded-full">
                      <li className='rounded-sm'>
                          <a className='rounded-full  cursor-default'>Select Category</a>
                          <ul class="second-level-menu">
                            { this.state.types && this.state.types.map((data, index) => {
                              return <li onClick={() => { this.selectCategory(data, index) }}>
                              <a>{ StringServices.InitCap(data.name) }</a>
                              <ul className='third-level-menu'>
                                { data.category && data.category.map((_cat, _index) => {
                                    return <li className='cursor-pointer'>
                                              <a onClick={() => { this.selectCategoryFab(_cat, _index) }}>{ this.GetCategoryName(_cat.name) }</a>
                                           </li>
                                }) }
                              </ul>
                            </li>
                            }) }
                          </ul>
                      </li>
                  </ul>
                ) }

          </div>
          
          <div className='flex flex-auto h-full overflow-y-visible' style={{ backgroundColor:'#141a3e' }}>
            <div class="max-w-screen-xl w-full py-8 px-4 sm:py-24 sm:px-6 lg:max-w-7xl lg:px-8">
            <div class="grid overflow-visible gap-y-10 sm:grid-cols-1 gap-x-6 xl:grid-cols-4 xl:gap-x-1">
              { this.state.products.map((item, index) => 
                      <>
                       {this.card(item,index)}
                      </>
              ) }
            </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default ProductList;
