import "./skintonemenu.scss";
import React from "react";


/**
 * Main
 */
class SkintoneMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }

  render() {
    return (
      <div>

        <div id="skinmenu">
          <a class="skinmenu-button icon-plus" href="#skinmenu"  title="Show navigation" onClick={()=>this.setState({show:!this.state})} >{this.props.MenuIcon?this.props.MenuIcon: <span class="fas fa-share-alt"></span>}</a>
          <a class="skinmenu-button icon-minus" href="#skinmenu0"  title="Hide navigation">{this.props.MenuIcon?this.props.MenuIcon: <span class="fas fa-share-alt"></span>}</a>
          {this.props.MenuButtons.map((item) => {
            return (
              <div class="skinmenu-item" >
                {item.img}
              </div>
            )
          })}
        </div>
      </div>
    );
  }
}

export default SkintoneMenu;
