import * as React from 'react';
import { withRouter } from 'react-router-dom';
import { DataGrid, GridColDef, GridApi, GridCellValue, GridCellParams } from '@mui/x-data-grid';
import { DashboardLayout } from "../navbar/Layout";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";
import Menuitem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import DropDown from '@mui/material/Select';
import Link from '@mui/material/Link';
import axios from 'axios';
import Input from '@mui/material/Input';
import hair1 from '../../Icons/hair1.png';
import edit from '../../Icons/edit.png';
import Delete1 from '../../Icons/delete.png';
import API from '../../api/services';
import { toast } from 'react-toastify';
toast.configure();

class Type extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rows: [],
            columns: [],
            newTypeTxt: ''
        }
        this.AddNewType = this.AddNewType.bind(this);
        this.CategoryMove = this.CategoryMove.bind(this);
    }

    async CancelEdit(item, index){
        this.state.rows[index].isRowEdit = false;
        this.setState({
            rows: this.state.rows
        })
    }

    async DeleteType(item, index) {
        console.log(item);
        let newArr = this.state.rows.filter((_item) => {
            return _item.id !== item.id
        })

        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            }
        };
        var data = {
            _id: item.id,

        }
        // formData.append("fileCount",  this.state.images.length);
        const response = await API.delete_type(data);
        //  axios
        //     .post(process.env.REACT_APP_SERVER_HOST + "/products/deletetype", data, config)

        // console.log(response);
        if (response[0].success){
            toast.success('Type Deleted Successfully!', toast.POSITION.BOTTOM_LEFT, { autoClose: 2000 })
        }else{
            toast.error('Cannot delete!', toast.POSITION.BOTTOM_LEFT, { autoClose: 2000 })
        }
        this.setState({
            rows: newArr
        })
    }

    CategoryMove(item) {
        this.props.history.push({
            pathname: '/products/addcategory',
            state: {
                type: item
            }
        });
    }

    EditType(item, index) {
        this.state.rows[index].isRowEdit = true;
        this.setState({
            rows: this.state.rows
        })
    }

    SaveType(item, index){
        const newRow = this.state.rows;
        newRow[index].type = this.state.newTypeTxt;
        newRow[index].isRowEdit = false;
        this.setState({
            rows: newRow
        })
    }

    onchangeType(e, item) {
        console.log(item);
        let newRows = this.state.rows;
        let newArr = newRows.filter((_item) => {
            return _item.id === item.id
        })

        newArr[0].type = e.target.value;
        this.setState({
            rows: newRows
        })
        console.log(newRows);
    }

    async updateType(item, index) {
        let newRows = this.state.rows;
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            }
        };
        var data = {
            '_id': item.id,
            name: this.state.newTypeTxt
        }
        // formData.append("fileCount",  this.state.images.length);
        const response = await API.update_type(data);
        //  axios
        //     .post(process.env.REACT_APP_SERVER_HOST + "/products/updatetype", data, config)

        // console.log(response);
        if (response.status === 200){
            toast.success('Type Updated Successfully!', toast.POSITION.BOTTOM_LEFT, { autoClose: 2000 })
        }else{
            toast.error('Cannot update!', toast.POSITION.BOTTOM_LEFT, { autoClose: 2000 })
        }
        newRows[index].type = this.state.newTypeTxt;
        newRows[index].isRowEdit = false;
        this.setState({
            rows: newRows
        })
    }
    componentDidMount() {
        this.getTypes();
    }
    async getTypes() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            }
        };
        const response = await API.get_alltype();
        //  axios
        //     .get(process.env.REACT_APP_SERVER_HOST + "/products/alltype", config)
        // console.log(response);

        let rowdata = response.map((item, index) => {
            return { id: item._id, type: item.name, _id: index + 1 }
        });
        this.setState({ rows: rowdata })
    }
    TypeOnchage(e) {
        this.setState({
            newTypeTxt: e.target.value
        });
    }

    async AddNewType() {
        if (this.state.newTypeTxt === "") return;
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: JSON.parse(localStorage.getItem("token")).token,
            }
        };
        var newRow = {
            id: this.state.rows.length + 1,
            type: this.state.newTypeTxt
        }
        var data = {
            'name': this.state.newTypeTxt,

        }
        // formData.append("fileCount",  this.state.images.length);
        const response = await API.add_type(data);
        //  axios
        //     .post(process.env.REACT_APP_SERVER_HOST + "/products/addtype", data, config)

        // console.log(response);
        response.data._id = this.state.rows.length + 1;
        response.data.id = response.data._id;
        response.data.type = response.data.name;
        console.log(response);
        if (response.data._id){
            toast.success('Type Added Successfully!', toast.POSITION.BOTTOM_LEFT, { autoClose: 2000 })
        }else{
            toast.error('Cannot add!', toast.POSITION.BOTTOM_LEFT, { autoClose: 2000 })
        }
        this.setState({
            rows: [...this.state.rows, response.data]
        })
    }

    render() {
        return (
            <DashboardLayout>
                <div className="lg:flex lg:items-center lg:justify-between py-2 px-8">
                    <div className="flex-1 min-w-0">
                        <h2 className="text-2xl font-bold leading-7 text-gray-900 sm:text-3xl sm:truncate mt-5 flex lg:mt-0 lg:ml-4">
                            Add Type
                        </h2>
                    </div>
                    <div className="mt-5 flex lg:mt-0 lg:ml-4">
                        <span className="sm:ml-3">
                            <input onChange={(e) => this.TypeOnchage(e)} class="shadow appearance-none border rounded w-full px-4 py-2 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Type">
                            </input>
                        </span>
                        <span className="sm:ml-3">
                            <button type="button" onClick = { () => this.AddNewType() } className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                <svg class="h-6 w-6" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                                    <path fill-rule="evenodd" d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z" clip-rule="evenodd" />
                                </svg>
                                Add Type
                            </button>
                        </span>
                    </div>
                </div>
                <div className="flex flex-col items-start">
                    <div className="py-2 align-middle items-end inline-block sm:px-6 lg:px-8">
                        <div className="shadow overflow-auto border-b min-w-min border-gray-200 sm:rounded-lg">
                            <table className="divide-y divide-x divide-gray-200 min-w-min">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Id
                                        </th>
                                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Type
                                        </th>
                                        <th scope="col" className="relative px-6 py-3">
                                            Edit
                                        </th>

                                        <th scope="col" className="relative px-6 py-3">
                                            Delete
                                        </th>
                                    </tr>
                                    </thead>
                                    <tbody className="bg-white divide-y divide-gray-200 overflow-auto">
                                        { this.state.rows && this.state.rows.map((item, index) => {
                                            return <tr>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                {  item._id }
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 underline cursor-pointer hover:text-indigo-900">
                                                { !item.isRowEdit ? (
                                                    <a class="text-indigo-600 hover:text-indigo-900" onClick = {() => this.CategoryMove(item)}>{ item.type }</a>
                                                ):(
                                                    <input onChange={(e) => this.TypeOnchage(e)} class="shadow appearance-none border rounded w-full px-4 py-2 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Type">
                                                    </input>                
                                                ) }
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium underline cursor-pointer hover:text-indigo-900">
                                                { !item.isRowEdit ? (
                                                    <a onClick = { () => this.EditType(item, index) } class="text-indigo-600 hover:text-indigo-900">Edit</a>    
                                                ):(
                                                    <a onClick = { () => this.updateType(item, index) } class="text-indigo-600 hover:text-indigo-900">Save</a>    
                                                ) }
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium underline cursor-pointer hover:text-indigo-900">
                                                { !item.isRowEdit ? (
                                                    <a onClick = { () => this.DeleteType(item, index) } class="text-indigo-600 hover:text-indigo-900">Delete</a>    
                                                ):(
                                                    <a onClick = { () => this.CancelEdit(item, index) } class="text-indigo-600 hover:text-indigo-900">Cancel</a>    
                                                ) }
                                            </td>
                                        </tr>
                                        }) }
                                        
                                    </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </DashboardLayout>
            // <DashboardLayout>
            //      <div style={{position:'fixed',right:'5%',top:'5px'}}>
            //          <Input style={{ position: 'relative',  marginTop: '2%' }} onChange={(e) => this.TypeOnchage(e)} variant="contained" placeholder="Enter Type"></Input>
            //          <Button style={{ position: 'relative', left: '20px', marginTop: '2%' }} variant="contained" onClick={() => this.AddNewType()} >Add Type</Button>

            //         </div>
            //     <div style={{ margin: "5%" }}>
            //         <div>
            //             {this.state.rows && (
            //                 <div>
            //                     <DataGrid rows={this.state.rows}
            //                               columns={this.state.columns}
            //                               pageSize={30}
            //                               rowsPerPageOptions={[5]} />
            //                 </div>
            //             )}
            //         </div>
            //     </div>
            // </DashboardLayout>
        )
    }
}

export default withRouter(Type);