import {mat4,vec3} from "gl-matrix";

const Meshes = class {
	constructor (Vertices,_faces, _isColor, GL) {
		this.vertices = [];// vertices
		this.Faces=null;
		this.sProgram=null;//program shader
		this.numVerts=null;//vertex count per mesh
		this.vertBuff=null;//vao- vertex buffer object
		this.textBuff=null;//texture buffer
		this.Name = "";
		this.isColor=null;// = false;
		this.PositionBuff=null;
		this.NormalBuff=null;
		this.TextureBuff=null;
		this.IndexBuff=null;
		this.gDirectionalLightbasecolor = [0.937, 0.921, 0.847];
		this._attributes = {};
		this.vertices = Vertices;
		this.Faces = _faces;
		this.numVerts = this.vertices.length;
		this.isColor = _isColor;
	 
		this.setAttributes(GL);
	}
	setAttributes = (gl)=> {
		var post = new Float32Array(this.ToPosition(this.vertices));
		this.PositionBuff = gl.createBuffer();
		gl.bindBuffer(gl.ARRAY_BUFFER, this.PositionBuff);
		gl.bufferData(gl.ARRAY_BUFFER, post, gl.DYNAMIC_DRAW);

		var normdata = new Float32Array(this.ToNormal(this.vertices));
		this.NormalBuff = gl.createBuffer();
		gl.bindBuffer(gl.ARRAY_BUFFER, this.NormalBuff);
		gl.bufferData(gl.ARRAY_BUFFER, normdata, gl.DYNAMIC_DRAW);

		var texturedata = new Float32Array(this.ToTexture(this.vertices));
	this.TextureBuff = gl.createBuffer();
	gl.bindBuffer(gl.ARRAY_BUFFER, this.TextureBuff);
	gl.bufferData(gl.ARRAY_BUFFER, texturedata, gl.DYNAMIC_DRAW);
	//console.log(post);
	//console.log(texturedata);
		var indexdata = new Uint32Array(this.Tofaces(this.Faces));
		this.IndexBuff = gl.createBuffer();
		gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.IndexBuff);
		gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, indexdata, gl.DYNAMIC_DRAW);
		this.numVerts = indexdata.length;
}
	Update = (camera, gl ,shader)=> {
		gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.IndexBuff);

		shader.bind(gl);
		var prevcolor = shader.gDirectionalLightbasecolor;
		var zer = new Float32Array(0, 0, 0);
		//if (this.gDirectionalLightbasecolor[0] != 0 && this.gDirectionalLightbasecolor[1] != 0 && this.gDirectionalLightbasecolor[2] != 0) {
		shader.gDirectionalLightbasecolor = this.gDirectionalLightbasecolor;
		shader.UpdateLighting(gl, this.gDirectionalLightbasecolor);
		//}
		var vertexLocation = shader.getAttLoc("aPos");
		var normalLocation = shader.getAttLoc("aNormal");
		var textureLocation = shader.getAttLoc("aTexCoords");
		if (vertexLocation != -1) {
			gl.bindBuffer(gl.ARRAY_BUFFER, this.PositionBuff);
			gl.vertexAttribPointer(vertexLocation, 3, gl.FLOAT, false, 0, 0);
			gl.enableVertexAttribArray(vertexLocation);
		}
		if (normalLocation != -1) {
			gl.bindBuffer(gl.ARRAY_BUFFER, this.NormalBuff);
			gl.vertexAttribPointer(normalLocation, 3, gl.FLOAT, false, 0, 0);
			gl.enableVertexAttribArray(normalLocation);
		}
		if (textureLocation != -1) {
			gl.bindBuffer(gl.ARRAY_BUFFER, this.TextureBuff);
			if(this.isColor)
				gl.vertexAttribPointer(textureLocation, 3, gl.FLOAT, false, 0, 0);
			else
			gl.vertexAttribPointer(textureLocation, 2, gl.FLOAT, false, 0, 0);

			gl.enableVertexAttribArray(textureLocation);
		}
	
    if(this.Name.toUpperCase()==="LFA_IRIS")
    {
		var LightOff = shader.getUniformLoc("LightOff");
        gl.uniform1i(LightOff, 1);
     // shader.SareeLighting(gl);
    }


		var modelloc =shader.getUniformLoc( 'model');
		gl.uniformMatrix4fv(modelloc, true, camera.GetModelMatrix());

		var projectionMatrix = shader.getUniformLoc(  'projection');
		gl.uniformMatrix4fv(projectionMatrix, true, camera.GetProjectionMatrix());

	    gl.drawElements(gl.TRIANGLES, this.numVerts, gl.UNSIGNED_INT, 0);

		gl.disableVertexAttribArray(vertexLocation);
		gl.disableVertexAttribArray(normalLocation);
		gl.disableVertexAttribArray(textureLocation);
    if(this.Name.toUpperCase()==="LFA_IRIS")
    {
		var LightOff = shader.getUniformLoc("LightOff");
        gl.uniform1i(LightOff, 0);
    }
		shader.gDirectionalLightbasecolor = prevcolor;

	}

	cnvrttoarr = (vertf) =>{

			var verti = [];
			var i;
			for (i = 0; i < vertf.length; i++) {

				verti.push(vertf[i].X);
				verti.push(vertf[i].Y);
				verti.push(vertf[i].Z);

				verti.push(vertf[i].Normal.X);
				verti.push(vertf[i].Normal.Y);
				verti.push(vertf[i].Normal.Z);

				if (this.isColor) {
					verti.push(vertf[i].Color.X);
					verti.push(vertf[i].Color.Y);
					verti.push(vertf[i].Color.Z);
				}
				else {
					if (vertf[i].Texture != null) {
						verti.push(vertf[i].Texture.X);
						verti.push(vertf[i].Texture.Y);
					}
					else {
						if (vertf[i].X == 0)
							verti.push(0);
						else
							verti.push(vertf[i].X);
						if (vertf[i].Y == 0)
							verti.push(0);
						else
							verti.push(vertf[i].Y);
					}
				}
				//if(bone)
				for (var j = 0; j < 10; j++) {
					if (vertf[i].BoneMap != null && j < vertf[i].BoneMap.length)
						verti.push(vertf[i].BoneMap[j]);
					else
						verti.push(0);
				}

				for (var k = 0; k < 10; k++) {
					if (k == 0)
						verti.push(1);
					else
						verti.push(0);
				}
			}
		return verti;
	}
	ToPosition =(vertf)=> {

		var verti = [];
		for(var i = 0; i < vertf.length; i++) {
			for (var j = 0; j < vertf[i].Position.length; j++)
			{
				verti.push(vertf[i].Position[j]);
            }
		}
		return verti;
	}
	ToNormal = (vertf) =>{
		var verti = [];
		for (var i = 0; i < vertf.length; i++) {
			for (var j = 0; j < vertf[i].Normal.length; j++) {
				verti.push(vertf[i].Normal[j]);
			}
		}
		return verti;
	}
	ToTexture = (vertf)=> {
		var verti = [];
		for (var i = 0; i < vertf.length; i++) {
			for (var j = 0; j < vertf[i].Texture.length; j++) {

				verti.push(vertf[i].Texture[j]);
			}
		}
		return verti;
	}
	Tofaces=(_faces)=> {
		var verti = [];
		for (var i = 0; i < _faces.length; i++) {
			verti.push(_faces[i]);
		}
		return verti;
	}
}
export default Meshes;
