import "./sherwanimenu.scss";
import React from "react";


/**
 * Main
 */
class SherwaniMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }

  render() {
    return (
      <div>

        <div id="sherwanimenu">
          <a class="sherwanimenu-button icon-plus" href="#sherwanimenu"  title="Show navigation" onClick={()=>this.setState({show:!this.state})} >{this.props.MenuIcon?this.props.MenuIcon: <span class="fas fa-share-alt"></span>}</a>
          <a class="sherwanimenu-button icon-minus" href="#sherwanimenu0"  title="Hide navigation">{this.props.MenuIcon?this.props.MenuIcon: <span class="fas fa-share-alt"></span>}</a>
          {this.props.MenuButtons.map((item) => {
            return (
              <div class="sherwanimenu-item" >
                {item.img}
              </div>
            )
          })}
        </div>
      </div>
    );
  }
}

export default SherwaniMenu;
