/**
 * @module table
 */
import React, { Component } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { lighten, makeStyles, withStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Checkbox from "@material-ui/core/Checkbox";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
import DeleteIcon from "@material-ui/icons/Delete";
import FilterListIcon from "@material-ui/icons/FilterList";
import Icon from "@material-ui/core/Icon";
import DeleteForeverSharpIcon from "@material-ui/icons/DeleteForeverSharp";
import EditSharpIcon from "@material-ui/icons/EditSharp";
import GroupAddSharpIcon from "@material-ui/icons/GroupAddSharp";
function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}
/**
* @author Muthu G & Balaji K
* @description Table cell Theme
* @param {Object} theme - childrens
*/
const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);
/**
* @author Muthu G & Balaji K
* @description Table Row Theme
*/
const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);


/**
* @author Muthu G & Balaji K
* @description descending Order
*/
function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}
/**
* @author Muthu G & Balaji K
* @description descending or ascending order
*/
function getComparator(order, orderBy) {
  return order === "desc"
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}
/**
* @author Muthu G & Balaji K
* @description table sort
*/
function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}
/**
* @author Muthu G & Balaji K
* @description ToolBar Theme
*/
const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  title: {
    flex: "1 1 100%",
  },
}));
/**
* @author Muthu G & Balaji K
* @description Enhanced Table Toolbar
* @param {Object} props - Props from parent
*/
const EnhancedTableToolbar = (props) => {
  const classes = useToolbarStyles();
  const { numSelected } = props;
  var TitlebarComponent = props.Titlebar;

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      {numSelected > 0 ? (
        <Typography
          className={classes.title}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {numSelected} selected
        </Typography>
      ) : (
        <Typography
          className={classes.title}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          {props.Title}
        </Typography>
      )}
      {props.Titlebar}
    </Toolbar>
  );
};

EnhancedTableToolbar.propTypes = {
  numSelected: PropTypes.number.isRequired,
};
/**
* @author Muthu G & Balaji K
* @description useStyles
*/
const useStyles1 = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  paper: {
    width: "100%",
    marginBottom: theme.spacing(2),
  },
  table: {
    minWidth: 750,
  },
  visuallyHidden: {
    border: 0,
    clip: "rect(0 0 0 0)",
    height: 1,
    margin: -1,
    overflow: "hidden",
    padding: 0,
    position: "absolute",
    top: 20,
    width: 1,
  },
}));
/**
 * @module OTable_react_Component
* @author Muthu G & Balaji K
* @description OTable
* @param {Object} props - Props from parent
*/
export class OTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: props.show,
      dataget: undefined,
      order: "asc",
      orderBy: "calories",
      selected: [],
      page: 0,
      dense: false,
      rowsPerPage: 5,
      classes: {
        root: {
          width: "100%",
        },
        paper: {
          width: "100%",
          marginBottom: 2,
        },
        table: {
          minWidth: 750,
        },
        visuallyHidden: {
          border: 0,
          clip: "rect(0 0 0 0)",
          height: 1,
          margin: -1,
          overflow: "hidden",
          padding: 0,
          position: "absolute",
          top: 20,
          width: 1,
        },
      },
    };
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name handleRequestSort
  * @description It will request for sort
  */
  handleRequestSort = (event, property) => {
    const isAsc = this.state.orderBy === property && this.state.order === "asc";
    this.setState({ order: isAsc ? "desc" : "asc" });
    this.setState({ orderBy: property });
  };
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name handleSelectAllClick
  * @description It will select all
  */
  handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = this.props.tableData.map((n) => n.name);
      this.setState({ selected: newSelecteds });

      return;
    }
    this.setState({ selected: [] });
  };
  componentWillMount = () => {

  };
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name handleClick
  * @description It click process for row
  */
  handleClick = (event, name) => {

    return;


    const selectedIndex = this.state.selected.indexOf(name);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(this.state.selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(this.state.selected.slice(1));
    } else if (selectedIndex === this.state.selected.length - 1) {
      newSelected = newSelected.concat(this.state.selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        this.state.selected.slice(0, selectedIndex),
        this.state.selected.slice(selectedIndex + 1)
      );
    }
    this.setState({ selected: newSelected });
  };

  handleChangePage = (event, newPage) => {
    this.setState({ page: newPage });
  };

  handleChangeRowsPerPage = (event) => {
    this.setState({ rowsPerPage: parseInt(event.target.value, 10) });
    this.setState({ page: 0 });
  };

  handleChangeDense = (event) => {
    this.setState({ dense: event.target.checked });
  };

  isSelected = (name) => this.state.selected.indexOf(name) !== -1;

  emptyRows = () =>
    this.state.rowsPerPage -
    Math.min(
      this.state.rowsPerPage,
      this.props.tableData.length - this.state.page * this.state.rowsPerPage
    );
    /**
    * @function
    * @author Muthu G & Balaji K
    * @name EnhancedTableHead
    * @description It used for table header
    */
  EnhancedTableHead = (props) => {
    const {
      classes,
      onSelectAllClick,
      order,
      orderBy,
      numSelected,
      rowCount,
      onRequestSort,
    } = props;
    const createSortHandler = (property) => (event) => {
      this.handleRequestSort(event, property);
    };
    var tstyle = {
      border: 0,
      clip: "rect(0 0 0 0)",
      height: 1,
      margin: -1,
      overflow: "hidden",
      padding: 0,
      position: "absolute",
      top: 20,
      width: 1,
    };
    return (
      <TableHead>
        <StyledTableRow>
          <StyledTableCell padding="checkbox">
            <Checkbox
              indeterminate={numSelected > 0 && numSelected < rowCount}
              checked={rowCount > 0 && numSelected === rowCount}
              onChange={onSelectAllClick}
              inputProps={{ "aria-label": "select all desserts" }}
            />
          </StyledTableCell>
          {this.props.headCells.map((headCell) => (
            <StyledTableCell
              key={headCell.id}
              align={headCell.numeric ? "right" : "left"}
              padding={headCell.disablePadding ? "none" : "default"}
              sortDirection={orderBy === headCell.id ? order : false}
              align="left"
            >
              <TableSortLabel
                align="left"
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : "asc"}
                onClick={createSortHandler(headCell.id)}
              >
                {headCell.label}
                {orderBy === headCell.id ? (
                  <span style={tstyle}>
                    {order === "desc"
                      ? "sorted descending"
                      : "sorted ascending"}
                  </span>
                ) : null}
              </TableSortLabel>
            </StyledTableCell>
          ))}
          <StyledTableCell>Actions</StyledTableCell>
        </StyledTableRow>
      </TableHead>
    );
  };
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    return (
      <div>
        <Paper >
          <EnhancedTableToolbar
            numSelected={this.state.selected.length}
            Title={this.props.Title}
            Titlebar={this.props.Titlebar}
          />
          <TableContainer style={{ maxHeight: 500}}>
            <Table
              aria-labelledby="tableTitle"
              size={this.state.dense ? "small" : "medium"}
              aria-label="enhanced table"
            >
              <this.EnhancedTableHead
                numSelected={this.state.selected.length}
                order={this.state.order}
                orderBy={this.state.orderBy}
                onSelectAllClick={this.state.handleSelectAllClick}
                onRequestSort={this.state.handleRequestSort}
                rowCount={this.props.tableData.length}
              />
              <TableBody>
                {stableSort(
                  this.props.tableData,
                  getComparator(this.state.order, this.state.orderBy)
                )
                  .slice(
                    this.state.page * this.state.rowsPerPage,
                    this.state.page * this.state.rowsPerPage +
                      this.state.rowsPerPage
                  )
                  .map((row, index) => {
                    const isItemSelected = this.isSelected(row.filename);
                    const labelId = `enhanced-table-checkbox-${index}`;

                    return (
                      <StyledTableRow
                        hover
                        onClick={(event) => this.handleClick(event, row)}
                        role="checkbox"
                        aria-checked={isItemSelected}
                        tabIndex={-1}
                        key={row._id}
                        selected={isItemSelected}
                      >
                        <StyledTableCell padding="checkbox">
                          {/*
                          <Checkbox
                            checked={isItemSelected}
                            inputProps={{ 'aria-labelledby': labelId }}
                          />
                          */}
                        </StyledTableCell>
                        {this.props.headCells.map((headCell) => (
                          <StyledTableCell
                            component="th"
                            id={labelId}
                            scope="row"
                            align="left"
                          >
                            {headCell.link ? (
                              <a
                                href=""
                                onClick={(e) => {
                                  this.props.clickLink(e, row);
                                }}
                              >
                                {" "}
                                {row[headCell.id]}
                              </a>
                            ) : Array.isArray(row[headCell.id]) ? (
                              row[headCell.id].map((e) => e.user).join(", ")
                            ) : (
                              row[headCell.id]
                            )}
                          </StyledTableCell>
                        ))}
                        <StyledTableCell align="center">
                          {this.props.isedit && (
                            <IconButton onClick={(e) => this.props.edit(row)}>
                              <EditSharpIcon />
                            </IconButton>
                          )}

                          <IconButton onClick={(e) => this.props.delete(row)}>
                            <DeleteForeverSharpIcon />
                          </IconButton>
                        </StyledTableCell>
                      </StyledTableRow>
                    );
                  })}
                {this.emptyRows > 0 && (
                  <StyledTableRow
                    style={{
                      height: (this.state.dense ? 33 : 53) * this.emptyRows,
                    }}
                  >
                    <TableCell colSpan={6} />
                  </StyledTableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={this.props.tableData.length}
            rowsPerPage={this.state.rowsPerPage}
            page={this.state.page}
            onChangePage={this.handleChangePage}
            onChangeRowsPerPage={this.handleChangeRowsPerPage}
          />
        </Paper>
        <FormControlLabel
          control={
            <Switch
              checked={this.state.dense}
              onChange={this.handleChangeDense}
            />
          }
          label="Dense padding"
        />
      </div>
    );
  }
}
