import React, { Component } from "react";
import API from "../../api/services";
import playpng from '../../Icons/play.png';
import { vec3 } from "gl-matrix";
import ProductList from "../Products/productListView";
import '@themesberg/flowbite';
import { createPopper  } from '@popperjs/core';
import PopOver from "../Modal/PopOver";
import JSZip from "jszip";
import JSZipUtils from "jszip-utils";
import { API_Urls } from "../../api/config";
import ProductCard from "../ProductCard/productCard";
import logoimg from '../../Icons/Logo.png'
import OynaEngine from "../../Engine/oyna_Engine";
import CircleMenuBar from "../circularMenu/index"
 import SkintoneMenu from "../circularMenu/skintonemenu"
 // import { CircleMenu, CircleMenuItem } from "react-circular-menu";
 import ProfileShoot from "../Modal/ProfileShoot/ProfileShoot"
 import girlpng from '../../Icons/girl.png'
 import dark1 from '../../Icons/dark1.png'
 import pause from '../../Icons/pause.png'
 import add1 from '../../Icons/add1.png'
 import LFA_POSEPng from '../../Icons/LFA_POSE.png'
 import fair1 from '../../Icons/fair1.png'
 import shirtLogo from '../../Icons/shirt.png';
 import sherwaniLogo from '../../Icons/sherwani.png';
 import fair2 from '../../Icons/fair2.png'
 import dusky1 from '../../Icons/dusky1.png'
 import profile from '../../Icons/profile.png';
 import IconButton from "@material-ui/core/IconButton";
 import sareeIcon from '../../Icons/sareeIcon.jpg';
 //import logoimg from '../../Icons/Logo.png'
 import PanToolAltIcon from '@mui/icons-material/PanToolAlt';
 import ThreeSixtyIcon from '@mui/icons-material/ThreeSixty';
 import RestartAltIcon from '@mui/icons-material/RestartAlt';
import SherwaniMenu from "../circularMenu/sherwanimenu";
import './customstyle.css';
class OynaMen extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isProfile : false,
            isTone : false,
            isShirt: false,
            showLoader: true,
            height: 300,
            width: 300,
            modelPath : undefined,
            products: [],
            showProducts: false,
            isRotate: true,
            DoRender:true,
            isExpand: false,
            isExpandsecond:true
        }
        this.LFAPose = "LFA"
        this.LAOPose = "LAO"
        this.engineRef = React.createRef();
        this.resize = this.resize.bind(this);
    }

    resize(e){
      e.preventDefault();
      console.log("e.target.innerWidth",e.target.innerWidth)
    }

    componentDidMount(){
        this.setState({
            modelPath: 'MenModels/menlao.zip'
        });
        let divEnginge = document.getElementById('enginecanvas');
        divEnginge.addEventListener('resize', function() {
        })
        window.addEventListener('resize', function(e) {
            
        })
        let resizeObserver = new ResizeObserver(() => {
          console.log("divEnginge.clientWidth",divEnginge.clientWidth)
          this.engineRef?.current?.resizeCanvas(divEnginge.clientHeight, divEnginge.clientWidth)
          console.log("The element was resized",resizeObserver);
        });
        resizeObserver.observe(divEnginge);
        this.Load();
    }

    async Load()
    {
        this.engineRef.current.Init();
        await this.LoadModels();
         this.LoadProducts();
        this.engineRef.current.CurrntPose = this.LFAPose;
    }

    async LoadModels(){
        let _this = this;
        await new JSZip.external.Promise(function(resolve, reject) {
            JSZipUtils.getBinaryContent(API_Urls.app + 'MenModels/menlao.zip', function(err, data ){
                if (err){
                    reject(err);
                }else{
                    resolve(data);
                }
            });
        }).then(function(data){
            return JSZip.loadAsync(data);
        })
        .then(function(zip) {
            return zip.file('lao.txt').async("string");
        }).then(function success(text){
            _this.engineRef?.current.LoadModelMen(text, _this.LFAPose);
        })

        await new JSZip.external.Promise(function(resolve, reject) {
            JSZipUtils.getBinaryContent(API_Urls.app + 'MenModels/lao.zip', function(err, data ){
                if (err){
                    reject(err);
                }else{
                    resolve(data);
                }
            });
        }).then(function(data){
            return JSZip.loadAsync(data);
        })
        .then(function(zip) {
            return zip.file('lao.txt').async("string");
        }).then(function success(text){
            _this.engineRef.current?.LoadModelMen(text, _this.LAOPose);
        })
        this.engineRef?.current?.InitWindow();
        this.engineRef?.current?.setState({
          showLoader: false
        })
    }

    async LoadProducts() {
        const config = {
          headers: {
            'Content-Type': 'application/json',
            Authorization: JSON.parse(localStorage.getItem("token")).token,
          }
        };
    
        // const allfiles = await axios
        //   .get(process.env.REACT_APP_SERVER_HOST + "/products/get_all_products", config)
    
        
        const products =await API.getProducts("men");// allfiles.data;
        if (products.length === 0) {
            this.setState({ showLoader: false })
            return
        };

        console.log("products",products)
        this.setState({ products: products })
        let ids = [];
        for (let ind = 0; ind < products.length; ind++) {
          ids.push(products[ind].images[0].filename)
        }
        this.SelectProduct(products[0]);
    
        var data = {
          'fileids': ids,
    
        }
        // formData.append("fileCount",  this.state.images.length);
        const allfilesImagw =  await API.get_images(data);
        //  axios
        //   .post(process.env.REACT_APP_SERVER_HOST + "/products/get_images", data, config)
    
        for (let i = 0; i < allfilesImagw.length; i++) {
          let item1 = products.filter((item) => item.images[0].filename === allfilesImagw[i].filename)[0]
          products[i].img = allfilesImagw[i].img;
          this.setState({ products: products })
          //  allfilesImagw.data[i].filename
        }
        this.setState({ products: [] })
        this.setState({ products: products })
        return;
    }

    SelectProductParent(e, item){
      // e.stopPropagation();
      this.SelectProduct(item);
    }

    async SelectProduct(item) {
        
        this.setState({ showLoader: true })
    
        
        const config = {
          headers: {
            'Content-Type': 'application/json',
            Authorization: JSON.parse(localStorage.getItem("token")).token,
          }
        };
        let ids = [];
        for (let ind = 0; ind < item.images.length; ind++) {
          ids.push(item.images[ind].filename)
        }
    
        var data = {
          'fileids': ids,
    
        }
        
        const allfilesImagw =await API.get_images(data);
        //  await axios
        //   .post(process.env.REACT_APP_SERVER_HOST + "/products/get_images", data, config)
    
        let textures = {};
        for (let i = 0; i < allfilesImagw.length; i++) {
          let item1 = item.images.filter((item) => item.filename === allfilesImagw[i].filename)[0]
          if (item1.originalname.toUpperCase() === "SHIRT_SHIRT")
            textures.SHIRT = allfilesImagw[i].img;
        }
    
        await this.engineRef.current?.setTextures(textures);
        let _this = this;
        setTimeout(() => {
    
          if ((_this.state.width < 1024)) {
            _this.setState({ showProducts: false })
          }
          _this.setState({ showLoader: false })
        }, 1000);
    
      }

    render(){
        const { width, showProducts } = this.state;
        const skintoneImage = {
          width: '100%',
          height: '100%',
          borderRadius: '50%',
          clipPath: `circle(80% at center)`
        }
        const skinBtns = [{
          img: <img src={dark1} onClick={() => {
           this.engineRef.current.pixelvalue = vec3.fromValues(180 / 255, 140 / 255, 120 / 255);
            this.engineRef.current.changeskintone();
          }} style={skintoneImage} />
        },
        {
          img: <img src={dusky1} onClick={() => {
           this.engineRef.current.pixelvalue = vec3.fromValues(220 / 255, 180 / 255, 160 / 255);
            this.engineRef.current.changeskintone();
          }} style={skintoneImage} />
        },
        {
          img: <img src={fair2} onClick={() => {
           this.engineRef.current.pixelvalue = vec3.fromValues(240 / 255, 240 / 255, 240 / 255);
            this.engineRef.current.changeskintone();
          }} style={skintoneImage} />
        },
        {
          img: <img src={fair1} onClick={() => {
           this.engineRef.current.pixelvalue = vec3.fromValues(230 / 255, 210 / 255, 180 / 255);
            this.engineRef.current.changeskintone();
          }} style={skintoneImage} />
        },
        ];
        const menuBtns = [
        {
          img:
            <div role="button" href="#0" onClick={() => {
                this.engineRef.current.CurrntPose = this.LFAPose;
                window.location = window.location.href.replace('#menu', '#0');
            }}> <img src={shirtLogo} style={skintoneImage} />
            </div>
        },
        {
          img:
           <div role="button" onClick={() => {
                this.engineRef.current.changePose("lao")
                window.location = window.location.href.replace('#menu', '#0');}}>
                <img src={this.state.Imagefile ? this.state.Imagefile : add1} class="rounded-full object-cover" style={{width: '60px',height: '50px'}} />
            </div>
        },
        {
          img:
            <div role="button" onClick={() => {
              if ((width < 1024)) {
                this.setState({ DoRender:false})
              }
              this.setState({ showModal: true })
              window.location = window.location.href.replace('#menu', '#0');
            }}>
              <img role="button" src={add1} style={skintoneImage} />
            </div>
    
        },
        ];
        const sherwaniMenuButtons = [
            {
                img:
                <div role="button" href="#sherwanimenu0" onClick={() => {
                    this.engineRef.current.CurrntPose = this.LAOPose;
                    window.location = window.location.href.replace('#sherwanimenu', '#sherwanimenu0');
                }}> <img src={sherwaniLogo} style={skintoneImage} />
                </div>
            },
            {
                img:
                 <div role="button" onClick={() => {
                      this.engineRef.current.changePose("lao")
                      window.location = window.location.href.replace('#sherwanimenu', '#sherwanimenu0');}}>
                      <img src={this.state.Imagefile ? this.state.Imagefile : add1} class="rounded-full object-cover" style={{width: '60px',height: '50px'}} />
                  </div>
            },
            {
                img:
                  <div role="button" onClick={() => {
                    if ((width < 1024)) {
                      this.setState({ DoRender:false})
                    }
                    this.setState({ showModal: true })
                    window.location = window.location.href.replace('#sherwanimenu', '#sherwanimenu0');
                  }}>
                    <img role="button" src={add1} style={skintoneImage} />
                  </div>
          
            },
        ]
        return(
            <>
            <div>
              { this.state.isExpandsecond ? (
                  <div className="z-10 fixed top-3 left-3 text-white cursor-pointer" onClick={() => { this.setState({isExpand: !this.state.isExpand})
                      console.log("Click me")
                      let _this=this;
                      setTimeout(() => {
                          _this.setState({isExpandsecond: !_this.state.isExpand})
                      }, 0);}} >
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M4 6h16M4 12h16M4 18h7" />
                      </svg>
                  </div>
              ): (
                <div></div>
              ) }

              <div className="relative">
                <div className="flex h-screen w-full">
                    <div className={`container2 ${ !this.state.isExpand ? `w-0` : `2xl:w-full sm:w-screen`} left-0 bg-opacity-75 bg-black`}>
                          <ProductList products={this.state.products}
                                       SelectProduct={(e,item) =>{ this.SelectProductParent(e,item) }}
                                       origin = "men"
                                       isMen = {true}
                                       setTextures={(val) => {
                                       this.engineRef.current.setTextures(val)}}
                                       setLoading={(val) => this.setState({ showLoader: val })}
                                       Close={() =>{ this.setState({ 
                                                      isExpand: !this.state.isExpand})
                                                      let _this = this;
                                                      setTimeout(() => {
                                                        _this.setState({isExpandsecond: !_this.state.isExpand})
                                                      }, 0);
                                                    }}>
                          </ProductList>
                    </div> 
                  <div id="enginecanvas" className={`container2  grow ${ this.state.isExpandsecond ? `w-full sm:w-0` : `2xl:w-1/2 sm:w-screen`} border-2 border-orange-400`}>
                    <OynaEngine ref={this.engineRef}></OynaEngine>
                    
                      {this.state.showModal && (
                        <div style={{ zIndex: 3 }}>
                          <ProfileShoot showFile={(e) => this.showFile(e)}
                                        imageFile={this.state.Imagefile}
                                        isMobile={this.props.isMobile}
                                        setImageFile={(e) => this.setState({ Imagefile: e })}
                                        proceedGenerate={(val) => this.proceedGenerate(val)}
                                        close={() => {this.setState({ showModal: false })
                                        this.engineRef.current.resumeDraw()} 
                            }/>
                        </div>
                      )}
                    {!this.state.showModal && (
                      <>
                        <div style={{ right: 10, top: 20, position: "fixed", color: "white" }}>
                          <button onClick={() => {
                                      this.engineRef.current.playpause()
                                      this.setState({isplay:!this.state.isplay})
                                  }} >
                            {this.state.isplay ? (<img src={pause} alt="Avatar" style={{ width: "70px" }}></img>) : (
                              <img src={playpng} alt="Avatar" style={{ width: "70px" }}></img>
                            )}
                          </button>
                        </div>
          
                        <div style={{ right: 20, top: 160, zIndex: 2, position: "fixed", color: "white" }}>
                              <CircleMenuBar name="models"
                                            MenuButtons={menuBtns}
                                            MenuIcon={<img src={shirtLogo} style={{backgroundColor: "rgb(20,26,62)",width: '100%',height: '100%'}} />}/>
                        </div>

                        <div style={{ right: 20, top: 240, zIndex: 2, position: "fixed", color: "white" }}>
                              <SherwaniMenu name="sherwanimodels"
                                            MenuButtons={sherwaniMenuButtons}
                                            MenuIcon={<img src={sherwaniLogo} style={{ backgroundColor: "rgb(20,26,62)",width: '100%',height: '100%'}} />}/>
                        </div>
      
                      <div style={{ right: 20, top: 320, position: "fixed", color: "white" }}>
                            <SkintoneMenu name="skintone"
                                          MenuButtons={skinBtns}
                                          MenuIcon={<img src={dark1} style={{width: '100%',height: '100%'}} />} />
                      </div>
                    </>
                  )}
      
                  {this.props.normalLoad && !this.state.showModal && (
                  <>
                      {width < 1024 && (
                        <div style={{ right: 10, top: 260, zIndex: 2, position: "fixed", color: "white" }}>
                        
                          <img src={sareeIcon} style={{
                            width: '70px', height: '70px', borderRadius: '50%',
                            clipPath: `circle(80% at center)`
                          }} onClick={() =>{
                          this.setState({ showProducts: true,DoRender:false })
                          } } />
                        </div>
                      )}
                    </>
                    )}
                    {this.state.showLoader && (
                      <div style={{ position: "fixed", textAlign: "center", zIndex: 25 }}>
                        <div>
                          <img className="rotateimg" src={logoimg} style={{
                                display: 'block',
                                margin: 'auto', position: 'fixed', left: '0', right: '0', top: '0', bottom: '0', padding: '10%'
                            }} />
                           
                        </div>
      
                      </div>
                    )}
                    {this.engineRef.current&&(
                        <div style={{ right:!this.props.normalLoad?10:width<1024?10:530, bottom: 10, zIndex: 2, position: "fixed", color: "white" }}>
                            <IconButton style={{ color: 'white', width: '50px', height: '50px' }} onClick={() => this.engineRef.current.setState({ isRotate: !this.engineRef.current.state.isRotate })}>
                                {this.engineRef.current.state.isRotate ? (
                                    <PanToolAltIcon style={{ color: 'white', width: '50px', height: '50px' }} />
                                ) : (<ThreeSixtyIcon style={{ color: 'white', width: '50px', height: '50px' }} />)}
                            </IconButton>
                        </div>
                    )}
                    <div style={{ right:!this.props.normalLoad?70:width<1024?70:600, bottom: 10, zIndex: 2, position: "fixed", color: "white" }}>
                        <IconButton style={{ color: 'white', width: '50px', height: '50px' }} onClick={()=>this.engineRef.current.camera.reset()}>
                          <RestartAltIcon style={{ color: 'white', width: '50px', height: '50px' }} />
                        </IconButton>
                    </div>
                  </div> 
                </div>
              </div>
            </div>
            </>
      
        )
    }
}

export default OynaMen;