/**
 * @module NavSidebar
 */
/* eslint-disable react/display-name, jsx-a11y/click-events-have-key-events */
import { Navigation } from "react-minimal-side-navigation";
import { useHistory, useLocation } from "react-router-dom";
import Icon from "awesome-react-icons";
import React, { useState } from "react";

import "react-minimal-side-navigation/lib/ReactMinimalSideNavigation.css";
/**
* @author Muthu G & Balaji K
* @description Getting User Role
* @param {Object} setrole - set role
*/
function Getrole(setrole) {
  // POST request using fetch with set headers
  const requestOptions = {
    method: "POST",

    headers: {
      "Content-Type": "application/json",
      Authorization: JSON.parse(localStorage.getItem("token")).token,
    },
  };
  fetch(process.env.REACT_APP_SERVER_HOST + "/Getrole", requestOptions)
    .then((response) => response.json())
    .then((data) => {
      setrole(data.role);
  });
  
  
}
/**
* @author Muthu G & Balaji K
* @description NavSidebar
*/
export const NavSidebar = () => {
  const history = useHistory();
  const location = useLocation();
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [navitem, setnavitem] = useState();
  var navitems = [
    {
      title: "Clients",
      itemId: "/client",
      elemBefore: () => <Icon name="coffee" />,
    },
    {
      title: "Branches",
      itemId: "/Branches",
      elemBefore: () => <Icon name="user" />,
      subNav: [
        {
          title: "All Branches",
          itemId: "/Branches/AllBranches",
        },
        // {
        //   title: "Users",
        //   itemId: "/Branches/Users"
        // }
        // ,
        {
          title: "Files",
          itemId: "/Branches/Files",
        },
      ],
    },
    {
      title: "Dashboard",
      itemId: "/another",
      subNav: [
        {
          title: "Dashboard",
          itemId: "/another/teams",
        },
        {
          title: "ProcessMessage",
          itemId: "/another/processmessage",
        },
      ],
    },
 
  ];

  if(process.env.REACT_APP_TYPE==='ecom')
  {
    var navitems2 = [
      {
        title: "Oyna Viewer",
        itemId: "/oynaviewer",
        elemBefore: () => <Icon name="coffee" />,
      },
      {
        title: "Products",
        itemId: "/products",
        elemBefore: () => <Icon name="user" />,
        subNav: [
          {
            title: "All Products",
            itemId: "/products/allproducts",
          },
          {
            title: "Add Products",
            itemId: "/products/addproducts",
          },
        ],
      },
    ];
    navitems=[...navitems,...navitems2]
  }

  if (!navitem) {
    if (localStorage.getItem("token")){
      if (JSON.parse(localStorage.getItem("token")).role !== "Super Admin") {
        navitems.shift();
      }
      setnavitem(navitems);
    }
  }
  return (
    <React.Fragment>
      {/* Sidebar Overlay */}
      <div
      onClick={() => setIsSidebarOpen(false)}
      className={`fixed inset-0 z-20 block transition-opacity bg-black opacity-50 2xl:hidden ${
        isSidebarOpen ? "block" : "hidden"
      }`}
    />



    <div>
      <button
      style={{zIndex:1,position:"fixed"}}
        className="btn-menu"
        onClick={() => setIsSidebarOpen(true)}
        type="button"
      >
        <Icon name="burger" className="w-6 h-6" />
      </button>
    </div>



      {/* Sidebar */}
      <div
        className={`fixed inset-y-0 left-0 z-30 w-64 overflow-y-auto transition duration-300 ease-out transform translate-x-0 bg-white border-r-2 2xl:translate-x-0 2xl:static  2xl:inset-0
       ${
         isSidebarOpen ? "ease-out translate-x-0" : "ease-in -translate-x-full"
       }`}
      >
        <div className="flex items-center justify-center" style={{width:200, margin: 30}}>
              <img src='/logo512.png' />
        </div>

        <div className="flex items-center justify-center mt-10 text-center py-6">
          <span className="mx-2 text-2xl font-semibold text-black">
            Oyna Cloud
          </span>
        </div>

        <Navigation
          activeItemId={location.pathname}
          onSelect={({ itemId }) => {
            history.push(itemId);
          }}
          items={navitem ? navitem : navitems}
        />

        <div className="absolute bottom-0 w-full my-8">
          <Navigation
            activeItemId={location.pathname}
            items={[
              // {
              //   title: "Settings",
              //   itemId: "/settings",
              //   elemBefore: () => <Icon name="activity" />,
              // },
              {
                title: "Logout",
                itemId: "/logout",
                elemBefore: () => <Icon name="activity" />,
              },
            ]}
            onSelect={({ itemId }) => {
              history.push(itemId);
            }}
          />
        </div>
      </div>
    </React.Fragment>
  );
};
