/**
 * @module TeamsPage
 */
import * as React from "react";
import Paper from "@material-ui/core/Paper";
import {
  Chart,
  AreaSeries,
  BarSeries,
  SplineSeries,
  ScatterSeries,
  ArgumentAxis,
  ValueAxis,
  Title,
  Legend,
  Tooltip,
} from "@devexpress/dx-react-chart-material-ui";

import {
  ValueScale,
  Stack,
  Animation,
  EventTracker,
  HoverState,
} from "@devexpress/dx-react-chart";
import { connectProps } from "@devexpress/dx-react-core";
import { format } from "d3-format";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
} from "react-bootstrap";
import { DashboardLayout } from "../navbar/Layout";
import ReactNotification from "react-notifications-component";
import { store } from "react-notifications-component";
import Loader from "react-loader-spinner";
import "react-notifications-component/dist/theme.css";
const consumptionSeriesName = "Consumption";
const consumptionColor = "#41c0f0";
const priceColor = "#fcd45a";
/**
* @author Muthu G & Balaji K
* @description label design
*/
const makeLabel = (symbol, color) => ({ text, style, ...restProps }) => (
  <ValueAxis.Label
    text={`${text} ${symbol}`}
    style={{
      fill: color,
      ...style,
    }}
    {...restProps}
  />
);
const PriceLabel = makeLabel("$", priceColor);
const LabelWithThousand = makeLabel("k", consumptionColor);

const patchProps = ({ hoverIndex, ...props }) => ({
  state: props.index === hoverIndex ? "hovered" : null,
  ...props,
});

const BarPoint = (props) => <BarSeries.Point {...patchProps(props)} />;

const pointOptions = { size: 7 };
const getPointOptions = (state) => (state ? { size: 7 } : { size: 0 });

const AreaPoint = (props) => {
  const patched = patchProps(props);
  return (
    <ScatterSeries.Point point={getPointOptions(patched.state)} {...patched} />
  );
};

const AreaWithPoints = ({ state, ...props }) => (
  <React.Fragment>
    <AreaSeries.Path {...props} />
    <ScatterSeries.Path {...props} />
  </React.Fragment>
);

const SplinePoint = (props) => (
  <ScatterSeries.Point point={pointOptions} {...patchProps(props)} />
);

const SplineWithPoints = (props) => (
  <React.Fragment>
    <SplineSeries.Path {...props} />
    <ScatterSeries.Path {...props} />
  </React.Fragment>
);

const series = [
  { name: "import", key: "import", color: "#08abbd" },
  { name: "Scan", key: "scan", color: "#d4d67e" },
  { name: "Routine", key: "routine", color: "#78bc97" },
  // { name: 'Mexico', key: 'mexico', color: '#9ccc65' },
  // { name: 'Russia', key: 'russia', color: '#1698af' },
  // {
  //   name: consumptionSeriesName, key: 'consumption', color: consumptionColor, type: AreaSeries,
  // },
  // {
  //   name: 'Oil Price', key: 'price', color: priceColor, scale: 'price', type: SplineSeries,
  // },
];

const legendRootStyle = {
  display: "flex",
  margin: "auto",
  flexDirection: "row",
};
const LegendRoot = (props) => (
  <Legend.Root {...props} style={legendRootStyle} />
);

const legendItemStyle = {
  flexDirection: "column",
  marginLeft: "-2px",
  marginRight: "-2px",
};
const LegendItem = (props) => (
  <Legend.Item {...props} style={legendItemStyle} />
);

const legendLabelStyle = {
  whiteSpace: "nowrap",
};
const LegendLabel = (props) => (
  <Legend.Label {...props} style={legendLabelStyle} />
);

const formatTooltip = format(".1f");
const TooltipContent = ({ data, text, style, ...props }) => {
  const alignStyle = {
    ...style,
    paddingLeft: "10px",
  };
  const items = series.map(({ name, key, color }) => {
    const val = data[key];
    return (
      <tr key={key}>
        <td>
          <svg width="10" height="10">
            <circle cx="5" cy="5" r="5" fill={color} />
          </svg>
        </td>
        <td>
          <Tooltip.Content style={alignStyle} text={name} {...props} />
        </td>
        <td align="right">
          <Tooltip.Content
            style={alignStyle}
            text={val ? formatTooltip(val) : "N/A"}
            {...props}
          />
        </td>
      </tr>
    );
  });
  return <table>{items}</table>;
};

const stacks = [
  { series: series.filter((obj) => !obj.type).map((obj) => obj.name) },
];

const modifyOilDomain = (domain) => [domain[0], 2200];
const modifyPriceDomain = () => [0, 110];

const getHoverIndex = ({ target }) => (target ? target.point : -1);
/**
 * @module TeamsPage_react_Component
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
export default class TeamsPage extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      target: null,
      showLoader:false,
    };

    this.changeHover = (target) => {
      this.setState({
        target: target ? { series: "Routine", point: target.point } : null,
      });
    };

    this.createComponents();
    this.createSeries();
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentDidUpdate
  * @description It will update the component
  */
  componentDidUpdate(prevProps, prevState) {
    if (getHoverIndex(prevState) !== getHoverIndex(this.state)) {
      this.BarPoint.update();
      this.SplinePoint.update();
      this.AreaPoint.update();
      this.TooltipContent.update();
    }
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name createComponents
  * @description It will Creating the component
  */
  createComponents() {
    const getHoverProps = () => ({
      hoverIndex: getHoverIndex(this.state),
    });
    this.BarPoint = connectProps(BarPoint, getHoverProps);
    this.SplinePoint = connectProps(SplinePoint, getHoverProps);
    this.AreaPoint = connectProps(AreaPoint, getHoverProps);

    this.TooltipContent = connectProps(TooltipContent, () => {
      const { data, target } = this.state;
      return { data: target ? data[target.point] : null };
    });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name createComponents
  * @description It will Creating the series
  */
  createSeries() {

    this.series = series.map(({ name, key, color, type, scale }) => {
      const props = {
        key: name,
        name,
        valueField: key,
        argumentField: "date",
        color,
        scaleName: scale || "oil",
        pointComponent: this.BarPoint,
      };

      if (type === AreaSeries) {
        props.seriesComponent = AreaWithPoints;
        props.pointComponent = this.AreaPoint;
      } else if (type) {
        props.seriesComponent = SplineWithPoints;
        props.pointComponent = this.SplinePoint;
      }
      return React.createElement(type || BarSeries, props);
    });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name showFile
  * @description It will popup the file dialog
  */
  showFile = async (e) => {
    e.preventDefault();
        this.setState({ showLoader: true });
    const reader = new FileReader();
    reader.onload = async (e) => {
      const text = e.target.result;
      this.csvJSON(text);

    };
    reader.readAsText(e.target.files[0]);
  };
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name csvJSON
  * @description It will convert the csv file to json object
    * @param {string} csv -csv file
  */
  csvJSON(csv) {
    var lines = csv.split("\n");

    var result = [];

    var Alldata = [];
    var update = [];
    var scan = [];
    var routine = [];
    var headers = [0, 1, 2, 4, 5, 6, 7, 8, 9, 10, 11, 12];
    for (var i = 1; i < lines.length; i++) {
      var obj = {};
      var currentline = lines[i].split(",");
      var splited = currentline[0].split("  ");
      const dateObj = new Date(splited[1]);
      var dt = this.getDateFormat(dateObj);
      if (!Alldata[dt])
        Alldata[dt] = { date: dt, import: 0, routine: 0, scan: 0 };

      switch (currentline[6]) {
        case "Import Process":
          Alldata[dt].import++;
          break;
        case "Get Fabric for Routine":
          Alldata[dt].routine++;
          break;
        case "Get Fabric for On Qr Scan":
          Alldata[dt].scan++;
          break;
        default:
        // code block
      }

    }
    var Alldataarr = [];

    for (const [key, value] of Object.entries(Alldata)) {
      if (!(value.routine === 0 && value.scan === 0 && value.import === 0))
        Alldataarr.push(value); // = {somethingElse: 1};
    }
    Alldataarr.sort(function (a, b) {
      return a.date > b.date ? 1 : b.date > a.date ? -1 : 0;
    });
    var notification = {
      title: "Wonderful!",
      message: "Configurable",
      type: "success",
      insert: "top",
      container: "top-left",
      animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
      animationOut: ["animate__animated animate__fadeOut"], // `animate.css v4` classes
    };

    store.addNotification({
      title: "Chart!",
      message: "Csv Loaded",
      type: "success",
      insert: "top",
      container: "top-right",
      animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
      animationOut: ["animate__animated animate__fadeOut"],
      dismiss: {
        duration: 5000,
        onScreen: false,
      },
      slidingExit: {
        duration: 800,
        timingFunction: "ease-out",
        delay: 0,
      },
    });
    this.setState({ data: Alldataarr });
    this.setState({ showLoader: false });
    return result; //JavaScript object

  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name getDateFormat
  * @description It will convert the date time
    * @param {object} dateobj -date format string
  */
  getDateFormat(dateObj) {
    const monthNames = [
      "Jan",
      "Feb",
      "Mar",
      "Aprl",
      "May",
      "Jun",
      "July",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];
    const month = monthNames[dateObj.getMonth()];
    const day = String(dateObj.getDate()).padStart(2, "0");
    const year = dateObj.getFullYear();
    const output = month + "-" + day;
    return output;
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    const { data, target } = this.state;

    return (
      <DashboardLayout>
        <h2>Dashboard</h2>
        <Row className="offset-4">
          <input type="file" onChange={(e) => this.showFile(e)} />
        </Row>
        <Row style={{ height: 30 }}></Row>
        <Loader type="Watch"
          color="#00BFFF"
          height={100}
          width={100}
          visible={this.state.showLoader}
          style={{
       display: "flex",
       justifyContent: "center",
       alignItems: "center",
        height:"60vh",
        position:"absolute",
        left: 0,/*added*/
        right: 0, /*added*/
        zIndex:1
     }}
        />
        <Paper>
          <Chart data={data}>
            <ValueScale name="oil" modifyDomain={modifyOilDomain} />
            <ValueScale name="price" modifyDomain={modifyPriceDomain} />

            <ArgumentAxis />
            <ValueAxis scaleName="oil" labelComponent={LabelWithThousand} />
            <ValueAxis
              scaleName="price"
              position="right"
              labelComponent={PriceLabel}
            />

            <Title text="Machine Performance" />

            {this.series}

            <Stack stacks={stacks} />
            <EventTracker />
            <HoverState hover={target} onHoverChange={this.changeHover} />
            <Tooltip
              targetItem={target}
              contentComponent={this.TooltipContent}
            />
            <Animation />
            <Legend
              position="bottom"
              rootComponent={LegendRoot}
              itemComponent={LegendItem}
              labelComponent={LegendLabel}
            />
          </Chart>
        </Paper>
      </DashboardLayout>
    );
  }
}
