/**
 * @module App
 */

import React from "react";
import { useMediaQuery } from "react-responsive";  
import Routes from "./routes";
import Login from '../../login';
import useToken from '../../apptoken';
import OynaViewer from "../oynaviewer/oynaviewer"
import RoutesEcom from "./routesEcom";
/**
* @author Muthu G & Balaji K
* @description React Main App
*/
const App = () => {
  const { token, setToken } = useToken();
  const isMobile = useMediaQuery({ query: '(max-width: 1224px)' })
  console.log(window.location.pathname); //yields: "/js" (where snippets run)
  console.log(window.location.href);
  if(window.location.pathname==='/oynaiframe'){
    return<OynaViewer  isMobile={isMobile}></OynaViewer>
  }
  else{

    if(!token) {
      return <Login setToken={setToken} />
    }
    else {
      // if(process.env.REACT_APP_TYPE==='ecom')
        return <RoutesEcom isMobile={isMobile} />;
        // else
        // return <Routes isMobile={isMobile} />;
    }

  }

};

export default App;
