/**
 * @module Branch
 */
import * as React from "react";
import { BrowserRouter, Switch, Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { DashboardLayout } from "../navbar/Layout";
import { withStyles, makeStyles, lighten } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import queryString from "query-string";
// import sha256 from "crypto-js/sha256";
// import hmacSHA512 from "crypto-js/hmac-sha512";
// import Base64 from "crypto-js/enc-base64";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import DeleteForeverSharpIcon from "@material-ui/icons/DeleteForeverSharp";
import EditSharpIcon from "@material-ui/icons/EditSharp";
import ReactNotification from "react-notifications-component";
import { store } from "react-notifications-component";
import GroupAddSharpIcon from "@material-ui/icons/GroupAddSharp";
import Typography from "@material-ui/core/Typography";
import { OTable } from "./table";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
} from "react-bootstrap";
import Loader from "react-loader-spinner";
var crypto = require("crypto");
require("dotenv").config();
/**
* @author Muthu G & Balaji K
* @description Table cell Theme
* @param {Object} theme - childrens
*/
const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);
/**
* @author Muthu G & Balaji K
* @description Table Row Theme
*/
const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});
const headCells = [
  {
    id: "name",
    numeric: false,
    disablePadding: true,
    label: "Name",
    link: true,
  },
  {
    id: "username",
    numeric: true,
    disablePadding: false,
    label: "Email",
    link: false,
  },
  {
    id: "apptype",
    numeric: true,
    disablePadding: false,
    label: "Application",
    link: false,
  },
];
/**
* @author Muthu G & Balaji K
* @description ToolBar Theme
*/
const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  title: {
    flex: "1 1 100%",
  },
}));
/**
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
const Titlebar = (props) => {
  const classes = useToolbarStyles();
  return (
    <>
      <Typography
        align="right"
        className={classes.title}
        component="div"
        style={{ width: 40 }}
      >
        <input type="file" onChange={props.showfilefun} />
        <IconButton variant="h6" onClick={props.clickfun}>
          <GroupAddSharpIcon variant="h6" style={{ fontSize: 40 }} />
        </IconButton>
        Add Node
      </Typography>
    </>
  );
};
/**
 * @module Branch_react_Component
* @author Muthu G & Balaji K
* @description Branch
* @param {Object} props - Props from parent
*/
class Branch extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      dataget: undefined,
      showLoader:true,
    };
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name Get_Branche_users
  * @description It will getting all users
  */
  GetBrancheUsers() {
    // POST request using fetch with set headers
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        branchid: this.props.match.params.id,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/GetUsers", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ dataget: data });
                this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentWillMount
  * @description It will mount before start
  */
  componentWillMount() {
    this.GetBrancheUsers();
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name addNewNodeToBranch
  * @description It will add new node to branch
  */
  addNewNodeToBranch() {
      this.setState({ showLoader: true });
   
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        branchid: this.props.match.params.id,
        reqfile: this.state.reqfile
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/adduser", requestOptions)
      .then((response) => response.json())
      .then((data) => {

        this.GetBrancheUsers();
        store.addNotification({
          title: "User!",
          message: "New User Added",
          type: "success",
          insert: "top",
          container: "top-right",
          animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
          animationOut: ["animate__animated animate__fadeOut"],
          dismiss: {
            duration: 5000,
            onScreen: false,
          },
          slidingExit: {
            duration: 800,
            timingFunction: "ease-out",
            delay: 0,
          },
        });
          this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name showFile
  * @description It will used for change event for file
    * @param {object} e -event
  */
  showFile = async (e) => {
    e.preventDefault();
    const reader = new FileReader();
    reader.onload = async (e) => {
      const text = e.target.result;
      this.setState({ reqfile: text });
    };
    reader.readAsText(e.target.files[0]);
  };
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name delete
  * @description It will Edit the row
  * @param {object} row -row
  */
  delete(row) {
  this.setState({ showLoader: true });
    const dataf = new FormData();
    {
      const requestOptions = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: JSON.parse(localStorage.getItem("token")).token,
        },
        body: JSON.stringify({
          id: row._id,
        }),
      };

      var url = process.env.REACT_APP_SERVER_HOST + "/deleteuser";
      fetch(url, requestOptions)
        .then((response) => response.json())
        .then((data) => {
          this.GetBrancheUsers();
          store.addNotification({
            title: "User!",
            message: "User has deleted",
            type: "danger",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
            animationOut: ["animate__animated animate__fadeOut"],
            dismiss: {
              duration: 5000,
              onScreen: false,
            },
            slidingExit: {
              duration: 800,
              timingFunction: "ease-out",
              delay: 0,
            },
          });
            this.setState({ showLoader: false });
        });
    }
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    const classes = [{}]; // useStyles();

    const handleClose = () => this.setState({ show: false });
    const handleShow = () => this.setState({ show: true });

    const handleSubmit = (event) => {
        this.setState({ showLoader: true });
      const form = event.currentTarget;
      const dataf = new FormData(event.currentTarget);
      {
        event.preventDefault();
        event.stopPropagation();
        const requestOptions = {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: JSON.parse(localStorage.getItem("token")).token,
          },
          body: JSON.stringify({
            name: this.state.name,
            username: this.state.username,
            apptype: this.state.apptype,
            password: this.state.password,
            branchid: this.props.match.params.id,
          }),
        };
        fetch(process.env.REACT_APP_SERVER_HOST + "/adduser", requestOptions)
          .then((response) => response.json())
          .then((data) => {
            this.GetBrancheUsers();
              this.setState({ showLoader: false });
          });
      }
    };

    return (
      <DashboardLayout>
        <Container fluid>
          <Row style={{ height: 60 }}></Row>
          <Row>
            <Col xs={10}>
            <Loader type="Watch"
              color="#00BFFF"
              height={100}
              width={100}
              visible={this.state.showLoader}
              style={{
           display: "flex",
           justifyContent: "center",
           alignItems: "center",
            height:"60vh",
            position:"absolute",
            left: 0,/*added*/
            right: 0, /*added*/
            zIndex:1
         }}
            />
              {this.state.dataget && (
                <OTable
                  Title="Nodes"
                  headCells={headCells}
                  tableData={this.state.dataget}
                  selecting={this.selecting}
                  delete={(row) => this.delete(row)}
                  edit={(row) => this.edit(row)}
                  isedit={false}
                  clickLink={(e, row) => {
                    e.preventDefault();
                    this.props.history.push("/users/" + row._id);
                  }}
                  Titlebar={
                    <Titlebar
                      clickfun={(e) => this.addNewNodeToBranch()}
                      showfilefun={(e) => this.showFile(e)}
                    />
                  }
                ></OTable>
              )}
            </Col>
          </Row>
        </Container>
      </DashboardLayout>
    );
  }
}

export default withRouter(Branch);
