/**
 * @module Route
 */
import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Logout from "./logout";
import OynaViewer from "../oynaviewer/oynaviewer"
import LoadingBar from "../Modal/LoadingBar";
import SareeImporter from "../SareeImporter/SareeImporter";
import ProductsTable from "../Products/ProductsTable";
import Type from "../Category/Type";
import Category from "../Category/Category";
import OynaMen from "../oynaviewer/OynaMen"; 
import AddNewProduct from '../Products/addNewProduct';
import Test from "../oynaviewer/test";
import SettingsPage from "./settings";
import AllBranches from "./allbranches";
import BranchPage from "./branch";
import UserPage from "./Users";
import MembersPage from "./members";
import Files from "./files";
import AboutPage from "./about";
import TeamsPage from "./teams";
import ProcessLog from "./processlog";
import HomePage from "./home";
/**
* @author Muthu G & Balaji K
* @description Routes for the react pages

*/
const RoutesEcom = (props) => {
  //  console.log("react");
  // localStorage.clear();
  
  console.log(props);
  return (
    <BrowserRouter>
      <Switch>

        <Route path="/logout" component={Logout}></Route>
        <Route path="/oynaviewer"   >
          <OynaViewer normalLoad {...props}></OynaViewer>
        </Route>
        <Route path="/loadbar"   >
          <LoadingBar ></LoadingBar>
        </Route>
        <Route path="/men"  >
          <OynaMen></OynaMen>
          {/* <OynaViewer men normalLoad {...props}></OynaViewer> */}
          {/* <Test></Test> */}
        </Route>
        <Route path="/products/allproducts">
          <ProductsTable></ProductsTable>
        </Route>
        <Route path="/products/addproducts" >
          <AddNewProduct/>
          {/* <SareeImporter /> */}
        </Route>
        <Route path = "/products/addtype">
          <Type/>
        </Route>
        <Route path = "/products/addcategory">
          <Category/>
        </Route>
        <Route path="/products">
          <ProductsTable></ProductsTable>
        </Route>
        <Route path="/Branches/Users">
          <MembersPage />
        </Route>
        <Route path="/Branches/Files" component={Files}></Route>
        <Route path="/Branches/AllBranches">
          <AllBranches />
        </Route>
        <Route path="/Branches">
          <AllBranches />
        </Route>
        <Route path="/about">
          <AboutPage />
        </Route>
        <Route path="/another/teams">
          <TeamsPage />
        </Route>
        <Route path="/another">
          <TeamsPage />
        </Route>
        <Route path="/another/processmessage">
          <ProcessLog />
        </Route>
        <Route path="/settings">
          <SettingsPage />
        </Route>
        <Route path="/client">
          <HomePage />
        </Route>
        <Route path="/branch/:id" component={BranchPage}></Route>

        <Route path="/users/:id" component={UserPage}></Route>

        <Route path="/logout" component={Logout}></Route>
        <Route path="/dashboard"  >
        <TeamsPage />
        </Route>
        <Route path="/"  >
          <OynaViewer normalLoad {...props}></OynaViewer>
        </Route>
      </Switch>
    </BrowserRouter>
  );
};

export default RoutesEcom;
