import React from "react";
import '../ProfileShoot/ProfileShoot.css';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import InputSlider from '../../Controls/InputSlider';
import { red } from "@mui/material/colors";
import profile from '../../../Icons/profile.png'
import folder from '../../../Icons/folder.png';
import webcam from '../../../Icons/webcam.png';
import submitimg from '../../../Icons/submit.png';
import hair1 from '../../../Icons/hair1.png';
import hair2 from '../../../Icons/hair2.png';
import hair3 from '../../../Icons/hair3.png';
import WebcamCapture from '../WebCam';
import Webcam from 'react-webcam';
import { LoadingBar } from '../LoadingBar';

class ProfileShoot extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            height: 165,
            weight: 45,
            hairStyle: 'custom',
            imagePath: { profile },
        }
        this.webcamRef = React.createRef();
        this.uploadImage = this.uploadImage.bind(this);
    }

    uploadImage() {
        console.log("Upload Image");
        document.getElementById("chooseImage").click();
        this.setState({ isCameraOn: false });
    }
    
    componentDidMount() {

    }

    captureImage() {
        const imageSrc = this.webcamRef.current.getScreenshot({ width: 1920, height: 1080 });
        console.log(imageSrc);
        this.setState({ isCameraOn: false });
        this.props.setImageFile(imageSrc);
    }
    page() {
        const videoConstraints = {
            width: 200,
            height: 200,
            facingMode: "user"
        };
        return (<div class="relative right-10 rounded-2xl bg-gradient-to-tr from-blue-900">
                    <div class="w-full flex flex-row">
                        <p class="text-left px-3 mt-2 py-1 font-bold text-white text-lg w-10/12">
                            PROFILE SHOOT
                        </p>
                        <div class="items-center py-3 px-3" onClick={()=>this.props.close()}>
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-7 w-7 transform hover:border-blue-600 transition duration-500 hover:scale-125 cursor-pointer" viewBox="0 0 20 20" fill="red">
                                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" fill="red" clipRule="evenodd" />
                            </svg>
                        </div>
                        {/* <div class="relative left-4 top-1" onClick={()=>this.props.close()}>
                                <button class=" px-2 py-0  font-semibold rounded-full shadow-md text-white bg-red-500 hover:bg-red-700 ">
                                X
                            </button>
                        </div> */}
                    </div>
                    <div className="px-2 m-2 py-2 rounded-lg border-2 border-gray-300">
                        <div class="m-2 px-2 py-2 content-center flex flex-row h-48 rounded-xl from-blue-500">
                        {this.state.isCameraOn ? (
                            <Webcam audio={false}
                                    id="videoid"
                                    ref={this.webcamRef}
                                    videoConstraints={videoConstraints}
                                    class="rounded-2xl object-cover  w-full"/>
                        ) : (<img class="rounded-2xl object-cover  w-full" src={this.props.imageFile ? this.props.imageFile : profile} id="uploadimg" name="uploadimg" />)}


                    <div class="absolute  right-4 float-right  content-center">
                        <div class="mt-8 relative items-center rounded-xl " >
                            <img class="cursor-pointer h-12" src={folder} onClick={() => this.uploadImage()} />
                            <img class="cursor-pointer  h-12" src={webcam} onClick={() => {
                                if (this.state.isCameraOn) {
                                    this.captureImage()
                                    this.setState({ isCameraOn: false });
                                }
                                else {
                                    this.setState({ isCameraOn: true });
                                }
                            }} />
                        </div>
                    </div>
                        </div>
                        
                        <div class=" m-2   px-2 py-2 flex flex-col rounded-xl">
                    <div class="w-full  flex flex-col">
                        <div class="px-6 pt-2 w-full ">
                            <Slider
                                value={this.state.weight}
                                onChange={(event) => this.setState({ weight: event.target.value === '' ? '' : Number(event.target.value) })}
                                min={45}
                                max={100}
                                width="20px"
                                valueLabelDisplay="auto"
                                valueLabelFormat={(val) => val + " KG"}
                                label="Weight"
                                defaultValue={[20, 37]}/>
                        </div>

                        <div class="text-center   text-white text-xl"> Weight : {this.state.weight} KG</div>
                    </div>
                    <div class="px-2 w-full  flex flex-col">
                        <div class="px-3 w-full">
                                <Slider
                                    value={this.state.height}
                                    onChange={(event) => this.setState({ height: event.target.value === '' ? '' : Number(event.target.value) })}
                                    min={130}
                                    max={200}
                                    width="20px"
                                    valueLabelDisplay="auto"
                                    valueLabelFormat={(val) => val + " cm"}
                                    label="height"
                                    defaultValue={[20, 37]}/>
                            </div>
                            <div class="text-center text-white text-xl"> Height : {this.state.height} cm</div>
                            </div>
                            <div class="px-1 gap-2 py-2 flex flex-row mt-3 justify-items-center">
                                <img class={ `h-20 border-2  rounded-full ${this.state.hairStyle==="custom"?"border-green-500":'border-gray-500'} `} src={hair1} onClick = { () => this.setState({hairStyle:"custom"}) } ></img>
                                <img class={ `h-20 border-2  rounded-full ${this.state.hairStyle==="ponytail_with_bangs"?"border-green-500":'border-gray-500'} `} src={hair2} onClick = { () => this.setState({hairStyle:"ponytail_with_bangs"})  } ></img>
                                <img class={ `h-20 border-2  rounded-full ${this.state.hairStyle==="long_wavy"?"border-green-500":'border-gray-500'} `} src={hair3} onClick = { () =>  this.setState({hairStyle:"long_wavy"}) } ></img>
                            </div>
                            <div class="text-center py-1 text-white text-xl"> Hair Styles</div>
                        </div>
                        
                    </div>
                    <div class="grid justify-items-center pb-3">
                        <button class="content-center align-center py-2 px-4 font-semibold rounded-lg shadow-md text-white bg-green-500 hover:bg-green-700"  onClick={()=>this.props.proceedGenerate( this.state)}>
                            Generate 3D
                        </button>
                    </div>
                    
                </div>)
    }
    render() {

        return (
            <>
                <div class="fixed flex-shrink-0  sm:hidden md:block  z-10 right-5 top-5">

                    {this.page()}

                </div>
                <div class="fixed flex-shrink-0  md:hidden sm:block md:h-full xl:w-48 sm:w-full  z-10 sm:right-0">

                    {this.page()}

                </div>
                <input style={{ visibility: 'hidden', display: 'none' }} id="chooseImage" type="file" accept="image/*" onChange={this.props.showFile} />

            </>
        )
    }
}

export default ProfileShoot;

