import {mat4,vec3,vec2} from "gl-matrix";

const Camera = class {
    constructor (aspect) {
		this.pitchAngle = 0;
		this.yawAngle = 0;
		this.Translational = vec3.fromValues(0, -80, 0);
		this.prevLocation = vec2.create();
		this.MultiPrev = [];
		this.model = mat4.create();
		this.modelMatrix = mat4.create();
		this.rotateXMatrix = mat4.create();
		this.rotateYMatrix = mat4.create();
		this.projMatrix = mat4.create();
		this.angleX = 0;
		this.angleY = 0;
		this.fov = 1.57079637;
		this.yaw = -1.57079637;
		this.AspectRatio = 0;
		this._front = vec3.fromValues(0, 0, -1);
		this._up = vec3.fromValues(0, 1, 0);
		this.Position = vec3.fromValues(0, 0, 100);
		this.AspectRatio = aspect;
		//this.MultiPrev.push(vec2.create());
		//this.MultiPrev.push(vec2.create());
		this.fov = this.DegreetoRadians(57);
		this.pointerdown_handler=this.pointerdown_handler.bind(this);
		this.pointermove_handler=this.pointermove_handler.bind(this);
		this.pointerup_handler=this.pointerup_handler.bind(this);
		this.remove_event=this.remove_event.bind(this);
		this.evCache=[];
		this.prevDiff=null;
		this.prevScale=0;
	}

 	GetProjectionMatrix = () => {
		return this.perspectiveMatrix(this.fov, this.AspectRatio, 10, 200);
 	}

 	GetViewMatrix = () => {
		var vmat = mat4.create();
		var added = vec3.create();
		vec3.add(added, this.Position, this._front)
		mat4.lookAt(vmat, this.Position, added, this._up);
		return vmat;
 	} 

 	GetModelMatrix = () => {
		var mult = mat4.create();
		var trans = mat4.create();
		var translated = mat4.create();
		var scalemat = mat4.create();
		mat4.fromXRotation(this.rotateXMatrix, 0.2);
		mat4.fromYRotation(this.rotateYMatrix, -this.yawAngle);
		mat4.multiply(mult, this.rotateXMatrix, this.rotateYMatrix);
		mat4.fromTranslation(trans, this.Translational);
		mat4.multiply(translated, trans, mult);
		mat4.fromScaling(scalemat, vec3.fromValues(0.5, 0.5, 0.5));
		mat4.multiply(this.model, scalemat, translated);
		return this.model;
 	}

	MouseMove =(e)=> {
		e.preventDefault();
		e.stopPropagation();
		if (e.buttons == 1) {
			this.pitchAngle += this.DegreetoRadians(e.pageY - this.prevLocation[1]);
			this.yawAngle += this.DegreetoRadians(e.pageX - this.prevLocation[0]);

			var twoPi = 2 * Math.PI;
			if (this.pitchAngle < 0) {
				this.pitchAngle += twoPi;
			}
			else if (this.pitchAngle > twoPi) {
				this.pitchAngle -= twoPi;
			}
			this.prevLocation = vec2.fromValues(e.pageX, e.pageY);
		}
		else if (e.buttons == 2) {
			this.Translational[1] -= this.DegreetoRadians(e.pageY - this.prevLocation[1]);
			this.Translational[0] += this.DegreetoRadians(e.pageX - this.prevLocation[0]);
			this.prevLocation = vec2.fromValues(e.pageX, e.pageY);
		}

	}

	MouseWheel=(e)=> {
		this.fov -= this.DegreetoRadians(e.wheelDelta * (1 / 120));
		if (this.fov < 0.01)
			this.fov = 0.01;	
		else if (this.fov > 2.5)
			this.fov = 2.5;
	}

	TouchStart =(e)=> {
		e.preventDefault();
		if (e.touches.length >= 2) {
			this.MultiPrev[0] = vec2.fromValues(e.touches[0].pageX, e.touches[0].pageY);
			this.MultiPrev[1] = vec2.fromValues(e.touches[1].pageX, e.touches[1].pageY);
		}
		else
			this.prevLocation = vec2.fromValues(e.touches[0].pageX, e.touches[0].pageY);
	}

	TouchEnd=(e)=> {
		e.preventDefault();
		if(e.touches.length >= 2) {
			this.MultiPrev[0] = vec2.fromValues(0, 0);
			this.MultiPrev[1] = vec2.fromValues(0, 0);
		}
		else
			this.prevLocation = vec2.fromValues(0, 0);
	}

	TouchMove =(e)=> {
		e.preventDefault();
		if (e.touches.length >= 2) {
			var Moved = e.touches[0].pageY - this.MultiPrev[0][1];
			Moved += e.touches[1].pageY - this.MultiPrev[1][1];
			Moved += e.touches[0].pageX - this.MultiPrev[0][0];
			Moved += e.touches[1].pageX - this.MultiPrev[1][0];
			Moved = -Moved * (1 / 12);
			this.fov -= this.DegreetoRadians(Moved);
			if (this.fov < 0.01)
				this.fov = 0.01;
			else if (this.fov > 2.5)
				this.fov = 2.5;
			this.MultiPrev[0] = vec2.fromValues(e.touches[0].pageX, e.touches[0].pageY);
			this.MultiPrev[1] = vec2.fromValues(e.touches[1].pageX, e.touches[1].pageY);
		}
		else {
			if (this.prevLocation[0] == 0 && this.prevLocation[1] == 0) return;
			this.pitchAngle += this.DegreetoRadians(e.touches[0].pageY - this.prevLocation[1]);
			this.yawAngle += this.DegreetoRadians(e.touches[0].pageX - this.prevLocation[0]);
			var twoPi = 2 * Math.PI;
			if (this.pitchAngle < 0) {
				this.pitchAngle += twoPi;
			}
			else if (this.pitchAngle > twoPi) {
				this.pitchAngle -= twoPi;
			}
		}
		this.prevLocation = vec2.fromValues(e.touches[0].pageX, e.touches[0].pageY);
	}
	reset(){
		this.Translational = vec3.fromValues(0, -80, 0);
		this.fov=this.DegreetoRadians(57);
		// this.yaw = -1.57079637;
		this.yawAngle=0;

	}
	rotate(ev){
		if(ev.type==="panleft" ||ev.type==="panright"){
			// this.Translational[1] -= this.DegreetoRadians(e.pageY - this.prevLocation[1]);
			// this.pitchAngle += ev.velocity;
			this.yawAngle +=window.devicePixelRatio>2? ev.velocity/2:ev.velocity/12;// this.DegreetoRadians(e.pageX - this.prevLocation[0]);

		
		}
	}
	pan(ev){
		if(ev.type==="panleft" ||ev.type==="panright"){
			// this.Translational[1] -= this.DegreetoRadians(e.pageY - this.prevLocation[1]);
			this.Translational[0] +=window.devicePixelRatio>2? ev.velocity*8:ev.velocity;
		}
		else if(ev.type==="panup"||ev.type==="pandown"){
			// this.Translational[1] -= this.DegreetoRadians(e.pageY - this.prevLocation[1]);
			this.Translational[1] -=window.devicePixelRatio>2? ev.velocity*8:ev.velocity;
		}
	}
	pinch(ev){


     	let Moved=	this.prevScale-(ev.scale);
		//  if(Math.abs(Moved)<2)
		// if(moved<1){
		// 	// Moved = +(1/Moved) * (1 / 12);
		// }
		// else
		if(ev.scale>1)
		 {
		
			let Moved=	(this.prevScale-1)-(ev.scale-1);
		
			// if(Moved>0)
			{
				Moved = -Moved * (1 / 12);
				console.log("in",Moved);
				if(Moved<0.3&&Moved>0)
	   this.fov -= Moved;
	//    this.prevScale=(ev.scale-1);
			 }
			}
		
		 else{
		
			let Moved=	(1-this.prevScale)-(1-ev.scale);
			console.log("out",Moved,);
			if(Moved>-0.3){
				Moved = Moved ;
				this.fov -= Moved;
			}
		


		 }
			   if (this.fov < 0.01)
			   this.fov = 0.01;
		   else if (this.fov > 2.5)
			   this.fov = 2.5;
	
			   this.prevScale=(ev.scale);
		if(ev.isFinal)
		this.prevScale=1;
	}
	pointerdown_handler(ev) {
		ev.preventDefault();
		// The pointerdown event signals the start of a touch interaction.
		// This event is cached to support 2-finger gestures
		this.evCache.push(ev);

	   }
	   pointermove_handler(ev) {
		ev.preventDefault();
		// This function implements a 2-pointer horizontal pinch/zoom gesture.
		//
		// If the distance between the two pointers has increased (zoom in),
		// the target element's background is changed to "pink" and if the
		// distance is decreasing (zoom out), the color is changed to "lightblue".
		//
		// This function sets the target element's border to "dashed" to visually
		// indicate the pointer's target received a move event.
	   // console.log("pointerMove", ev);
		// ev.target.style.border = "dashed";
	   
		// Find this event in the cache and update its record with this event
		for (var i = 0; i < this.evCache.length; i++) {
		  if (ev.pointerId == this.evCache[i].pointerId) {
			this.evCache[i] = ev;
		  break;
		  }
		}
	   
		// If two pointers are down, check for pinch gestures
		if (this.evCache.length == 2) {
		  // Calculate the distance between the two pointers
		  var curDiff = Math.abs(this.evCache[0].clientX - this.evCache[1].clientX);
	   
		
		let Moved=curDiff -this.prevDiff ;
		
		if(Moved!==0&&Math.abs(Moved)<1)
		{
			Moved = Moved * (1 / 5);
			console.log(Moved);
			this.fov -= Moved;
			if (this.fov < 0.01)
				this.fov = 0.01;
			else if (this.fov > 2.5)
				this.fov = 2.5;
		}
	
		
		//   if (  this.prevDiff > 0) {
		// 	if (curDiff >   this.prevDiff) {
		// 	  // The distance between the two pointers has increased
		// 	 // console.log("Pinch moving OUT -> Zoom in", ev);
		// 	 //console.log("zoom in" );
		// 	  ev.target.style.background = "pink";
		// 	}
		// 	if (curDiff <   this.prevDiff) {
		// 	 // console.log("zoom out");
		// 	  // The distance between the two pointers has decreased
		// 	//  console.log("Pinch moving IN -> Zoom out",ev);
		// 	  ev.target.style.background = "lightblue";
		// 	}
		//   }
	   
		  // Cache the distance for the next move event
		  this.prevDiff = curDiff;
		}
	   }
	   pointerup_handler(ev) {
		ev.preventDefault();
		// console.log(ev.type, ev);
		// Remove this pointer from the cache and reset the target's
		// background and border
		this.remove_event(ev);
		ev.target.style.background = "white";
		ev.target.style.border = "1px solid black";
	  
		// If the number of pointers down is less than two then reset diff tracker
		if (this.evCache.length < 2) {
		  this.prevDiff = -1;
		}
	  }
	  remove_event(ev) {
		// Remove this event from the target's cache
		for (var i = 0; i < this.evCache.length; i++) {
		  if (this.evCache[i].pointerId == ev.pointerId) {
			this.evCache.splice(i, 1);
			break;
		  }
		}
	   }
	perspectiveMatrix =(fieldOfViewInRadians, aspectRatio, near, far)=> {
		var f = 1.0 / Math.tan(fieldOfViewInRadians / 2);
		var rangeInv = 1 / (near - far);
		return[
			f / aspectRatio, 0, 0, 0,
			0, f, 0, 0,
			0, 0, (near + far) * rangeInv, -1,
			0, 0, near * far * rangeInv * 2, 0
		];
	}

	clamp = (num, min, max)=> {
		return num <= min ? min : num >= max ? max : num;
	}

	RadianstoDegrees =(angle) =>{
		return angle * (180 / Math.PI);
	}

	DegreetoRadians = (angle)=> {
		return angle * (Math.PI / 180);
	}
}
export default Camera;