/**
 * @module HomePage
 */
 import * as React from "react";
 import ProductList from "../Products/productListView"
 import { vec3 } from "gl-matrix";
 import IconButton from "@material-ui/core/IconButton";
 import JSZip, {  } from "jszip";
 import JSZipUtils from "jszip-utils"
 import localForage from "localforage";
 import { API_Urls } from "../../api/config";
 import axios from 'axios';
 import CircleMenuBar from "../circularMenu/index"
 import SkintoneMenu from "../circularMenu/skintonemenu"
 // import { CircleMenu, CircleMenuItem } from "react-circular-menu";
 import ProfileShoot from "../Modal/ProfileShoot/ProfileShoot"
 import girlpng from '../../Icons/girl.png'
 import dark1 from '../../Icons/dark1.png'
 import pause from '../../Icons/pause.png'
 import add1 from '../../Icons/add1.png'
 import LFA_POSEPng from '../../Icons/LFA_POSE.png'
 import fair1 from '../../Icons/fair1.png'
 import fair2 from '../../Icons/fair2.png'
 import dusky1 from '../../Icons/dusky1.png'
 import profile from '../../Icons/profile.png';
 import playpng from '../../Icons/play.png';
 import sareeIcon from '../../Icons/sareeIcon.jpg';
 import logoimg from '../../Icons/Logo.png'
 import PanToolAltIcon from '@mui/icons-material/PanToolAlt';
 import ThreeSixtyIcon from '@mui/icons-material/ThreeSixty';
 import RestartAltIcon from '@mui/icons-material/RestartAlt';
 import API from "../../api/services";
 import OynaEngine from "../../Engine/oyna_Engine";
 // axios.defaults.baseURL =  process.env.REACT_APP_SERVER_HOST;
 // const canvasToDisplaySizeMap = new Map([[canvas, [300, 150]]]);
 class OynaViewer extends React.Component {
   constructor(props) {
     super(props);
     console.log(props)
     this.state = {
       collapsebtn: true,
       isplay: false,
       init: false,
       file: null,
       products: [],
       showProducts: false,
       isExpand: false,
       isExpandsecond:true
     };

     this.timePassed = 0;
     this.timeLeft = 0;
     this.timerInterval = null;
     this.serverURL = process.env.REACT_APP_SERVER_IP;
     this.urlicon = this.serverURL + "Icons/";
     this.HostUrl = process.env.REACT_APP_SERVER_HOST;
     this.oynaEngine= React.createRef();
   }
   resize = () => {
     this.setState({
       height: window.innerHeight,
       width: window.innerWidth
     });
   };


   async LoadProducts() {
     const config = {
       headers: {
         'Content-Type': 'application/json',
         Authorization: JSON.parse(localStorage.getItem("token")).token,
       }
     };
 
     // const allfiles = await axios
     //   .get(process.env.REACT_APP_SERVER_HOST + "/products/get_all_products", config)
 
     // console.log(allfiles);
     const products =await API.getProducts("women");// allfiles.data;
     console.log(products);
     this.setState({ products: products })
     let ids = [];
     for (let ind = 0; ind < products.length; ind++) {
       ids.push(products[ind].images[0].filename)
     }
     if (products[0] === undefined) return;
     this.SelectProduct(products[0]);
 
     var data = {
       'fileids': ids,
 
     }
     // formData.append("fileCount",  this.state.images.length);
     const allfilesImagw =  await API.get_images(data);
     //  axios
     //   .post(process.env.REACT_APP_SERVER_HOST + "/products/get_images", data, config)
 
     for (let i = 0; i < allfilesImagw.length; i++) {
       let item1 = products.filter((item) => item.images[0].filename === allfilesImagw[i].filename)[0]
       products[i].img = allfilesImagw[i].img;
       this.setState({ products: products })
       //  allfilesImagw.data[i].filename
     }
     this.setState({ products: [] })
     this.setState({ products: products })
     console.log(products);
     return;
   }
   async SelectProduct(item) {
     this.setState({ showLoader: true })
 
     console.log("clicked", item);
     const config = {
       headers: {
         'Content-Type': 'application/json',
         Authorization: JSON.parse(localStorage.getItem("token")).token,
       }
     };
     let ids = [];
     for (let ind = 0; ind < item.images.length; ind++) {
       ids.push(item.images[ind].filename)
     }
 
     var data = {
       'fileids': ids,
 
     }
     const allfilesImagw =await API.get_images(data);
     //  await axios
     //   .post(process.env.REACT_APP_SERVER_HOST + "/products/get_images", data, config)
 
     let textures = {};
     for (let i = 0; i < allfilesImagw.length; i++) {
       let item1 = item.images.filter((item) => item.filename === allfilesImagw[i].filename)[0]
       if (item1.originalname.toUpperCase() === "SAREE")
         textures.SAREE = allfilesImagw[i].img;
       else if (item1.originalname.toUpperCase() === "PALLU")
         textures.PALLU = allfilesImagw[i].img;
       else if (item1.originalname.toUpperCase() === "BLOUSE")
         textures.BLOUSE = allfilesImagw[i].img;
 
     }
 
     await this.oynaEngine.current.setTextures(textures);
     let _this = this;
     setTimeout(() => {
 
       if ((_this.state.width < 1024)) {
         _this.setState({ showProducts: false })
       }
       _this.setState({ showLoader: false })
     }, 1000);
 
   }
   async initModels(){
    let cachedata = await localForage.getItem('Model');
    if (cachedata) {
      await this.oynaEngine.current.LoadModel(cachedata, this.LFAPose);

    } else {
      let _this = this;
      await new JSZip.external.Promise(function (resolve, reject) {
        JSZipUtils.getBinaryContent(API_Urls.app + 'Models/lao.zip', function (err, data) {
          if (err) {
            reject(err);
          } else {
            resolve(data);
          }
        });
      }).then(function (data) {
        return JSZip.loadAsync(data);
      })
        .then(function (zip) {
          return zip.file("lao.txt").async("string");
        }).then(function success(text) {
          _this.oynaEngine.current.LoadModel(text, _this.oynaEngine.current.LFAPose);
          localForage.setItem('Model', text);
        },
          function error(e) {
          });
    }

    let Real_Model = await localForage.getItem('Real_Model');
    if (Real_Model) {
      await this.oynaEngine.current.LoadModel(Real_Model, this.oynaEngine.current.LAOPose);
    }
    this.oynaEngine.current.InitWindow();
    this.oynaEngine.current.setState({
      showLoader: false
    })

   }
   componentDidMount() {
     this.resize();
     let _this = this;
     if (this.props.normalLoad) {
       this.oynaEngine.current.Init();
       this.initModels();
        this.LoadProducts();
     }
     else {
       window.addEventListener('message', function (event) {
         //var origin = event.origin || event.originalEvent.origin; // For Chrome, the origin property is in the event.originalEvent object.
         // if (origin !== /*the container's domain url*/)
         //     return;
         if (event.data.call == 'sendValue') {
           console.log("send value", event);
           if (!_this.state.sareetextures) {
             _this.oynaEngine.current.setState({ sareetextures: event.data });
             _this.oynaEngine.current.Init();
           }
 
         }
         else if (event.data.call == 'openPanel') {
           console.log("openPanel", event);
           _this.oynaEngine.current.losecontext.restoreContext()
           // if (!_this.state.sareetextures) {
           //   _this.setState({ sareetextures: event.data });
           //   _this.Init();
           // }
 
         }
         else if (event.data.call == 'closePanel') {
           console.log("closePanel", event);
           _this.oynaEngine.current.losecontext.loseContext();
           // if (!_this.state.sareetextures) {
           //   _this.setState({ sareetextures: event.data });
           //   _this.Init();
           // }
 
         }
       }, false);
     }
     let divEnginge = document.getElementById('enginecanvas');
     divEnginge.addEventListener('resize', function() {
     })
     window.addEventListener('resize', function(e) {
         
     })
     let resizeObserver = new ResizeObserver(() => {
       console.log("divEnginge.clientWidth",divEnginge.clientWidth)
       this.oynaEngine?.current?.resizeCanvas(divEnginge.clientHeight, divEnginge.clientWidth)
       console.log("The element was resized",resizeObserver);
     });
     resizeObserver.observe(divEnginge);

   }


 
   showFile = async (e) => {
     e.preventDefault();
     console.log("Show File");
     this.setState({ file: e.target.files[0] });
 
     const reader = new FileReader();
     reader.onload = async (e) => {
       const text = e.target.result;
 
       this.setState({ Imagefile: text });
     };
     if (e.target.files[0])
       reader.readAsDataURL(e.target.files[0]);
   };
 
   ProfileShot() {
     return (
       <div style={{ backgroundColor: "white", zIndex: 15, position: "absolute" }} id="addbranchmodal" role="dialog" aria-labelledby="addbranchmodal" aria-hidden="true">
         <div ><h6>This is New Feature</h6>
           <input type="file" onChange={(e) => this.showFile(e)} />
           <button type="button" class="btn btn-secondary" data-dismiss="modal" onClick={() => {
 
             this.setState({ showModal: false })
           }}>Close</button>
         </div>
       </div>
 
 
     )
   }
 
   dataURLtoFile(dataurl, filename) {
 
     var arr = dataurl.split(','),
       mime = arr[0].match(/:(.*?);/)[1],
       bstr = atob(arr[1]),
       n = bstr.length,
       u8arr = new Uint8Array(n);
 
     while (n--) {
       u8arr[n] = bstr.charCodeAt(n);
     }
 
     return new File([u8arr], filename, { type: mime });
   }
   async GetAvatar(filetext, params) {
     const formData = new FormData();
     console.log(params);
     const file = this.dataURLtoFile(this.state.Imagefile, { profile })
     formData.append("avatarfile", file);
     formData.append("height", params.height);
     formData.append("weight", params.weight);
     formData.append("hairstyle", params.hairStyle);
     let _this = this;
     console.log(_this.HostUrl + "/AvatarGenerator");
     const config = {
       headers: {
         "content-type": "multipart/form-data",
         Authorization: ""// JSON.parse(localStorage.getItem("token")).token,
       },
       responseType: 'arraybuffer',
 
       onUploadProgress: (progressEvent) => {
         console.log({
           progress: Math.round(
             (100 * progressEvent.loaded) / progressEvent.total
           )
         });
         this.setState({
           progress: Math.round(
             (100 * progressEvent.loaded) / progressEvent.total
           ),
         });
       },
     };
     await axios
       .post(_this.HostUrl + "/AvatarGenerator", formData, config)
       .then(async (response) => {
         console.log(response);
 
         JSZip.loadAsync(response.data).then(function (zip) {
           return zip.file("lao.txt").async("string")
         })
           .then(function (content) {
             console.log(content);
             // use content
             _this.LoadModel(content, _this.LAOPose);
 
             localForage.setItem('Real_Model', content);
 
           });
 
       });
 
 
   }

   /**
   * @function
   * @author Muthu G & Balaji K
   * @name render
   * @description It will render the page
   */

 
   async proceedGenerate(params) {
     this.setState({
       showLoader: true,
       showModal: false
     });
     await this.GetAvatar(this.state.file, params);
     this.setState({
       showLoader: false
     });
     // this.setState({  })
   }
   resumeDraw(){
     this.setState({DoRender:true});
     let _this=this;
     setTimeout(() => {
       _this.oynaEngine.current.draw()
     }, 20);
   }
   
   render() {
     const { width, showProducts } = this.state;
     const skintoneImage = {
       width: '100%',
       height: '100%',
       borderRadius: '50%',
       clipPath: `circle(80% at center)`
     }
     const skinBtns = [{
       img: <img src={dark1} onClick={() => {
        this.oynaEngine.current.pixelvalue = vec3.fromValues(180 / 255, 140 / 255, 120 / 255);
         this.oynaEngine.current.changeskintone();
       }} style={skintoneImage} />
     },
     {
       img: <img src={dusky1} onClick={() => {
        this.oynaEngine.current.pixelvalue = vec3.fromValues(220 / 255, 180 / 255, 160 / 255);
         this.oynaEngine.current.changeskintone();
       }} style={skintoneImage} />
     },
     {
       img: <img src={fair2} onClick={() => {
        this.oynaEngine.current.pixelvalue = vec3.fromValues(240 / 255, 240 / 255, 240 / 255);
         this.oynaEngine.current.changeskintone();
       }} style={skintoneImage} />
     },
     {
       img: <img src={fair1} onClick={() => {
        this.oynaEngine.current.pixelvalue = vec3.fromValues(230 / 255, 210 / 255, 180 / 255);
         this.oynaEngine.current.changeskintone();
       }} style={skintoneImage} />
     },
     ];
     const menuBtns = [{
       img:
         <div role="button" href="#0" onClick={() => {
          this.oynaEngine.current.changePose("lfa")
          
  
           window.location = window.location.href.replace('#menu', '#0');
         }}  > <img src={LFA_POSEPng} style={skintoneImage} />
         </div>
     },
     {
 
       img: <div role="button" onClick={() => {
        this.oynaEngine.current.changePose("lao")
       
 
         window.location = window.location.href.replace('#menu', '#0');
       }}>
         <img src={this.state.Imagefile ? this.state.Imagefile : add1} class="rounded-full object-cover" style={{width: '60px',height: '50px'}} />
       </div>
     },
     {
       img:
         <div role="button" onClick={() => {
           if ((width < 1024)) {
             this.setState({ DoRender:false})
           }
           this.setState({ showModal: true })
           window.location = window.location.href.replace('#menu', '#0');
         }} >
           <img role="button" src={add1} style={skintoneImage} />
         </div>
 
     },
     ];
   
     return (
      <>
        <div>
           { this.state.isExpandsecond ? (
                  <div className="z-10 fixed top-3 left-3 text-white cursor-pointer" onClick={() => { this.setState({isExpand: !this.state.isExpand})
                       console.log("Click me")
                       let _this=this;
                       setTimeout(() => {
                        _this.setState({isExpandsecond: !_this.state.isExpand})
                       }, 0);}} >
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M4 6h16M4 12h16M4 18h7" />
                      </svg>
                  </div>
           ): (
             <div></div>
           ) }
            
            <div className="relative">
                  <div className="flex h-screen w-full">
                    <div className={`container2 ${ !this.state.isExpand ? `w-0` : `w-full`} left-0 bg-transparent`}>
                      <ProductList products={this.state.products}
                                  SelectProduct={(item) =>{ this.SelectProduct(item) }}
                                  origin = "women"
                                  setTextures={(val) => {
                                  this.engineRef.current.setTextures(val)}}
                                  setLoading={(val) => this.setState({ showLoader: val })}
                                  Close={() =>{ this.setState({ 
                                                isExpand: !this.state.isExpand})
                                                let _this = this
                                                setTimeout(() => {
                                                  _this.setState({isExpandsecond: !_this.state.isExpand})
                                                }, 0);
                                              }}>
                       </ProductList>
                    </div> 
                    <div id="enginecanvas" className={`container2  grow ${ this.state.isExpandsecond ? `w-full` : `w-1/2`} `}>
                        <OynaEngine  ref={this.oynaEngine} >
                        </OynaEngine>
                        {this.state.showModal && (
                          <div style={{ zIndex: 3 }}>
                            <ProfileShoot showFile={(e) => this.showFile(e)}
                              imageFile={this.state.Imagefile}
                              isMobile={this.props.isMobile}
                              setImageFile={(e) => this.setState({ Imagefile: e })}
                              proceedGenerate={(val) => this.proceedGenerate(val)}
                              close={() => {this.setState({ showModal: false })
                              this.resumeDraw()
                              
                            } 
                              }/>
                          </div>
                        )}
                      {!this.state.showModal && (
                        <>
                          <div style={{ right: 10, top: 20, position: "fixed", color: "white" }}>
                            <button onClick={() => {
                              this.oynaEngine.current.playpause()
                              this.setState({isplay:!this.state.isplay})
                              }} >
                              {this.state.isplay ? (<img src={pause} alt="Avatar" style={{ width: "70px" }}></img>) : (
                                <img src={playpng} alt="Avatar" style={{ width: "70px" }}></img>
                              )}
                            </button>
                          </div>
            
                          <div style={{ right: 20, top: 160, zIndex: 2, position: "fixed", color: "white" }}>
                            <CircleMenuBar name="models"
                              MenuButtons={menuBtns}
                              MenuIcon={<img src={girlpng} style={{
                                backgroundColor: "rgb(20,26,62)",
                                width: '100%',
                                height: '100%'
                              }} />}
                            />
                          </div>
            
                          <div style={{ right: 20, top: 240, position: "fixed", color: "white" }}>
                            <SkintoneMenu
                              name="skintone"
                              MenuButtons={skinBtns}
                              MenuIcon={<img src={dark1} style={{
                                width: '100%',
                                height: '100%'
                              }} />} />
                          </div>
                        </>
                      )}
            
                      {this.props.normalLoad && !this.state.showModal && (
                        <>
                          {width < 1024 && (
                            <div style={{ right: 10, top: 260, zIndex: 2, position: "fixed", color: "white" }}>
            
                              <img src={sareeIcon} style={{
                                width: '70px', height: '70px', borderRadius: '50%',
                                clipPath: `circle(80% at center)`
                              }} onClick={() =>{
                                this.setState({ showProducts: true,DoRender:false })
                                  
                                
                              } } />
            
                            </div>
                          )}
                        </>
                      )}
                      {this.state.showLoader && (
                        <div style={{ position: "fixed", textAlign: "center", zIndex: 25 }}>
                          <div >
            
                            <img className="rotateimg" src={logoimg} style={{
                              display: 'block',
                              margin: 'auto', position: 'fixed', left: '0', right: '0', top: '0', bottom: '0', padding: '10%'
                            }} />
                      
                          </div>
            
                        </div>
                      )}
                      {this.oynaEngine.current&&(
                            <div style={{ right:10, bottom: 10, zIndex: 2, position: "fixed", color: "white" }}>
                                <IconButton style={{ color: 'white', width: '50px', height: '50px' }} onClick={() => this.oynaEngine.current.setState({ isRotate: !this.oynaEngine.current.state.isRotate })}>
                                    {this.oynaEngine.current.state.isRotate ? (
                                        <PanToolAltIcon style={{ color: 'white', width: '50px', height: '50px' }} />
                                    ) : (<ThreeSixtyIcon style={{ color: 'white', width: '50px', height: '50px' }} />)}
                              </IconButton>
                            </div>
                      )}
                      <div style={{ right:70, bottom: 10, zIndex: 2, position: "fixed", color: "white" }}>
                            <IconButton style={{ color: 'white', width: '50px', height: '50px' }} onClick={()=>this.oynaEngine.current.camera.reset()}>
                              <RestartAltIcon style={{ color: 'white', width: '50px', height: '50px' }} />
                            </IconButton>
                      </div>
           
           
                    </div>
                  </div>
            </div>
        </div>
      </>
     );
   }
 }
 
 export default OynaViewer;
 