import {mat4,vec3} from "gl-matrix";

const Shader = class {
    constructor (id, vertex, fragment) {
        this.program=null;
        this._id = id;
        this._vertex = vertex;
        this._fragment = fragment;
        this._uniform = {};
        this._attributes = {};
    
        this.gDirectionalLightDirectionBaseColor = [0.937, 0.921, 0.847];
    }
    compileShader =(gl)=> {
        var fs = this._getShader(gl, this._fragment, gl.FRAGMENT_SHADER);
        var vs = this._getShader(gl, this._vertex, gl.VERTEX_SHADER);

        // Create the shader program

        this.program = gl.createProgram();
        gl.attachShader(this.program, vs);
        gl.attachShader(this.program, fs);
        gl.linkProgram(this.program);

        // If creating the shader program failed, alert

        if (!gl.getProgramParameter(this.program, gl.LINK_STATUS)) {
            alert("Unable to initialize the shader program.");
        }


        gl.useProgram(this.program);
        this._loadUniforms(gl);
        this._loadAttributes(gl);
    }


    _getShader = (gl, shaderString, shaderType)=> {
        // Now figure out what type of shader script we have,
        // based on its MIME type.

        var shader = gl.createShader(shaderType);

        // Send the source to the shader object

        gl.shaderSource(shader, shaderString);

        // Compile the shader program

        gl.compileShader(shader);

        // See if it compiled successfully

        if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
            alert("An error occurred compiling the shaders: " + gl.getShaderInfoLog(shader));
            return null;
        }

        return shader;
    }

    bind= (gl)=> {
        gl.useProgram(this.program);
    }

    _loadUniforms= (gl)=>{
        var uniforms = gl.getProgramParameter(this.program, gl.ACTIVE_UNIFORMS);
        var name;
        var k;

        for (k = 0; k < uniforms; k++) {
            name = gl.getActiveUniform(this.program, k).name;
            this._uniform[name] = gl.getUniformLocation(this.program, name);
        }
    }

    _loadAttributes= (gl)=> {
        var attributes = gl.getProgramParameter(this.program, gl.ACTIVE_ATTRIBUTES);
        var name;
        var k;

        for (k = 0; k < attributes; k++) {
            name = gl.getActiveAttrib(this.program, k).name;
            this._attributes[name] = gl.getAttribLocation(this.program, name);
        }
    }

    getUniformLoc= (name)=>{
        if (this._uniform[name] !== null) return this._uniform[name];
    }

    getAttLoc= (name)=>{
        if (this._attributes[name] !== null) return this._attributes[name];
    }

    EnableButtonLight= (gl)=> {

        gl.useProgram(this.program);

        var gDirectionalLight_Direction = this.getUniformLoc("gDirectionalLight.Direction");

        gl.uniform3fv(gDirectionalLight_Direction, [0, 0, 5]);

        var gDirectionalLight_Direction_Base_Color = this.getUniformLoc("gDirectionalLight.Base.Color");
        gl.uniform3fv(gDirectionalLight_Direction_Base_Color, [1.0,1.0, 1.0]);

        var gDirectionalLight_Base_AmbientIntensity = this.getUniformLoc("gDirectionalLight.Base.AmbientIntensity");
        gl.uniform1f(gDirectionalLight_Base_AmbientIntensity, 1.0);

        var gDirectionalLight_Base_DiffuseIntensity = this.getUniformLoc("gDirectionalLight.Base.DiffuseIntensity");
        gl.uniform1f(gDirectionalLight_Base_DiffuseIntensity, 1.0);

    }

    DisableButtonLight= (gl)=> {

        gl.useProgram(this.program);

        var gDirectionalLight_Direction = this.getUniformLoc("gDirectionalLight.Direction");
        if (this.isColor)
            gl.uniform3fv(gDirectionalLight_Direction, [0, 0, 1]);
        else
            gl.uniform3fv(gDirectionalLight_Direction, [0, 0, -1]);

        var gDirectionalLight_Direction_Base_Color = this.getUniformLoc("gDirectionalLight.Base.Color");
        gl.uniform3fv(gDirectionalLight_Direction_Base_Color, this.gDirectionalLightDirectionBaseColor);

        var gDirectionalLight_Base_AmbientIntensity = this.getUniformLoc("gDirectionalLight.Base.AmbientIntensity");
        gl.uniform1f(gDirectionalLight_Base_AmbientIntensity, 0.7);

        var gDirectionalLight_Base_DiffuseIntensity = this.getUniformLoc("gDirectionalLight.Base.DiffuseIntensity");
        gl.uniform1f(gDirectionalLight_Base_DiffuseIntensity, 0.7);

    }

    setUnifromLocations =(camera, gl)=> {

        gl.useProgram(this.program);

        var projectionMatrix = this.getUniformLoc( 'projection');
        gl.uniformMatrix4fv(projectionMatrix, true, camera.GetProjectionMatrix());

        var view = this.getUniformLoc( 'view');
        gl.uniformMatrix4fv(view, true, camera.GetViewMatrix());

        var mmat = mat4.create();
        var model = this.getUniformLoc( 'model');
        gl.uniformMatrix4fv(model, true, mmat);

        var viewPos = this.getUniformLoc( "viewPos");
        gl.uniform3fv(viewPos, [0, 0, 100]);

        var gNumSpotLights = this.getUniformLoc("gNumSpotLights");
        gl.uniform1i(gNumSpotLights, 0);

        var gNumPointLights = this.getUniformLoc("gNumPointLights");
        gl.uniform1i(gNumPointLights, 0);

        var gColorMap = this.getUniformLoc( "gColorMap");
        gl.uniform1i(gColorMap, 0);

        var gDirectionalLight_Direction = this.getUniformLoc("gDirectionalLight.Direction");
        if (this.isColor)
            gl.uniform3fv(gDirectionalLight_Direction, [0, 0, 1]);
        else
            gl.uniform3fv(gDirectionalLight_Direction, [0, 0, -1]);

        var gDirectionalLight_Direction_Base_Color = this.getUniformLoc("gDirectionalLight.Base.Color");
        gl.uniform3fv(gDirectionalLight_Direction_Base_Color,this.gDirectionalLightDirectionBaseColor);

        var gDirectionalLight_Base_AmbientIntensity = this.getUniformLoc( "gDirectionalLight.Base.AmbientIntensity");
        gl.uniform1f(gDirectionalLight_Base_AmbientIntensity, 0.5);

        var gDirectionalLight_Base_DiffuseIntensity = this.getUniformLoc( "gDirectionalLight.Base.DiffuseIntensity");
        gl.uniform1f(gDirectionalLight_Base_DiffuseIntensity,0.7);


        var gSpecularPower = this.getUniformLoc( "gSpecularPower");
        gl.uniform1f(gSpecularPower, 0);


        var gMatSpecularIntensity = this.getUniformLoc("gMatSpecularIntensity");
        gl.uniform1f(gMatSpecularIntensity, 0);


    }
  SareeLighting= (gl)=> {
    gl.useProgram(this.program);


    var gDirectionalLight_Direction_Base_Color = this.getUniformLoc("gDirectionalLight.Base.Color");
    gl.uniform3fv(gDirectionalLight_Direction_Base_Color, this.gDirectionalLightDirectionBaseColor);

    var gDirectionalLight_Base_AmbientIntensity = this.getUniformLoc( "gDirectionalLight.Base.AmbientIntensity");
    gl.uniform1f(gDirectionalLight_Base_AmbientIntensity, 0.8);

    var gDirectionalLight_Base_DiffuseIntensity = this.getUniformLoc( "gDirectionalLight.Base.DiffuseIntensity");
    gl.uniform1f(gDirectionalLight_Base_DiffuseIntensity,0.8);

    var gSpecularPower = this.getUniformLoc( "gSpecularPower");
    gl.uniform1f(gSpecularPower, 50);

    var gMatSpecularIntensity = this.getUniformLoc("gMatSpecularIntensity");

    gl.uniform1f(gMatSpecularIntensity, 0);
  }
  DisableSareeLighting= (gl)=>{
    gl.useProgram(this.program);

    var gDirectionalLight_Direction_Base_Color = this.getUniformLoc("gDirectionalLight.Base.Color");
    gl.uniform3fv(gDirectionalLight_Direction_Base_Color, this.gDirectionalLightDirectionBaseColor);

    var gDirectionalLight_Base_AmbientIntensity = this.getUniformLoc( "gDirectionalLight.Base.AmbientIntensity");
    gl.uniform1f(gDirectionalLight_Base_AmbientIntensity, 0.7);

    var gDirectionalLight_Base_DiffuseIntensity = this.getUniformLoc( "gDirectionalLight.Base.DiffuseIntensity");
    gl.uniform1f(gDirectionalLight_Base_DiffuseIntensity,0.7);
    var gSpecularPower = this.getUniformLoc( "gSpecularPower");
    gl.uniform1f(gSpecularPower, 10);

    var gMatSpecularIntensity = this.getUniformLoc("gMatSpecularIntensity");

    gl.uniform1f(gMatSpecularIntensity, 0);
  }
    UpdateLighting=(gl, gDirectionalLightDirectionBaseColor)=> {
        gl.useProgram(this.program);

        var gDirectionalLight_Direction_Base_Color = this.getUniformLoc("gDirectionalLight.Base.Color");
        gl.uniform3fv(gDirectionalLight_Direction_Base_Color, gDirectionalLightDirectionBaseColor);
    }
}

export default Shader;