/**
 * @module Files
 */
import * as React from "react";
import { BrowserRouter, Switch, Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { DashboardLayout } from "../navbar/Layout";
import { OTable } from "./table";
import { withStyles, makeStyles, lighten } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import queryString from "query-string";
import axios from "axios";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import DeleteForeverSharpIcon from "@material-ui/icons/DeleteForeverSharp";
import EditSharpIcon from "@material-ui/icons/EditSharp";
import CloudUploadSharpIcon from "@material-ui/icons/CloudUploadSharp";
import Typography from "@material-ui/core/Typography";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
} from "react-bootstrap";
import ReactNotification from "react-notifications-component";
import { store } from "react-notifications-component";
import Loader from "react-loader-spinner";

require("dotenv").config();
/**
* @author Muthu G & Balaji K
* @description Table cell Theme
* @param {Object} theme - childrens
*/
const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);
/**
* @author Muthu G & Balaji K
* @description Table Row Theme
*/
const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});
const headCells = [
  {
    id: "filename",
    numeric: false,
    disablePadding: true,
    label: "filename",
    link: true,
  },
  {
    id: "created",
    numeric: true,
    disablePadding: false,
    label: "Date",
    link: false,
  },
  {
    id: "assigned",
    numeric: true,
    disablePadding: false,
    label: "Assigned Nodes",
    link: false,
  },
  {
    id: "delivered",
    numeric: true,
    disablePadding: false,
    label: "Deleivered Nodes",
    link: false,
  },
];
/**
* @author Muthu G & Balaji K
* @description ToolBar Theme
*/
const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  title: {
    flex: "1 1 100%",
  },
}));
/**
* @author Muthu G & Balaji K
* @description Titlebar
* @param {Object} props - Props from parent
*/
const Titlebar = (props) => {
  const classes = useToolbarStyles();
  return (
    <Typography
      align="right"
      className={classes.title}
      component="div"
      style={{ width: 40 }}
    >
      <IconButton onClick={props.clickfun}>
        <CloudUploadSharpIcon style={{ fontSize: 40 }} />
      </IconButton>
      Upload File
    </Typography>
  );
};
/**
 * @module Files_react_Component
* @author Muthu G & Balaji K
* @description Files
* @param {Object} props - Props from parent
*/
class Files extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      datafiles: undefined,
      file: null,
      branches: [],
      nodes: [],
      showLoader:true,
    };
    this.onFormSubmit = this.onFormSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
    //  this.branches=["23","sdf"];
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name onFormSubmit
  * @description It will upload file
  */
  onFormSubmit(e) {
    e.preventDefault();
    const formData = new FormData();
    formData.append("oynafile", this.state.file);
    var selectedid = [];
    this.state.nodes.forEach(function (item) {
      if (item.selected === true)
        selectedid.push({ user: item.name, id: item._id });
    });


    formData.append("users", JSON.stringify(selectedid));
    let currentFile = this.state.file;
    this.setState({ progress: 0 });
    this.setState({ currentFile: currentFile });

    // setProgress(0);
    // setCurrentFile(currentFile);
    const config = {
      headers: {
        "content-type": "multipart/form-data",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
      onUploadProgress: (progressEvent) => {
        this.setState({
          progress: Math.round(
            (100 * progressEvent.loaded) / progressEvent.total
          ),
        });
      },
    };
    axios
      .post("/uploadfile", formData, config)
      .then((response) => {
        this.setState({ progress: 0 });
        this.setState({ currentFile: null });
        store.addNotification({
          title: "File!",
          message: "Uploaded successfully",
          type: "success",
          insert: "top",
          container: "top-right",
          animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
          animationOut: ["animate__animated animate__fadeOut"],
          dismiss: {
            duration: 5000,
            onScreen: false,
          },
          slidingExit: {
            duration: 800,
            timingFunction: "ease-out",
            delay: 0,
          },
        });
        this.GetAllFiles();

        //alert("The file is successfully uploaded");
      })
      .catch((error) => {});
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name onChange
  * @description file change event
  * @param {Object} e - event
  */
  onChange(e) {
    //  setSelectedFiles(event.target.files);
    this.setState({ files: e.target.files });
    this.setState({ file: e.target.files[0] });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name branchChange
  * @description branch change event
  * @param {Object} e - event
  */
  branchChange(e) {

    var selected = this.state.branches.filter(function (item) {
      return item.name === e.target.value;
    })[0];
    if (!selected) return;
    this.setState({ branch: e.target.value });
    this.GetBrancheUsers(selected._id);
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name GetBrancheUsers
  * @description get branch user
  * @param {string} branchid - branchid
  */
  GetBrancheUsers(branchid) {
    // POST request using fetch with set headers
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        branchid: branchid,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/GetUsers", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ nodes: data });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name download_File
  * @description get file from server
    * @param {object} e - event
  * @param {object} row - row data
  */
  download_File(e, row) {
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({
        branchid: "",
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/avatarGeneration", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("return response");
      });
      return;
      this.setState({ showLoader: true });
    e.preventDefault();
    axios({
      url: "/download_File/" + row.fileid, //your url
      method: "GET",
      headers: {
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
      responseType: "blob", // important
    }).then((response) => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", row.filename); //or any other extension
      document.body.appendChild(link);
      link.click();
      store.addNotification({
        title: "File!",
        message: "Downloaded successfully",
        type: "success",
        insert: "top",
        container: "top-right",
        animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
        animationOut: ["animate__animated animate__fadeOut"],
        dismiss: {
          duration: 5000,
          onScreen: false,
        },
        slidingExit: {
          duration: 800,
          timingFunction: "ease-out",
          delay: 0,
        },
      });
      this.GetAllFiles();
        this.setState({ showLoader: false });
    });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name GetAllBranches
  * @description get all branches
  */
  GetAllBranches() {
    // POST request using fetch with set headers
    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/Allbranches", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        if (data.length > 0) this.GetBrancheUsers(data[0]._id);
        this.setState({ branches: data });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name GetAllFiles
  * @description get all Files
  */
  GetAllFiles() {
    const requestOptions = {
      method: "POST",
      body: JSON.stringify({}),
      headers: {
        "Content-Type": "application/json",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
    };
    fetch(process.env.REACT_APP_SERVER_HOST + "/allfiles", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ datafiles: data });
          this.setState({ showLoader: false });
      });
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentWillMount
  * @description It will mount before start
  */
  componentWillMount() {
    this.GetAllFiles();
    this.GetAllBranches();
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name delete
  * @description It will delete the file
  * @param {object} row -row
  */
  delete(row) {
  this.setState({ showLoader: true });
    const dataf = new FormData();
    {
      const requestOptions = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: JSON.parse(localStorage.getItem("token")).token,
        },
        body: JSON.stringify({
          id: row._id,
          path: row.filepath + "/" + row.fileid,
        }),
      };
      var url = process.env.REACT_APP_SERVER_HOST + "/deletefile";
      fetch(url, requestOptions)
        .then((response) => response.json())
        .then((data) => {
          this.GetAllFiles();
          store.addNotification({
            title: "File!",
            message: "File has deleted",
            type: "danger",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
            animationOut: ["animate__animated animate__fadeOut"],
            dismiss: {
              duration: 5000,
              onScreen: false,
            },
            slidingExit: {
              duration: 800,
              timingFunction: "ease-out",
              delay: 0,
            },
          });
            this.setState({ showLoader: false });
        });
    }
  }

  selecting(row) {
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    const classes = [{}];
    const handleClose = () => this.setState({ show: false });
    const handleShow = () => this.setState({ show: true });

    return (
      <DashboardLayout>
        <Container fluid>
          <Row style={{ height: 30 }}></Row>
          <Row className="">
            <Col xs={10}>
              {this.state.currentFile && (
                <div className="progress">
                  <div
                    className="progress-bar progress-bar-info progress-bar-striped"
                    role="progressbar"
                    aria-valuenow={this.state.progress}
                    aria-valuemin="0"
                    aria-valuemax="100"
                    style={{ width: this.state.progress + "%" }}>
                    {this.state.progress}%
                  </div>
                </div>
              )}
            </Col>
          </Row>
          <Row style={{ height: 30 }}></Row>
          <Row>
            <Col xs={10}>
            <Loader type="Watch"
              color="#00BFFF"
              height={100}
              width={100}
              visible={this.state.showLoader}
              style={{
           display: "flex",
           justifyContent: "center",
           alignItems: "center",
            height:"60vh",
            position:"absolute",
            left: 0,/*added*/
            right: 0, /*added*/
            zIndex:1
         }}
            />

              {this.state.datafiles && (
                <OTable
                  Title="All Files"
                  headCells={headCells}
                  tableData={this.state.datafiles}
                  selecting={this.selecting}
                  delete={(row) => this.delete(row)}
                  isedit={false}
                  clickLink={(e, row) => {
                    this.download_File(e, row);
                  }}
                  Titlebar={<Titlebar clickfun={handleShow} />}
                ></OTable>
              )}
            </Col>
          </Row>
        </Container>

        <Modal show={this.state.show} onHide={handleClose}>
          <Form onSubmit={this.onFormSubmit}>
            <Modal.Header closeButton>
              <Modal.Title>New Node</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form.Group>
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>File</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <input
                      type="file"
                      name="myImage"
                      onChange={this.onChange}
                    />{" "}
                  </Col>
                </Row>
              </Form.Group>

              <Form.Group>
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Branch </Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {" "}
                    <Form.Control
                      as="select"
                      onChange={(e) => {
                        this.branchChange(e);
                      }}
                      placeholder="Select"
                      name="branch"
                    >
                      {this.state.branches.map((row) => (
                        <option>{row.name}</option>
                      ))}
                    </Form.Control>{" "}
                  </Col>
                </Row>
              </Form.Group>

              <Form.Group>
                <Row>
                  <Col xs={4}>
                    {" "}
                    <Form.Label>Nodes</Form.Label>{" "}
                  </Col>
                  <Col xs={8}>
                    {this.state.nodes.map((row) => (
                      <Form.Check
                        type="checkbox"
                        id={row._id}
                        label={row.name}
                        onChange={(e) => {
                          row.selected = e.target.checked;
                        }}
                      />
                    ))}
                  </Col>
                </Row>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                Close
              </Button>
              <Button type="submit" variant="primary" onClick={handleClose}>
                Upload
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      </DashboardLayout>
    );
  }

}

export default Files;
