import React , { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import './customstyle.css';
class Test extends React.Component{
    constructor(props){
        super(props);
        this.onClick = this.onClick.bind(this);
        this.state = {
            isExpand: false,
            isExpandsecond:true
        }
    }

    onClick(){
        
    }

    componentDidMount(){    }

    render(){
        return(
            <div>
                <button className='z-10' onClick={() => { this.setState({isExpand: !this.state.isExpand})
                let _this=this;
            setTimeout(() => {
                _this.setState({isExpandsecond: !_this.state.isExpand})
            }, 0);
            }}>Clicke Me</button>
                <div className='relative'>
                <div className='flex h-screen w-full '>
                <div className={`container2 ${ !this.state.isExpand ? `w-0` : `w-full`} bg-yellow-500  left-0`} >
                </div>
                <div className={`container2  grow ${ this.state.isExpandsecond ? `w-full` : `w-1/2`} bg-pink-500 `}></div>
            </div>
                </div>
        
            </div>
        )
    }
}

export default Test;