const shader_fs =
`#version 300 es
precision highp float;
const int MAX_POINT_LIGHTS = 2;
const int MAX_SPOT_LIGHTS = 2;

in vec2 TexCoords;
in vec3 Normal;                                                                   
in vec3 FragPos;  
in vec4 OriginalPos;

struct VSOutput
{
    vec2 TexCoord;
    vec3 Normal;                                                                   
    vec3 WorldPos;                                                                 
};


struct BaseLight
{
    vec3 Color;
    float AmbientIntensity;
    float DiffuseIntensity;
};

struct DirectionalLight
{
    BaseLight Base;
    vec3 Direction;
};
                                                                                    
struct Attenuation                                                                  
{                                                                                   
    float Constant;                                                                 
    float Linear;                                                                   
    float Exp;                                                                      
};                                                                                  
                                                                                    
struct PointLight                                                                           
{                                                                                           
    BaseLight Base;                                                                  
    vec3 Position;                                                                          
    Attenuation Atten;                                                                      
};                                                                                          
                                                                                            
struct SpotLight                                                                            
{                                                                                           
    PointLight Base;                                                                 
    vec3 Direction;                                                                         
    float Cutoff;                                                                           
};                                                                                          
                                                                                            
uniform int gNumPointLights;                                                                
uniform int gNumSpotLights;                                                                 
uniform DirectionalLight gDirectionalLight;        
uniform DirectionalLight gDirectionalLight2;    
uniform PointLight gPointLights[MAX_POINT_LIGHTS];                                          
uniform SpotLight gSpotLights[MAX_SPOT_LIGHTS];                                             
uniform sampler2D gColorMap;    
uniform sampler2D alphamap; 
uniform int isalphamap; 
uniform vec3 viewPos;                                                                  
uniform float gMatSpecularIntensity;                                                        
uniform float gSpecularPower; 
uniform int LightOff; 
layout(location = 0) out vec4 FragColor;
layout(location = 1) out vec4 position;
layout(location = 2) out vec4 normal;

vec4 CalcLightInternal(BaseLight Light, vec3 LightDirection, VSOutput In)            
{                                                                                           
    vec4 AmbientColor = vec4(Light.Color * Light.AmbientIntensity, 1.0);
    float DiffuseFactor = dot(In.Normal, -LightDirection);                                     
                                                                                            
    vec4 DiffuseColor  = vec4(0, 0, 0, 0);                                                  
    vec4 SpecularColor = vec4(0, 0, 0, 0);                                                  
                                                                                            
    if (DiffuseFactor > 0.0) {                                                                
        DiffuseColor = vec4(Light.Color * Light.DiffuseIntensity * DiffuseFactor, 1.0);
                                                                                            
        vec3 VertexToEye = normalize(viewPos - In.WorldPos);                             
        vec3 LightReflect = normalize(reflect(LightDirection, In.Normal));                     
        float SpecularFactor = dot(VertexToEye, LightReflect);                                      
        if (SpecularFactor > 0.0) {                                                         
            SpecularFactor = pow(SpecularFactor, gSpecularPower);
            SpecularColor = vec4(Light.Color * gMatSpecularIntensity * SpecularFactor, 1.0);
        }                                                                                   
    }                                                                                       
                                                                                            
    return (AmbientColor + DiffuseColor + SpecularColor);                                   
}                                                                                           
                                                                                            
vec4 CalcDirectionalLight(VSOutput In ,DirectionalLight dirlight)                                                      
{                                                                                           
    return CalcLightInternal(dirlight.Base, dirlight.Direction, In);  
}                                                                                           
                                                                                            
vec4 CalcPointLight(PointLight l, VSOutput In)                                       
{                                                                                           
    vec3 LightDirection = In.WorldPos - l.Position;                                           
    float Distance = length(LightDirection);                                                
    LightDirection = normalize(LightDirection);                                             
                                                                                            
    vec4 Color = CalcLightInternal(l.Base, LightDirection, In);                         
    float Attenuation =  l.Atten.Constant +                                                 
                         l.Atten.Linear * Distance +                                        
                         l.Atten.Exp * Distance * Distance;                                 
                                                                                            
    return Color / Attenuation;                                                             
}                                                                                           
                                                                                            
vec4 CalcSpotLight(SpotLight l, VSOutput In)                                         
{                                                                                           
    vec3 LightToPixel = normalize(In.WorldPos - l.Base.Position);                             
    float SpotFactor = dot(LightToPixel, l.Direction);                                      
                                                                                            
    if (SpotFactor > l.Cutoff) {                                                            
        vec4 Color = CalcPointLight(l.Base, In);                                        
        return Color * (1.0 - (1.0 - SpotFactor) * 1.0/(1.0 - l.Cutoff));                   
    }                                                                                                                                                                            
}                                                                                           
                            

                                                                
void main()
{                                    
    VSOutput In;
    In.TexCoord = TexCoords;
    In.Normal   = normalize(Normal);
    In.WorldPos = FragPos;
    position = OriginalPos;
    normal= vec4(normalize(Normal),0.0);
    vec4 TotalLight = CalcDirectionalLight(In,gDirectionalLight);                                         
    TotalLight += CalcDirectionalLight(In,gDirectionalLight2);                                                                                  
    for (int i = 0 ; i < gNumPointLights ; i++) {                                           
        TotalLight += CalcPointLight(gPointLights[i], In);                              
    }                                                                                       
                                                                                            
    for (int i = 0 ; i < gNumSpotLights ; i++) {                                            
        TotalLight += CalcSpotLight(gSpotLights[i], In);                                
    }                                                                                       
    // TotalLight *=  vec4(1);      
    if(isalphamap==1)
    {
    //vec4 texcolor=texture(gColorMap, In.TexCoord.xy)*TotalLight ;
    //vec4 aphacolor=texture(gColorMap, In.TexCoord.xy) ;
    //FragColor = vec4(0.0,0.0,0.0, aphacolor.r);
    vec4 texcolor=texture(gColorMap, In.TexCoord.xy);
    vec4 aphacolor=texture(alphamap, In.TexCoord.xy) ;
  if(aphacolor.r<0.1)
   discard;
    FragColor = vec4(texcolor.x,texcolor.y,texcolor.z,aphacolor.r);//*TotalLight;
   
    }
    else
    {
        if(LightOff==1)
        {
         vec4 finalcolor =texture(gColorMap, In.TexCoord.xy);  
        FragColor = vec4(finalcolor.x,finalcolor.y,finalcolor.z,1.0);
        }
    else
    {
    vec4 finalcolor =texture(gColorMap, In.TexCoord.xy)*TotalLight ;  
        FragColor = finalcolor ;
    }
        
    }

}
`;
const shader_fsold =
`#version 300 es
                                precision highp float;
                                const int MAX_POINT_LIGHTS = 2;
                                const int MAX_SPOT_LIGHTS = 2;

                                in vec2 TexCoords;
                                in vec3 Normal;
                                in vec3 FragPos;
                                in vec4 OriginalPos;

                                struct VSOutput
                                {
                                vec2 TexCoord;
                                vec3 Normal;
                                vec3 WorldPos;
                                };


                                struct BaseLight
                                {
                                vec3 Color;
                                float AmbientIntensity;
                                float DiffuseIntensity;
                                };

                                struct DirectionalLight
                                {
                                BaseLight Base;
                                vec3 Direction;
                                };

                                struct Attenuation
                                {
                                float Constant;
                                float Linear;
                                float Exp;
                                };

                                struct PointLight
                                {
                                BaseLight Base;
                                vec3 Position;
                                Attenuation Atten;
                                };

                                struct SpotLight
                                {
                                PointLight Base;
                                vec3 Direction;
                                float Cutoff;
                                };

                                uniform int gNumPointLights;
                                uniform int gNumSpotLights;
                                uniform DirectionalLight gDirectionalLight;
                                uniform PointLight gPointLights[MAX_POINT_LIGHTS];
                                uniform SpotLight gSpotLights[MAX_SPOT_LIGHTS];
                                uniform sampler2D gColorMap;
                                uniform vec3 viewPos;
                                uniform float gMatSpecularIntensity;
                                uniform float gSpecularPower;
                                uniform int LightOff;
                                layout(location = 0) out vec4 FragColor;
                                layout(location = 1) out vec4 position;
                                layout(location = 2) out vec4 normal;

                                vec4 CalcLightInternal(BaseLight Light, vec3 LightDirection, VSOutput In)
                                {
                                vec4 AmbientColor = vec4(Light.Color * Light.AmbientIntensity, 1.0);
                                float DiffuseFactor = dot(In.Normal, -LightDirection);

                                vec4 DiffuseColor  = vec4(0, 0, 0, 0);
                                vec4 SpecularColor = vec4(0, 0, 0, 0);

                                if (DiffuseFactor > 0.0) {
                                DiffuseColor = vec4(Light.Color * Light.DiffuseIntensity * DiffuseFactor, 1.0);

                                vec3 VertexToEye = normalize(viewPos - In.WorldPos);
                                vec3 LightReflect = normalize(reflect(LightDirection, In.Normal));
                                float SpecularFactor = dot(VertexToEye, LightReflect);
                                if (SpecularFactor > 0.0) {
                                SpecularFactor = pow(SpecularFactor, gSpecularPower);
                                SpecularColor = vec4(Light.Color * gMatSpecularIntensity * SpecularFactor, 1.0);
                                }
                                }

                                return (AmbientColor + DiffuseColor + SpecularColor);
                                }

                                vec4 CalcDirectionalLight(VSOutput In)
                                {
                                return CalcLightInternal(gDirectionalLight.Base, gDirectionalLight.Direction, In);
                                }

                                vec4 CalcPointLight(PointLight l, VSOutput In)
                                {
                                vec3 LightDirection = In.WorldPos - l.Position;
                                float Distance = length(LightDirection);
                                LightDirection = normalize(LightDirection);

                                vec4 Color = CalcLightInternal(l.Base, LightDirection, In);
                                float Attenuation =  l.Atten.Constant +
                                l.Atten.Linear * Distance +
                                l.Atten.Exp * Distance * Distance;

                                return Color / Attenuation;
                                }

                                vec4 CalcSpotLight(SpotLight l, VSOutput In)
                                {
                                vec3 LightToPixel = normalize(In.WorldPos - l.Base.Position);
                                float SpotFactor = dot(LightToPixel, l.Direction);

                                if (SpotFactor > l.Cutoff) {
                                vec4 Color = CalcPointLight(l.Base, In);
                                return Color * (1.0 - (1.0 - SpotFactor) * 1.0/(1.0 - l.Cutoff));
                                }
                                }



                                void main()
                                {
                                VSOutput In;
                                In.TexCoord = TexCoords;
                                In.Normal   = normalize(Normal);
                                In.WorldPos = FragPos;
                                position = OriginalPos;
                                normal= vec4(normalize(Normal),0.0);
                                vec4 TotalLight = CalcDirectionalLight(In);

                                for (int i = 0 ; i < gNumPointLights ; i++) {
                                TotalLight += CalcPointLight(gPointLights[i], In);
                                }

                                for (int i = 0 ; i < gNumSpotLights ; i++) {
                                TotalLight += CalcSpotLight(gSpotLights[i], In);
                                }
                                // TotalLight *=  vec4(1);
                                if(LightOff==1)
                                FragColor =texture(gColorMap, In.TexCoord.xy);
                                else
                                FragColor =texture(gColorMap, In.TexCoord.xy)*TotalLight ;
                                //FragColor =vec4(FragColor.xyz,0.5);
                                // FragColor =texture(gColorMap, In.TexCoord.xy);
                                }

`;

// Vertex shader program
const shader_vs =
`#version 300 es
layout (location = 0) in vec3 aPos;
layout (location = 1) in vec3 aNormal;
layout (location = 2) in vec2 aTexCoords;
uniform mat4 model;
uniform mat4 view;
uniform mat4 projection;

out vec3 Normal;
out vec3 FragPos;
out vec2 TexCoords;
out vec4 OriginalPos;

uniform int WithoutModel;
void main()
{
  if(WithoutModel==0)
  OriginalPos = vec4(aPos, 1.0)*model;
  else
  OriginalPos = vec4(aPos, 1.0) ;
  vec4 PosL   = vec4(aPos, 1.0);
  gl_Position  =PosL*model* view * projection ;// *projection ;
  TexCoords = aTexCoords;
  vec4 NormalL =vec4(aNormal, 0.0) ;
  Normal=(NormalL*model).xyz;
  FragPos = (PosL*model).xyz;
  //gl_Position = vec4(aPos, 1.0) * model*inverse(BoneTransform)* view * projection;
  //FragPos = vec3(vec4(aPos, 1.0)  * model);
  //Normal = aNormal * mat3(transpose(inverse(model )));
  //TexCoords = aTexCoords;
}`;
const shader_vsold =
`#version 300 es

                                layout (location = 0) in vec3 aPos;
                                layout (location = 1) in vec3 aNormal;
                                layout (location = 2) in vec2 aTexCoords;

                                uniform mat4 model;
                                uniform mat4 view;
                                uniform mat4 projection;

                                out vec3 Normal;
                                out vec3 FragPos;
                                out vec2 TexCoords;
                                out vec4 OriginalPos;


                                uniform int WithoutModel;
                                void main()
                                {

                                if(WithoutModel==0)
                                OriginalPos = vec4(aPos, 1.0)*model;
                                else
                                OriginalPos =vec4(aPos, 1.0) ;
                                vec4 PosL    =  vec4(aPos, 1.0);

                                gl_Position  =PosL*model* view * projection ;// *projection ;
                                TexCoords = aTexCoords;
                                vec4 NormalL =   vec4(aNormal, 0.0) ;
                                Normal=(NormalL*model).xyz;
                                FragPos = (PosL*model).xyz;

                                }
`;

// Program to render object in the texture space (unfolded)
const Normal_fs =
  `#version 300 es

                                precision highp float;

                                const int MAX_POINT_LIGHTS = 2;
                                const int MAX_SPOT_LIGHTS = 2;

                                in vec3 TexCoords;
                                in vec3 Normal;
                                in vec3 FragPos;
                                in vec4 ProjPos;
                                struct VSOutput
                                {
                                vec3 TexCoord;
                                vec3 Normal;
                                vec3 WorldPos;
                                };


                                struct BaseLight
                                {
                                vec3 Color;
                                float AmbientIntensity;
                                float DiffuseIntensity;
                                };

                                struct DirectionalLight
                                {
                                BaseLight Base;
                                vec3 Direction;
                                };

                                struct Attenuation
                                {
                                float Constant;
                                float Linear;
                                float Exp;
                                };

                                struct PointLight
                                {
                                BaseLight Base;
                                vec3 Position;
                                Attenuation Atten;
                                };

                                struct SpotLight
                                {
                                PointLight Base;
                                vec3 Direction;
                                float Cutoff;
                                };

                                uniform int gNumPointLights;
                                uniform int gNumSpotLights;
                                uniform DirectionalLight gDirectionalLight;
                                uniform PointLight gPointLights[MAX_POINT_LIGHTS];
                                uniform SpotLight gSpotLights[MAX_SPOT_LIGHTS];
                                uniform sampler2D gColorMap;
                                uniform vec3 viewPos;
                                uniform float gMatSpecularIntensity;
                                uniform float gSpecularPower;

                                layout(location = 0) out vec4 FragColor;
                                layout(location = 1) out vec4 position;

                                vec4 CalcLightInternal(BaseLight Light, vec3 LightDirection, VSOutput In)
                                {
                                vec4 AmbientColor = vec4(Light.Color * Light.AmbientIntensity, 1.0);
                                float DiffuseFactor = dot(In.Normal, -LightDirection);

                                vec4 DiffuseColor  = vec4(0, 0, 0, 0);
                                vec4 SpecularColor = vec4(0, 0, 0, 0);

                                if (DiffuseFactor > 0.0) {
                                DiffuseColor = vec4(Light.Color * Light.DiffuseIntensity * DiffuseFactor, 1.0);

                                vec3 VertexToEye = normalize(viewPos - In.WorldPos);
                                vec3 LightReflect = normalize(reflect(LightDirection, In.Normal));
                                float SpecularFactor = dot(VertexToEye, LightReflect);
                                if (SpecularFactor > 0.0) {
                                SpecularFactor = pow(SpecularFactor, gSpecularPower);
                                SpecularColor = vec4(Light.Color * gMatSpecularIntensity * SpecularFactor, 1.0);
                                }
                                }

                                return (AmbientColor + DiffuseColor + SpecularColor);
                                }

                                vec4 CalcDirectionalLight(VSOutput In)
                                {
                                return CalcLightInternal(gDirectionalLight.Base, gDirectionalLight.Direction, In);
                                }

                                vec4 CalcPointLight(PointLight l, VSOutput In)
                                {
                                vec3 LightDirection = In.WorldPos - l.Position;
                                float Distance = length(LightDirection);
                                LightDirection = normalize(LightDirection);

                                vec4 Color = CalcLightInternal(l.Base, LightDirection, In);
                                float Attenuation =  l.Atten.Constant +
                                l.Atten.Linear * Distance +
                                l.Atten.Exp * Distance * Distance;

                                return Color / Attenuation;
                                }

                                vec4 CalcSpotLight(SpotLight l, VSOutput In)
                                {
                                vec3 LightToPixel = normalize(In.WorldPos - l.Base.Position);
                                float SpotFactor = dot(LightToPixel, l.Direction);

                                if (SpotFactor > l.Cutoff) {
                                vec4 Color = CalcPointLight(l.Base, In);
                                return Color * (1.0 - (1.0 - SpotFactor) * 1.0/(1.0 - l.Cutoff));
                                }
                                else {
                                return vec4(0,0,0,0);
                                }
                                }

                                // out vec4 FragColor;

                                void main()
                                {
                                VSOutput In;
                                In.TexCoord = TexCoords;
                                In.Normal   = normalize(Normal);
                                In.WorldPos = FragPos;
                                position =ProjPos;
                                vec4 TotalLight = CalcDirectionalLight(In);

                                for (int i = 0 ; i < gNumPointLights ; i++) {
                                TotalLight += CalcPointLight(gPointLights[i], In);
                                }

                                for (int i = 0 ; i < gNumSpotLights ; i++) {
                                TotalLight += CalcSpotLight(gSpotLights[i], In);
                                }
                                // TotalLight *=  vec4(1);
                                FragColor =vec4( In.TexCoord,1)*TotalLight ;
                                // FragColor =vec4(In.Normal,1.0)*TotalLight ;
                                }

`;

const Normal_vs =
  `#version 300 es

                                layout (location = 0) in vec3 aPos;
                                layout (location = 1) in vec3 aNormal;
                                layout (location = 2) in vec3 aTexCoords;

                                uniform mat4 model;
                                uniform mat4 view;
                                uniform mat4 projection;

                                out vec3 Normal;
                                out vec3 FragPos;
                                out vec3 TexCoords;
                                out vec4 ProjPos;

                                void main()
                                {
                                vec4 PosL    = vec4(aPos, 1.0);
                                gl_Position = vec4(aPos, 1.0) * model * view * projection;
                                TexCoords = aTexCoords;
                                ProjPos=PosL;
                                vec4 NormalL =  vec4(aNormal, 0.0) ;
                                Normal = aNormal * mat3(transpose(inverse(model)));
                                FragPos = vec3(vec4(aPos, 1.0) * model);
                                }
`;
export{ shader_fs,
    shader_fsold,
    shader_vs,
    shader_vsold,
    Normal_fs,
    Normal_vs};
