

const Texture = class {
    constructor (id,gl) {
		this.Name=id;
		this.TextureId=null;
	}
	
	BindTexture= (gl ,Source)=> {
		var teximage = new Image();
		//this.teximage.src = "./img/khronos_webgl.png";

		teximage.src = Source;
		let loader = img => new Promise(resolve => {
			// resolve the arguments as an Array
			img.onload = e => {
				if (this.TextureId==null)
				this.TextureId = gl.createTexture();
				gl.activeTexture(gl.TEXTURE0);

				gl.bindTexture(gl.TEXTURE_2D, this.TextureId);
				gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, teximage.width, teximage.height, 0, gl.RGBA, gl.UNSIGNED_BYTE, teximage);

				gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR);
				gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.LINEAR);

				gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.MIRRORED_REPEAT);
				gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.MIRRORED_REPEAT);

				gl.generateMipmap(gl.TEXTURE_2D);
				gl.bindTexture(gl.TEXTURE_2D,null);
			};
			// force resetting the src, otherwise onload may already have fired
			img.src = img.src;
		});
		async function loadImages() {
			const a = await loader(teximage);
		}

		loadImages().then(arr => {
			console.log(arr);
		}).catch(console.error);
	}
	Use= (gl)=> {
		gl.activeTexture(gl.TEXTURE0);
		gl.bindTexture(gl.TEXTURE_2D, this.TextureId);
    }
	Use2= (gl) =>{
		gl.activeTexture(gl.TEXTURE1);
		gl.bindTexture(gl.TEXTURE_2D, this.TextureId);
    }
}
export default Texture;
