/**
 * @module Logout
 */
import * as React from "react";
import { BrowserRouter, Switch, Link, Redirect } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { DashboardLayout } from "../navbar/Layout";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import queryString from "query-string";
import {
  Modal,
  Col,
  Row,
  Container,
  Button,
  Toast,
  Form,
} from "react-bootstrap";
/**
 * @module Logout_react_Component
* @author Muthu G & Balaji K
* @description Logout
* @param {Object} props - Props from parent
*/
class Logout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      dataget: undefined,
    };
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name componentWillMount
  * @description It will mount before start
  */
  componentWillMount() {
    localStorage.clear();
    window.location.href = "/";

  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  render() {
    return <Redirect to="/" />;
  }
}

export default Logout;
