/**
 * @module Login_js
 */

import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './login.css';
import Cookies from 'universal-cookie';
import ReactNotification from 'react-notifications-component';
import { store } from 'react-notifications-component';
import { useMediaQuery } from 'react-responsive';
import API from './api/services';
const cookies = new Cookies();
/**
* @author Muthu G & Balaji K
* @description It will login User
* @param {String} username - user name
* @param {String} password - password
*/
async function loginUser(credentials) {
  const resp= await API.login(credentials)
console.log(resp);
 return resp;
//   fetch(process.env.REACT_APP_SERVER_HOST+'/login', {
//    method: 'POST',
//    headers: {
//      'Content-Type': 'application/json'
//    },
//    body: JSON.stringify(credentials)
//  }).then(data => data.json()
// )
}
/**
 * @module Login_Page
* @author Muthu G & Balaji K
* @description Login Page
* @param {String} setToken - token set to local storage
*/
export default function Login({ setToken }) {
  const [username, setUserName] = useState();
  const [password, setPassword] = useState();
  /**
  * @function
  * @name handleSubmit
  * @author Muthu G & Balaji K
  * @description handleSubmit
  * @param {String} submit - token set to local storage
  */
  const handleSubmit = async e => {
    e.preventDefault();
    const token = await loginUser({
      username,
      password
    });
    console.log(token);
    if(!token.sucess){
      store.addNotification({
        title: "Login!",
        message: token.msg,
        type: "danger",
        insert: "top",
        container: "top-right",
        animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
        animationOut: ["animate__animated animate__fadeOut"],
        dismiss: {
          duration: 5000,
          onScreen: false,
        },
        slidingExit: {
          duration: 800,
          timingFunction: "ease-out",
          delay: 0,
        },
      });
    }
    
     console.log(token);
    setToken(token);
  }

  const isDesktop = useMediaQuery({query: '(min-width: 1224px)'})
  
  return(
    <>
      <ReactNotification />
    <div class="container mt-5">

      <h1 class="offset-md-5">Login</h1>

      <div class="row">
        <div class="col-sm-4 offset-md-4">
          <div class="card">
          <div class="card-body">
      <form onSubmit={handleSubmit}>
      <div class="form-group">
        <label for="email">Email</label>
          <input type="text" class="form-control"  onChange={e => setUserName(e.target.value)} />
          </div>
            <div class="form-group">
        <label for="password">Password</label>
          <input type="password" class="form-control"  onChange={e => setPassword(e.target.value)} />

          </div>
        <div>
          <button type="submit" class="btn btn-dark">Submit</button>
              <a class="btn btn-light" href="/register" type="button">Register</a>
        </div>
      </form>
      </div>
    </div>
  </div>

  </div>
  </div>
  </>
  )
}
/**
* @function
* @name prop_type
* @author Muthu G & Balaji K
* @description prop type set
*/
Login.propTypes = {
  setToken: PropTypes.func.isRequired
};
